import {useRoute, useNavigation} from '@react-navigation/native';
import {Icon, TopNavigationAction} from '@ui-kitten/components';
import {useEffect, useMemo, useState} from 'react';
import {useTranslation} from 'react-i18next';
import {IconRejectReview, IconWorkflow} from '../../../assets/images';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import HeaderCM from '../../components/Header/HeaderCM';
import ModalFullScreen from '../../components/ModalFullScreen';
import StepCM from '../review-eid/components/Step';
import {SafeAreaView} from 'react-native-safe-area-context';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import ViewEcontractFlow from './view-econtract';
import {Alert, Pressable, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import TextCM from '../../components/Text';
import InfoBodyScreen from '../review-eid/components/Info';
import {IRecipientTicketImpl} from '../../../model/interface/service.interface';
import {DateUtils} from '../../../utils';
import NavigationName from '../../../constants/NavigationName';
import {Status} from '../../../model/enum/common.enum';
import ButtonCM from '../../components/Button';
import {cloneDeep, isEmpty} from 'lodash';
import ReviewEcontract from './review-econtract';
import TextFieldCM from '../../components/TextField';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import Toast from 'react-native-toast-message';
import {ticketActions} from '../../../redux/slices';
import {useReviewEcontractServices} from '../../../hooks/apiHooks/useReviewEcontractServices';
import {Font} from '../../../constants';
import {
  EnvRemind,
  IFieldIdFromColla,
  IParty,
  IRecipient,
} from './review-econtract/interface';
import {HeaderField} from './interface';
import {
  EContractNotionFile,
  headerFieldsColla,
  listTypeUserAction,
} from './review-econtract/helper';
import Flow from '../ticket-detail/components/TabView/components/Flow';
import ViewFormScreen from '../../components/ViewFormScreen';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';

const ReviewEcontractFlow = () => {
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const router = useRoute<any>();
  const dataEcontractFlow = router?.params?.dataUserTask;
  const isFinish = router?.params?.isFinish || false;
  const navigation = useNavigation<any>();
  const [isShow, setisShow] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isShowInfo, setIsShowInfo] = useState<boolean>(false);
  const [error, setError] = useState<{[key: string]: string}>({});
  const [isShowFormNegotiation, setIsShowFormNegotiation] = useState(false);
  const [note, setNote] = useState('');
  const [noteError, setNoteError] = useState('');
  const [isShowNote, setIsShowNote] = useState(true);
  const [isShowForm, setIsShowForm] = useState(true);
  const [isShowFormInfo, setIsShowFormInfo] = useState(false);
  const [dataFormByUsertaskId, setDataFormByUsertaskId] = useState<any>();
  const [templateStructure, setTemplateStructure] = useState<any>();
  const [mergeFileList, setMergeFileList] = useState<
    {
      fileId: string;
      fileName: string;
      order: number;
    }[]
  >([]);

  const [eWriterDocInfoWriter, setEWriterDocInfoWriter] = useState<any>();

  const [envRemind, setEnvRemind] = useState<EnvRemind>({
    isRemindEnv: false,
    overdueDate: new Date(),
    remindStartDate: new Date(),
    remindFrequency: 'daily',
    remindContent: null,
  });

  const [isDTFlow, setIsDTFlow] = useState(false);
  const [dataPartyForm, setDataPartyForm] = useState<IParty[]>([]);
  const [listFormEnvData, setListFormEnvData] = useState<HeaderField[]>([]);
  const [listCaOtpRecipientForm, setListCaOtpRecipientForm] = useState<any>({});
  const [documentList, setDocumentList] = useState<IFieldIdFromColla[]>([]);
  const [ticketModelerFlow, setTicketModelerFlow] = useState<any>();
  const [generateEnvelopeId, setGenerateEnvelopeId] = useState('');
  const [isErrorView, setIsErrorView] = useState({
    isError: dataEcontractFlow.documentResource === 'ecollabo' ? false : true,
    errorViewMessage:
      dataEcontractFlow.documentResource === 'ecollabo'
        ? ''
        : 'RV.econtract.title',
  });
  const {
    dataTicketRedux,
    recipientDetaiImpl,
    formData,
    modeler,
    statusPartyRaw,
  } = useAppSelector(state => state.ticket);
  const [dataUserCreateTicket, setDataUserCreateTicket] = useState<any>(null);
  const [recipientDetaiImplLast] = useState<IRecipientTicketImpl>(
    recipientDetaiImpl?.find(
      data => data.userTaskId === dataEcontractFlow?.userTaskId,
    ) as IRecipientTicketImpl,
  );

  const {dataAccount} = useAppSelector(state => state.account);
  const {accessToken} = useAppSelector(state => state.common);

  const ReviewTicketServices = useReviewServices();
  const TicketService = useTicketServices();
  const ReviewEcontractServices = useReviewEcontractServices();

  const dataInfo: any[] = useMemo(() => {
    return [
      {
        key: 'RV.createBy',
        type: 'avatar',
        value: {
          name: dataUserCreateTicket?.personalName || '---',
          email: dataTicketRedux.createdBy || '---',
          avatarUrl: dataUserCreateTicket?.imageUrl,
        },
      },
      {
        key: 'RV.ticketName',
        type: 'normal',
        value: {
          name: dataTicketRedux?.title || '---',
        },
      },
      {
        key: 'RV.process',
        type: 'normal',
        value: {
          name:
            (dataTicketRedux?.jsonXml &&
              JSON.parse(dataTicketRedux?.jsonXml).properties.name) ||
            '---',
        },
      },
      {
        key: 'RV.createDate',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(
            dataTicketRedux?.createdDate,
          ),
        },
      },
      {
        key: 'RV.infoForm',
        type: 'form',
        value: {
          name: formData?.formName,
          funcViewFrom: () => {
            setIsShowFormInfo(true);
          },
        },
      },
      {
        key: 'RV.status',
        type: 'status',
        value: {
          status:
            dataTicketRedux.ticketStatus === Status.voided
              ? Status.voided
              : recipientDetaiImplLast?.recipientStatus || Status.processing,
        },
      },
    ];
  }, [dataTicketRedux, formData, modeler, dataUserCreateTicket]);

  useEffect(() => {
    getFormEcontract();
    getTicketModelerFlow();
    getUserCreateTicket();
  }, []);

  const renderRightAction = () => {
    return (
      <TopNavigationAction
        onPress={() => setisShow(true)}
        icon={<IconWorkflow width={20} height={20} />}
      />
    );
  };

  const getFormEcontract = async () => {
    try {
      const data = await ReviewEcontractServices.getDataFormFill({
        versionId: recipientDetaiImplLast.versionFormId,
      });
      if (data) {
        setDataFormByUsertaskId(data);
      }
    } catch (error) {}
  };

  const getTicketModelerFlow = async () => {
    const data = await ReviewEcontractServices.getTicketModelerFlowStatusRaw(
      dataTicketRedux.id,
    );
    if (data) {
      setTicketModelerFlow(data);
    }
  };

  const getUserCreateTicket = async () => {
    try {
      const user = await TicketService.getMultiLoginAccount([
        dataTicketRedux.createdBy,
      ]);
      if (user && user.length > 0) {
        setDataUserCreateTicket(user[0]);
      }
    } catch (error) {}
  };

  const handleReject = async () => {
    if (!note) {
      setIsShowNote(true);
      setNoteError('CM.empty');
      return;
    }
    setNoteError('');
    const body = {
      ...recipientDetaiImplLast?.recipient,
      reason: note,
    };
    const dataReject = await ReviewTicketServices.actionTicket(
      recipientDetaiImplLast?.id,
      recipientDetaiImplLast?.ticketId,
      recipientDetaiImplLast?.userTaskId,
      body,
      accessToken,
      'reject',
    );
    if (dataReject) {
      Toast.show({
        type: 'success',
        text1: t('CM.noti'),
        text2: t('CM.rejectSucesss'),
      });
      dispatch(
        ticketActions.setDataTicket({
          ...dataTicketRedux,
          ticketStatus: Status.rejected,
        }),
      );
      navigation.goBack();
    } else {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.rejectFailed'),
      });
    }
  };

  const checkOpenModal = async () => {
    const checkValid = listFormEnvData.find(
      data => !data.value.trim() && data.required,
    );
    if (checkValid) {
      const errArr = listFormEnvData.filter(
        data => !data.value.trim() && data.required,
      );
      errArr.forEach(data => {
        setError(prev => ({
          ...prev,
          [data.id]: 'CM.empty',
        }));
      });
      return false;
    }
    return true;
  };

  const handleSubmit = async (isViewFormScreen: boolean) => {
    try {
      if (!isViewFormScreen) {
        const checkValid = listFormEnvData.find(
          data => !data.value.trim() && data.required,
        );
        if (checkValid) {
          listFormEnvData
            .filter(data => !data.value.trim() && data.required)
            .forEach(data => {
              setError(prev => ({
                ...prev,
                [data.id]: 'CM.empty',
              }));
            });
          return;
        }
        if (dataFormByUsertaskId?.versionId) {
          setIsShowFormNegotiation(true);
          return;
        }
      }
      setIsLoading(true);
      const bodyCreateContract = {
        id: '',
        refId: null,
        selector: '',
        lookup: dataTicketRedux.id,
        attrs: {},
        payload: '',
        body: null,
      };
      // get file after save data sub form
      const fileDTOList = await ReviewEcontractServices.getAllFileInNotion(
        dataTicketRedux.id,
        dataEcontractFlow?.userTaskId,
      );
      if (dataEcontractFlow.documentResource === 'ewriter') {
        if (!isRecipientSignTypeValid(templateStructure?.template)) {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: t('RV.econtract.alert.error.otp_with_ekyc'),
          });
          return;
        }

        const dto = await createEWriterDTO(fileDTOList);

        bodyCreateContract.payload = JSON.stringify(dto);
        bodyCreateContract.selector =
          'flow_start_kyta_create_econtract_temp_n_determine';
      }

      if (
        dataEcontractFlow.documentResource === 'ecollabo' ||
        dataEcontractFlow.documentResource === 'eclm' ||
        dataEcontractFlow.documentResource === 'eform'
      ) {
        const dto = createECollaboDTO(fileDTOList);
        bodyCreateContract.payload = JSON.stringify(dto);
        bodyCreateContract.selector =
          'flow_start_kyta_create_econtract_auto_determine';
      }

      const createContractResp = await ReviewEcontractServices.createContract(
        bodyCreateContract,
      );
      const result = handleEContractResponse(createContractResp);
      if (result) {
        Toast.show({
          type: result.alertType,
          text1: t('CM.noti'),
          text2: t(`RV.${result.message}`),
        });
        if (result.alertType === 'success') {
          navigation.goBack();
        }
      }
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  const createECollaboDTO = (fileDTOList: EContractNotionFile[]) => {
    const dto = {
      refId: dataTicketRedux.id,
      isDossier: false,
      files: [],
      docTypeCode: null,
      headerFields: [],
      parties: null,
      owner: ticketModelerFlow?.createdBy,
      recipientDetailsId: dataEcontractFlow?.recipientDetailsId,
      userTaskId: dataEcontractFlow?.userTaskId,
      ticketId: dataTicketRedux.id,
      userTaskVersion: null,
      remind: envRemind.isRemindEnv
        ? {
            expiredTime: DateUtils.convertDateToDDMMYYYY(envRemind.overdueDate),
            remindBefore: DateUtils.convertDateToDDMMYYYY(
              envRemind.remindStartDate,
            ),
            remindEvery: envRemind.remindFrequency === 'daily' ? '1' : '0',
            remindMessage: envRemind.remindContent,
          }
        : null,
    };
    const headerFields = headerFieldsColla;
    headerFields.forEach(field => {
      if (field.id !== 'envSubmittedFrom') {
        if (listFormEnvData.find(data => data.id == field.id)) {
          field.value = listFormEnvData.find(
            data => data.id == field.id,
          )?.value;
        }
        if (field.id === 'refId') {
          field.value = dataTicketRedux.id;
        }
      }
    });
    dto.headerFields = headerFields.map(field => {
      return {
        id: field.id,
        name: field.name,
        type: field.type,
        value: field.value,
      };
    }) as any;

    dto.files = [
      ...fileDTOList.map((item, index) => {
        return {
          name: item.fileName,
          id: item.id,
          order: item.order,
          resource: item.resource,
        };
      }),
    ] as any;
    return dto;
  };

  const createEWriterDTO = async (fileDTOList: EContractNotionFile[]) => {
    const ekycIdCardContent = await getRecipientEKYCInfo();
    let dto = {
      refId: dataTicketRedux.id,
      templateId: eWriterDocInfoWriter.templateEcontractId,
      customData: [],
      inputData: {
        datas: [[]],
      },
      owner: '',
      recipientDetailsId: dataEcontractFlow?.recipientDetailsId,
      userTaskId: dataEcontractFlow?.userTaskId,
      ticketId: dataTicketRedux.id,
      userTaskVersion: null,
      remind: envRemind.isRemindEnv
        ? {
            expiredTime: DateUtils.convertDateToDDMMYYYY(envRemind.overdueDate),
            remindBefore: DateUtils.convertDateToDDMMYYYY(
              envRemind.remindStartDate,
            ),
            remindEvery: envRemind.remindFrequency === 'daily' ? '1' : '0',
            remindMessage: envRemind.remindContent,
          }
        : null,
      files: [...fileDTOList],
      envelopeId: generateEnvelopeId,
    };

    const templateStructureDatas = templateStructure?.lotData
      ?.datas[0] as any[];
    templateStructureDatas.forEach(field => {
      if (field.id !== 'envSubmittedFrom') {
        if (listFormEnvData.find(data => data.id == field.id)) {
          field.value = listFormEnvData.find(
            data => data.id == field.id,
          )?.value;
        }
        if (field.id === 'refId') {
          field.value = dataTicketRedux.id;
        }
      }
    });

    if (ekycIdCardContent) {
      const customData = getRecipientCaOtpInfo(ekycIdCardContent);
      dto.customData = customData as any;
    }

    dataPartyForm.forEach(party => {
      listTypeUserAction.forEach(action => {
        (party.recipient as any)[action].forEach((recipient: IRecipient) => {
          templateStructureDatas.forEach(field => {
            if (field.id === recipient.id) {
              if (field.type === 'r') {
                field.value = recipient.name;
              } else if (field.type === 'm') {
                field.value = recipient.email;
              } else if (field.type === 'phone') {
                field.value = recipient.phone;
              } else if (field.type.toLowerCase() === 'fca') {
                if (
                  !ekycIdCardContent ||
                  !ekycIdCardContent.recipientId ||
                  !ekycIdCardContent.back.content ||
                  !ekycIdCardContent.back.contentContentType ||
                  !ekycIdCardContent.front.content ||
                  !ekycIdCardContent.front.contentContentType
                ) {
                  field.type = 'fca_pre';
                  const value = JSON.parse(field.value);
                  if (value.type && value.type.toLowerCase() === 'fca') {
                    value.type = 'fca_pre';
                  }
                  field.value = JSON.stringify(value);
                } else if (
                  ekycIdCardContent &&
                  (!ekycIdCardContent.back.content ||
                    !ekycIdCardContent.back.contentContentType ||
                    !ekycIdCardContent.front.content ||
                    !ekycIdCardContent.front.contentContentType)
                ) {
                }
              }
            }
          });
        });
      });
    });
    dto = {
      ...dto,
      inputData: {
        datas: [cloneDeep(templateStructureDatas) as any],
      },
    };
    dto.inputData.datas[0].forEach((data: any) => {
      if (data.id === 'dueDays') {
        data.value = templateStructure?.template.dueInDays;
      }
    });
    dto.owner = templateStructure?.template?.requester?.contact?.email;

    if (dto.customData.length > 0) {
      dto.customData.forEach((customData: any) => {
        if (dto.inputData.datas.length > 0) {
          dto.inputData.datas[0] = JSON.parse(
            JSON.stringify(
              dto.inputData.datas[0].filter(
                (field: any) =>
                  field.id !== customData.recipientId ||
                  (field.id === customData.recipientId &&
                    field.type !== 'fca_pre'),
              ),
            ),
          );
        }
      });
    } else {
      dto.inputData.datas[0] = JSON.parse(
        JSON.stringify(
          dto.inputData.datas[0].filter(
            (field: any) => field.type !== 'fca_pre' && field.type !== 'fca',
          ),
        ),
      );
    }
    return dto;
  };

  const getRecipientCaOtpInfo = (ekycIdCardContent: any) => {
    let result: any[] = [];
    if (Object.keys(listCaOtpRecipientForm).length > 0) {
      dataPartyForm.forEach(party => {
        listTypeUserAction.forEach(action => {
          (party.recipient as any)[action].forEach((recipient: IRecipient) => {
            const recipientFormValue = listCaOtpRecipientForm[recipient.id];
            recipientFormValue.email = recipient.email;
            recipientFormValue.personalName = recipient.name;
            recipientFormValue.telephoneNumber = recipient.phone;

            if (
              ekycIdCardContent &&
              recipient.id === ekycIdCardContent.recipientId
            ) {
              if (
                recipientFormValue.photoBackSideIDCard &&
                recipientFormValue.photoBackSideIDCardContentType &&
                recipientFormValue.photoFrontSideIDCard &&
                recipientFormValue.photoFrontSideIDCardContentType
              ) {
                recipientFormValue.type = 'fca';
              }
              recipientFormValue.photoBackSideIDCard =
                ekycIdCardContent.back.content;
              recipientFormValue.photoBackSideIDCardContentType =
                ekycIdCardContent.back.contentContentType;
              recipientFormValue.photoFrontSideIDCard =
                ekycIdCardContent.front.content;
              recipientFormValue.photoFrontSideIDCardContentType =
                ekycIdCardContent.front.contentContentType;

              recipientFormValue.address =
                ekycIdCardContent.ekycRepository.attrs.address;
              recipientFormValue.commune =
                ekycIdCardContent.ekycRepository.attrs.commune;
              recipientFormValue.country =
                ekycIdCardContent.ekycRepository.attrs.country;
              recipientFormValue.location =
                ekycIdCardContent.ekycRepository.attrs.location;
              recipientFormValue.personalID =
                ekycIdCardContent.ekycRepository.attrs.personalID;
              recipientFormValue.provideAddress =
                ekycIdCardContent.ekycRepository.attrs.provideAddress;
              recipientFormValue.stateOrProvince =
                ekycIdCardContent.ekycRepository.attrs.stateOrProvince;
              recipientFormValue.refId =
                ekycIdCardContent.ekycRepository.attrs.refId;
              recipientFormValue.personalName =
                ekycIdCardContent.ekycRepository.attrs.personalName;

              recipientFormValue.ekycContent =
                ekycIdCardContent.ekycRepository.ekycContent;
              recipientFormValue.provideDate = DateUtils.convertDateToDDMMYYYY(
                ekycIdCardContent.ekycRepository.attrs.provideDate,
              );
            }

            result.push(recipientFormValue);
          });
        });
      });
    }
    return result;
  };

  const getRecipientEKYCInfo = async () => {
    let eIdUserTaskId: string = '';
    if (statusPartyRaw?.party) {
      statusPartyRaw?.party?.recipients.forEach((recipient: any) => {
        if (recipient.role === 'eID') {
          eIdUserTaskId = recipient.id;
        }
      });
    }
    if (!eIdUserTaskId) return null;
    const params = {
      ticketId: dataTicketRedux.id,
      userTaskId: eIdUserTaskId,
    };
    const ekycHistory = await ReviewTicketServices.getHistoryEkycDetail(params);
    if (!ekycHistory) return null;
    const ekycRepositoryId: string = ekycHistory.ekycRepositoryId;
    const ekycRepository: any = await ReviewTicketServices.getEkycRepoById(
      ekycRepositoryId,
    );

    const ekycIdCardInfo: any =
      await ReviewEcontractServices.getEkycRepositoryDetailIdCard(
        ekycRepository.id,
        {
          ekycRepositoryId: ekycRepositoryId,
          id: ekycRepository.id,
          recipientId: ekycRepository.id,
        },
      );

    if (ekycIdCardInfo && ekycIdCardInfo.back && ekycIdCardInfo.front) {
      return {
        recipientId: ekycRepository.personalId,
        ekycRepository: ekycIdCardInfo.ekycRepository,
        front: {
          content: ekycIdCardInfo.front.content,
          contentContentType: ekycIdCardInfo.front.name,
        },
        back: {
          content: ekycIdCardInfo.back.content,
          contentContentType: ekycIdCardInfo.back.name,
        },
      };
    } else {
      return null;
    }
  };

  const handleEContractResponse = (response: string | number) => {
    const result = {
      message: '',
      alertType: '',
    };
    if (
      response === '-1' ||
      response === -1 ||
      response === '13' ||
      response === 13
    ) {
      result.message = 'econtract.alert.error.13-error';
      result.alertType = 'error';
    } else if (response === '2' || response === 2) {
      result.message = 'econtract.alert.error.2-ekyc';
      result.alertType = 'error';
    } else if (response === '3' || response === 3) {
      result.message = 'econtract.alert.error.3-error';
      result.alertType = 'error';
    } else if (response === '4' || response === 4) {
      result.message = 'econtract.alert.error.4-ewriter-file';
      result.alertType = 'error';
    } else if (response === '5' || response === 5) {
      result.message = 'econtract.alert.error.5-error';
      result.alertType = 'error';
    } else if (response === '6' || response === 6) {
      result.message = 'econtract.alert.error.6-no-sign-control';
      result.alertType = 'error';
    } else if (response === '7' || response === 7) {
      result.message = 'econtract.alert.error.7-digital-sign-amount-invalid';
      result.alertType = 'error';
    } else if (response === '8' || response === 8) {
      result.message = 'econtract.alert.error.8-field-role-invalid';
      result.alertType = 'error';
    } else if (response === '9' || response === 9) {
      result.message = 'econtract.alert.error.9-error';
      result.alertType = 'error';
    } else if (response === '10' || response === 10) {
      result.message = 'econtract.alert.error.10-error';
      result.alertType = 'error';
    } else if (response === '12' || response === 12) {
      result.message = 'econtract.alert.error.12-filename-not-found';
      result.alertType = 'error';
    } else if (response === '14' || response === 14) {
      result.message = 'econtract.alert.error.14-rcp-no-sign-control';
      result.alertType = 'error';
    } else if (response === '0' || response === 0) {
      result.message = 'econtract.alert.success.create';
      result.alertType = 'success';
    } else {
      result.message = 'econtract.alert.success.create';
      result.alertType = 'success';
    }
    return result;
  };

  const isRecipientSignTypeValid = (templateData: any): boolean => {
    if (templateData) {
      for (let j = 0; j < templateData.parties.length; j++) {
        for (let i = 0; i < templateData.parties[j].recipients.length; i++) {
          const signTypes = templateData.parties[j].recipients[i].signTypes;
          if (signTypes && signTypes.includes('sign_fca.otp')) {
            if (signTypes.includes('sign_ekyc')) {
              continue;
            } else {
              return false;
            }
          } else {
            continue;
          }
        }
      }
      return true;
    } else {
      return true;
    }
  };

  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('RV.econtract.infoContract')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        renderContentRight={renderRightAction}
      />

      <KeyboardAwareScrollView style={{flex: 1}}>
        <View style={styles.ctnBody}>
          <View style={styles.ctnSubBody}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowInfo(!isShowInfo)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowInfo ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>{t('CM.infoCommon')}</TextCM>
            </Pressable>

            {isShowInfo && (
              <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                <InfoBodyScreen dataInfo={dataInfo} />
              </View>
            )}
          </View>
        </View>
        {isFinish ? (
          <ViewEcontractFlow envelopeId={dataEcontractFlow.envelopeId} />
        ) : (
          <>
            {dataFormByUsertaskId && (
              <View style={styles.ctnBody}>
                <View style={[styles.ctnSubBody]}>
                  <Pressable
                    style={styles.ctnHeader}
                    onPress={() => setIsShowForm(!isShowForm)}>
                    <Icon
                      fill={'#6F2CED'}
                      width={16}
                      height={16}
                      name={
                        isShowForm ? 'arrow-circle-down' : 'arrow-circle-right'
                      }
                    />
                    <TextCM style={styles.textTitle}>
                      {t('RV.econtract.formNeedAction')}
                    </TextCM>
                  </Pressable>

                  {isShowForm && (
                    <View style={styles.ctnFormName}>
                      <TextCM style={styles.textTitle}>
                        {t('RV.econtract.fillForm')}{' '}
                      </TextCM>
                      <TouchableOpacity
                        onPress={async () => {
                          setIsShowFormNegotiation(await checkOpenModal());
                        }}>
                        <TextCM style={styles.formName}>
                          {dataFormByUsertaskId?.formName}
                        </TextCM>
                      </TouchableOpacity>
                    </View>
                  )}
                </View>
              </View>
            )}
            <View style={styles.ctnBody}>
              <ReviewEcontract
                error={error}
                setError={setError}
                dataEcontractFlow={dataEcontractFlow}
                setTemplateStructure={setTemplateStructure}
                setMergeFileList={setMergeFileList}
                setEWriterDocInfoWriter={setEWriterDocInfoWriter}
                setEnvRemind={setEnvRemind}
                envRemind={envRemind}
                setIsDTFlow={setIsDTFlow}
                setDataPartyForm={setDataPartyForm}
                dataPartyForm={dataPartyForm}
                listFormEnvData={listFormEnvData}
                setListFormEnvData={setListFormEnvData}
                setListCaOtpRecipientForm={setListCaOtpRecipientForm}
                setIsErrorView={setIsErrorView}
                isErrorView={isErrorView}
                setDocumentList={setDocumentList}
                setGenerateEnvelopeId={setGenerateEnvelopeId}
              />
            </View>
            <View style={styles.ctnBody}>
              <View
                style={[
                  styles.ctnSubBody,
                  {
                    marginBottom: 16,
                  },
                ]}>
                <Pressable
                  style={styles.ctnHeader}
                  onPress={() => setIsShowNote(!isShowNote)}>
                  <Icon
                    fill={'#6F2CED'}
                    width={16}
                    height={16}
                    name={
                      isShowNote ? 'arrow-circle-down' : 'arrow-circle-right'
                    }
                  />
                  <TextCM style={styles.textTitle}>{t('CM.note')}</TextCM>
                </Pressable>

                {isShowNote && (
                  <View style={styles.ctnNote}>
                    <TextFieldCM
                      isRequired={true}
                      label={t('CM.fillNote')}
                      value={note}
                      onChangeText={text => {
                        setNote(text);
                        setNoteError('');
                      }}
                      multiline
                      maxLength={400}
                      errorMessage={t(noteError)}
                    />
                  </View>
                )}
              </View>
            </View>
          </>
        )}
      </KeyboardAwareScrollView>
      {!isFinish &&
        (recipientDetaiImplLast?.recipientStatus === Status.processing ||
          recipientDetaiImplLast?.recipientStatus === null) &&
        !isErrorView.isError && (
          <View style={styles.ctnFooter}>
            <>
              <TouchableOpacity onPress={handleReject}>
                <IconRejectReview width={36} height={36} />
              </TouchableOpacity>
              <ButtonCM
                disabled={isEmpty(error) ? false : true}
                style={{flex: 1}}
                label={t('CM.confirm')}
                onPress={() => handleSubmit(false)}
                loading={isLoading}
              />
            </>
          </View>
        )}
      <ModalFullScreen
        isVisible={isShow}
        children={<StepCM onClose={() => setisShow(false)} />}
      />

      <ModalFullScreen
        isVisible={isShowFormNegotiation}
        children={
          <ViewFormScreen
            versionId={dataFormByUsertaskId?.versionId}
            readOnly={false}
            data={{
              _userTaskId: dataEcontractFlow?.userTaskId,
              _ticketId: dataTicketRedux?.id,
              formId: dataFormByUsertaskId?.formId,
            }}
            isShowNoti={true}
            handleSubmit={() => handleSubmit(true)}
            onClose={() => setIsShowFormNegotiation(false)}
            nameHeader={dataFormByUsertaskId?.formName}
            envelopeEnvNo={generateEnvelopeId}
          />
        }
      />

      <ModalFullScreen
        isVisible={isShowFormInfo}
        children={
          <>
            <View style={{flex: 1}}>
              <Flow />
            </View>
            <ButtonCM
              style={{backgroundColor: '#f2ecfd', marginHorizontal: 16}}
              styleLabel={{color: '#6d1fad'}}
              label={t('CM.back')}
              onPress={() => setIsShowFormInfo(false)}
            />
          </>
        }
      />
    </SafeAreaView>
  );
};

export default ReviewEcontractFlow;
