import React, {useState, useEffect} from 'react';
import {
  View,
  Text,
  TextInput,
  TouchableOpacity,
  FlatList,
  Modal,
  SafeAreaView,
  Image,
} from 'react-native';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import {IUserModal} from '../../../review-ecollabo-flow/components/share/interface';
import useDebounce from '../../../../../hooks/useDebounce';
import styles from './styles';
import {Icon} from '@ui-kitten/components';
import TextCM from '../../../../components/Text';
import {useAppSelector} from '../../../../../hooks/useRedux';

interface CustomSelectProps {
  onSelect: (item: IUserModal | null) => void;
  placeholder?: string;
}

const PAGE_SIZE = 20;

const CustomSelect: React.FC<CustomSelectProps> = ({
  onSelect,
  placeholder = 'TK.select',
}) => {
  const {t} = useTranslation();
  const [isOpen, setIsOpen] = useState(false);
  const [selectedItem, setSelectedItem] = useState<IUserModal | null>(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [listDataUser, setListDataUser] = useState<IUserModal[]>([]);
  const [page, setPage] = useState(0);
  const [totalRecords, setTotalRecords] = useState(0);
  const [refreshing, setRefreshing] = useState(false);

  const {dataAccount} = useAppSelector(state => state.account);

  const searchQueryDebounce = useDebounce(searchQuery, 500);

  const ReviewServices = useReviewServices();

  useEffect(() => {
    getDataUserInfoModel(0, searchQueryDebounce);
  }, [searchQueryDebounce]);

  const getDataUserInfoModel = async (
    page: number,
    searchQueryDebounce: string,
  ) => {
    try {
      const params = {
        page: page,
        size: PAGE_SIZE,
      };
      const body = {
        advanced: true,
        orgIn: '/1833/5390/5392/5943',
        q: searchQueryDebounce.trim().toLowerCase(),
        resource: 'table_user',
      };
      const dataResp = await ReviewServices.getUserInfoModel(params, body);
      if (dataResp) {
        if (page === 0) {
          setListDataUser(
            dataResp.data.filter(
              (data: IUserModal) => data.email !== dataAccount.email,
            ),
          );
        } else {
          setListDataUser(
            [...listDataUser, ...dataResp.data].filter(
              (data: IUserModal) => data.email !== dataAccount.email,
            ),
          );
        }
        setTotalRecords(dataResp.totalRecords);
      }
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const loadMoreData = () => {
    if (listDataUser.length < totalRecords) {
      getDataUserInfoModel(page + 1, searchQueryDebounce);
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    setRefreshing(true);
    setPage(0);
    getDataUserInfoModel(0, searchQueryDebounce);
  };

  const handleSelect = (item: IUserModal) => {
    setSelectedItem(item);
    onSelect(item);
    setIsOpen(false);
  };

  const handleClear = () => {
    setSelectedItem(null);
    onSelect(null);
  };

  const handleOpenModal = () => {
    setIsOpen(true);
    setSearchQuery('');
  };

  const renderItem = ({item}: {item: IUserModal}) => (
    <TouchableOpacity style={styles.item} onPress={() => handleSelect(item)}>
      <Image
        width={32}
        height={32}
        borderRadius={16}
        source={{
          uri:
            item.imageUrl ||
            'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
        }}
      />
      <View>
        <TextCM style={styles.textname}>{item.personalName}</TextCM>
        <TextCM style={styles.textEmail}>{item.email}</TextCM>
      </View>
    </TouchableOpacity>
  );

  return (
    <View style={styles.container}>
      <View style={styles.selectContainer}>
        <TouchableOpacity style={styles.select} onPress={handleOpenModal}>
          <Text style={styles.selectText}>
            {selectedItem?.email || t(placeholder)}
          </Text>
        </TouchableOpacity>
        {selectedItem && (
          <TouchableOpacity style={styles.clearButton} onPress={handleClear}>
            <Icon name="close" width={20} height={20} />
          </TouchableOpacity>
        )}
      </View>

      <Modal visible={isOpen} animationType="slide" transparent={true}>
        <SafeAreaView style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <TextInput
              style={styles.searchInput}
              placeholder={t('CM.search')}
              value={searchQuery}
              onChangeText={setSearchQuery}
            />
            <FlatList
              refreshing={refreshing}
              onRefresh={onRefresh}
              data={listDataUser}
              renderItem={renderItem}
              keyExtractor={item => item.email}
              onEndReached={loadMoreData}
              onEndReachedThreshold={0.5}
              initialNumToRender={PAGE_SIZE}
            />
            <TouchableOpacity
              style={styles.closeButton}
              onPress={() => setIsOpen(false)}>
              <Text style={styles.closeButtonText}>{t('CM.close')}</Text>
            </TouchableOpacity>
          </View>
        </SafeAreaView>
      </Modal>
    </View>
  );
};

export default CustomSelect;
