import {useIsFocused, useNavigation, useRoute} from '@react-navigation/native';
import {
  Image,
  StatusBar,
  useWindowDimensions,
  View,
  TouchableOpacity,
} from 'react-native';
import styles from './styles';
import TextCM from '../../components/Text';
import FontSize, {SizeDP} from '../../../constants/Size';
import {useEffect, useState} from 'react';
import {
  IconMailFill,
  IconPeople,
  Iconhigh,
  Iconlow,
  Iconmedium,
  IconCheckmarkCircle,
  IconStar,
  IconStarOutline,
} from '../../../assets/images';
import HeaderTicketDetail from './components/Header';
import TabViewCM from './components/TabView';
import {Color, Font, NavigationName} from '../../../constants';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import {ticketActions} from '../../../redux/slices';
import {SafeAreaView} from 'react-native-safe-area-context';
import {Propity, Status} from '../../../model/enum/common.enum';
import {useTranslation} from 'react-i18next';
import {Divider, Icon, TopNavigationAction} from '@ui-kitten/components';
import BottomSheetCM from '../../components/BottomSheet';
import {ListAction} from './helper';
import {dataListStatusTicket} from '../my-ticket/helper';
import {dataListStatusTicketFollowNoti} from '../ticket-follow/helper';
import {FAB, Portal} from 'react-native-paper';
import CommunicationTicketScreen from './communication-ticket';
import {useOriginServices} from '../../../hooks/apiHooks/useOriginServices';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import ModalFullScreen from '../../components/ModalFullScreen';
import AuthorityUserAction from './components/authorityUserAction';
import {NotificationActionType} from '../notification/interface';
import React from 'react';
import RateNow from './components/RateNow';
import NoteTicket from './components/NoteTicket';
interface IRoute {
  dataTicketId: string;
  email: string;
  status: Status;
  selectTabBottom: number;
}

const TicketDetail = () => {
  const navigation = useNavigation<any>();
  const route = useRoute<any>();
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const {dataTicketId, email, status, selectTabBottom}: IRoute = route?.params;
  const recipientDetailsId = route?.params?.recipientDetailsId ?? '';
  const userTaskId = route?.params?.userTaskId ?? '';
  const typeView: 'view' | 'quick' = route?.params?.typeView ?? 'view';
  const notifyActionType: NotificationActionType =
    route?.params?.notifyActionType ?? '';
  const [isShow, setIsShow] = useState<boolean>(false);

  const [dataStatus, setDataStatus] = useState(
    selectTabBottom === 1
      ? dataListStatusTicket.find(item => item.status === status)
      : dataListStatusTicketFollowNoti.find(item => item.status === status),
  );

  const [bottomSheetSelect, setBottomSheetSelect] = useState('');
  const [openFab, setOpenFab] = useState(false);
  const [isRealoadWebview, setIsRealoadWebview] = useState(false);
  const [checkAuthority, setCheckAuthority] = useState<any>();
  const [showRateTicket, setShowRateTicket] = useState(false);

  const isFoucs = useIsFocused();

  const {dataTicketRedux, dataUserTicket, dataOrgin, statusPartyRaw} =
    useAppSelector(state => state.ticket);
  const {dataAccount} = useAppSelector(state => state.account);
  const {accessToken} = useAppSelector(state => state.common);

  const orginServices = useOriginServices();
  const ListServices = useTicketServices();
  const ReviewTicketServices = useReviewServices();

  const {width} = useWindowDimensions();
  const toogleIsReloadWebview = () => setIsRealoadWebview(!isRealoadWebview);
  const toogleRateNow = () => {
    setShowRateTicket(prev => !prev);
    setIsShow(false);
  };

  useEffect(() => {
    (async () => {
      await getAccountUserTicket();
    })();
  }, [selectTabBottom]);

  const getAccountUserTicket = async () => {
    try {
      const values = await Promise.all([
        ListServices.getTicketById(dataTicketId, accessToken),
        ReviewTicketServices.getStatusParty(dataTicketId, accessToken),
      ]);
      if (!dataStatus) {
        const statusData =
          selectTabBottom === 1
            ? dataListStatusTicket.find(
                item => item.status === values[0]?.ticketStatus,
              )
            : dataListStatusTicketFollowNoti.find(
                item => item.status === values[0]?.ticketStatus,
              );
        setDataStatus(statusData);
      }
      const orgIn = values[0]?.orgIn?.split('/')[
        values[0]?.orgIn?.split('/')?.length - 1
      ] as any;
      let dataOrg;
      let listModeler;
      const dataUser = await ListServices.getMultiLoginAccount(
        [values?.[0]?.createdBy],
        accessToken,
      );

      try {
        dataOrg = await orginServices.getOrigin(orgIn, accessToken);
      } catch (error) {}

      try {
        listModeler = await ListServices.getImplementByModelerId(
          values[0].modelerId,
        );
      } catch (error) {}

      setCheckAuthority(
        listModeler?.data
          .filter((val: any) => val.userTaskId == userTaskId)
          .find((h: any) => h.email == dataAccount.email),
      );
      dispatch(ticketActions.setDataTicket(values[0]));
      dispatch(ticketActions.setDataUserTicket(dataUser[0]));
      dispatch(ticketActions.setDataOrgin(dataOrg));
      dispatch(ticketActions.setStatusPartyRaw(values[1]));
      if (notifyActionType === 'ticket_tag_comment') {
        setBottomSheetSelect('comments');
      }
    } catch (error) {
      console.log(error);
    }
  };

  const renderPriority = (priority: Propity) => {
    switch (priority) {
      case Propity.high:
        return <Iconhigh width={SizeDP(20)} height={SizeDP(20)} />;
      case Propity.medium:
        return <Iconmedium width={SizeDP(20)} height={SizeDP(20)} />;
      case Propity.low:
        return <Iconlow width={SizeDP(16)} height={SizeDP(16)} />;
    }
  };

  const handleOpen = () => {
    setIsShow(true);
  };

  const renderRightActions = (): React.ReactElement => (
    <View style={{alignItems: 'center', flexDirection: 'row'}}>
      {/* {renderPriority(dataTicketRedux.priority)} */}
      <TopNavigationAction
        hitSlop={15}
        onPress={handleOpen}
        icon={<Icon fill="#00204D" name="more-vertical" />}
      />
    </View>
  );

  const renderActionComments = () => {
    return (
      <CommunicationTicketScreen onClose={() => setBottomSheetSelect('')} />
    );
  };

  const renderInfoTicket = () => {
    const rating = dataTicketRedux?.rate;
    const renderRatingStars = () => {
      if (rating == undefined || rating == null) {
        return (
          <TouchableOpacity
            style={styles.btnRate}
            onPress={() => toogleRateNow()}>
            <TextCM style={styles.btnRate}>{t('TK.rate_now')}</TextCM>
          </TouchableOpacity>
        );
      }
      return (
        <>
          {new Array(5)
            .fill(1)
            .map((_, index) =>
              index >= rating ? (
                <IconStarOutline
                  key={index}
                  width={SizeDP(16)}
                  height={SizeDP(16)}
                  fill={'#D3D3D3'}
                />
              ) : (
                <IconStar
                  key={index}
                  width={SizeDP(16)}
                  height={SizeDP(16)}
                  fill={Color.yellow}
                />
              ),
            )}
        </>
      );
    };

    return (
      <View style={{rowGap: 10}}>
        <View style={[styles.columnCenter]}>
          <View style={[styles.columnSubCenter]}>
            <Icon
              fill="#00204D"
              width={SizeDP(16)}
              height={SizeDP(16)}
              name="person"
            />
            <TextCM style={styles.textTit}>{t('TK.staff')}</TextCM>
          </View>
          <View
            style={[
              {
                columnGap: 10,
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'flex-start',
                flex: 1,
              },
            ]}>
            <Image
              width={SizeDP(24)}
              height={SizeDP(24)}
              borderRadius={SizeDP(12)}
              source={{
                uri: dataAccount?.imageUrl
                  ? dataAccount?.imageUrl
                  : 'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
            <TextCM style={styles.textDes}>
              {'' + dataUserTicket?.personalName}
            </TextCM>
          </View>
        </View>
        <View style={[styles.columnCenter]}>
          <View style={[styles.columnSubCenter]}>
            <IconMailFill width={SizeDP(16)} height={SizeDP(16)} />
            <TextCM style={styles.textTit}>{t('CM.email')}</TextCM>
          </View>
          <TextCM style={styles.textDes}>{dataUserTicket?.email}</TextCM>
        </View>
        <View style={[styles.columnCenter]}>
          <View style={[styles.columnSubCenter]}>
            <IconPeople width={SizeDP(16)} height={SizeDP(16)} />
            <TextCM style={styles.textTit}>{t('CM.orgIn')}</TextCM>
          </View>
          <TextCM style={styles.textDes}>{dataOrgin?.name}</TextCM>
        </View>
        <View style={[styles.columnCenter]}>
          <View style={[styles.columnSubCenter]}>
            <Icon name="bookmark" width={SizeDP(16)} height={SizeDP(16)} />
            <TextCM style={styles.textTit}>{t('CM.status')}</TextCM>
          </View>
          <View style={[{flex: 1, flexDirection: 'row'}]}>
            <View
              style={{
                backgroundColor: dataStatus?.bgColor,
                paddingHorizontal: 5,
                paddingVertical: 3,
                borderRadius: 4,
              }}>
              <TextCM
                style={[
                  {
                    color: dataStatus?.color,
                    textAlign: 'center',
                    fontSize: FontSize.FontTinier,
                    fontFamily: Font.InterMedium500,
                  },
                ]}>
                {t(`TK.${dataTicketRedux?.ticketStatus}`)}
              </TextCM>
            </View>
            <View style={{flex: 1}}></View>
          </View>
        </View>
        {dataTicketRedux?.ticketStatus == 'completed' && (
          <View style={[styles.columnCenter]}>
            <View style={[styles.columnSubCenter]}>
              <IconCheckmarkCircle
                fill={Color.gray}
                width={SizeDP(16)}
                height={SizeDP(16)}
              />
              <TextCM style={styles.textTit}>{t('TK.rate')}</TextCM>
            </View>
            <View style={{flex: 1, flexDirection: 'row'}}>
              {renderRatingStars()}
              <View style={{flex: 1}}></View>
            </View>
          </View>
        )}
      </View>
    );
  };

  const handleGoBack = () => {
    navigation.navigate(NavigationName.BottomTabs);
  };

  const getListAction = (checkAuthority: any) => {
    if (selectTabBottom === 1 || !checkAuthority) {
      return ListAction.filter(
        action => action.navigationName !== 'authority',
      ).map(action => ({
        icon: () => <Icon fill={'#985FF4'} name={action.iconName} />,
        label: t(`CM.${action.title}`),
        labelStyle: styles.iconStyle,
        onPress: () => setBottomSheetSelect(action.navigationName),
      }));
    }
    const checkAuthor = JSON.parse(checkAuthority?.authorizationType);
    let listAction = ListAction;
    if (checkAuthor?.length > 0) {
    } else {
      listAction = ListAction.filter(
        action => action.navigationName !== 'authority',
      );
    }
    return listAction.map(action => ({
      icon: () => <Icon fill={'#985FF4'} name={action.iconName} />,
      label: t(`CM.${action.title}`),
      labelStyle: styles.iconStyle,
      onPress: () => setBottomSheetSelect(action.navigationName),
    }));
  };

  const onCloseAuthority = () => {
    setBottomSheetSelect('');
    toogleIsReloadWebview();
  };

  return (
    <>
      <SafeAreaView style={{backgroundColor: '#fff', flex: 1}}>
        <StatusBar
          barStyle={'dark-content'}
          backgroundColor={Color.Transparent}
        />
        <HeaderTicketDetail
          onPressIconLeft={handleGoBack}
          contentStyle={{backgroundColor: '#fff'}}
          title={dataTicketRedux?.title ? dataTicketRedux?.title : '---'}
          renderContentRight={renderRightActions}
        />
        <Divider />
        <View style={{flex: 1}}>
          {dataTicketRedux?.id === dataTicketId && (
            <TabViewCM
              userTaskId={userTaskId}
              recipientDetailsId={recipientDetailsId}
              selectTabBottom={selectTabBottom}
              isRealoadWebview={isRealoadWebview}
              notifyActionType={notifyActionType}
              typeView={typeView}
            />
          )}
        </View>
        {isFoucs && (
          <View>
            <Portal>
              <FAB.Group
                open={openFab}
                visible
                variant="primary"
                style={{
                  paddingRight: width - 100,
                }}
                fabStyle={styles.fabStyle}
                icon={
                  !openFab
                    ? () => <Icon fill={'#fff'} name="plus-outline" />
                    : () => <Icon fill={'#fff'} name="close-outline" />
                }
                actions={getListAction(checkAuthority)}
                onStateChange={() => setOpenFab(!openFab)}
                onPress={() => {
                  if (openFab) {
                    // do something if the speed dial is open
                  }
                }}
              />
            </Portal>
          </View>
        )}
      </SafeAreaView>
      <BottomSheetCM
        isVisible={isShow}
        onClose={() => setIsShow(false)}
        title={t(`CM.info`)}
        renderContent={renderInfoTicket()}
      />

      {showRateTicket && (
        <RateNow isVisible={showRateTicket} toogleRateNow={toogleRateNow} />
      )}

      <ModalFullScreen
        isVisible={bottomSheetSelect === 'comments'}
        children={renderActionComments()}
      />

      <ModalFullScreen
        isVisible={bottomSheetSelect === 'authority'}
        children={
          <AuthorityUserAction
            userTaskId={userTaskId}
            recipientDetailsId={recipientDetailsId}
            onClose={onCloseAuthority}
          />
        }
      />
      <ModalFullScreen
        isVisible={bottomSheetSelect === 'note'}
        children={<NoteTicket onClose={() => setBottomSheetSelect('')} />}
      />
    </>
  );
};
export default TicketDetail;
