import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import {
  Divider,
  Icon,
  IndexPath,
  TopNavigationAction,
} from '@ui-kitten/components';
import {IconRejectReview, IconWorkflow} from '../../../assets/images';
import {Pressable, TouchableOpacity, View} from 'react-native';
import TextCM from '../../components/Text';
import styles from './styles';
import ButtonCM from '../../components/Button';
import {useTranslation} from 'react-i18next';
import {useEffect, useMemo, useState} from 'react';
import InputCM from './components/InputCM';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import DropdownListCM from '../../components/DropDownList';
import {listNoti} from './helper';
import InfoBodyScreen from './components/Info';
import TextFieldCM from '../../components/TextField';
import {isEmpty} from 'lodash';
import ModalFullScreen from '../../components/ModalFullScreen';
import StepCM from './components/Step';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import {DateUtils, RegexUtils} from '../../../utils';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import {IRecipientTicketImpl} from '../../../model/interface/service.interface';
import {useNavigation, useRoute} from '@react-navigation/native';
import Toast from 'react-native-toast-message';
import QRCode from 'react-qr-code';
import {Status} from '../../../model/enum/common.enum';
import {ticketActions} from '../../../redux/slices';
import EkycData from './components/EkycForm';
import EkycInfo from './components/EkycInfo';
import Flow from '../ticket-detail/components/TabView/components/Flow';
import React from 'react';
import {getConfig} from '../../../services/httpClient';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';
enum TYPE {
  name = 'name',
  phone = 'phone',
  email = 'email',
  notiType = 'notiType',
}

const ReviewEIDScreen = () => {
  const {t} = useTranslation();
  const route = useRoute<any>();
  const dispatch = useAppDispatch();
  const dataRouter = route?.params?.dataUserTask ?? {};
  const navigation = useNavigation<any>();
  const [isShowInfo, setIsShowInfo] = useState(false);
  const [isShowInfoID, setIsShowInfoID] = useState(true);
  const [typeSelectNoti, setTypeSelectNoti] = useState<IndexPath[]>([
    new IndexPath(0),
  ]);
  const [error, setError] = useState<{[key: string]: string}>({});
  const [valueForm, setValueForm] = useState<{[key: string]: any}>({});
  const [isCanSubmit, setIsCanSubmit] = useState(false);
  const [isShowForm, setIsShowForm] = useState(false);
  const [note, setNote] = useState('');
  const [noteError, setNoteError] = useState('');

  const [isShow, setisShow] = useState(false);
  const [isShowNote, setIsShowNote] = useState(true);
  const [dataQrCode, setDataQrCode] = useState('');
  const [dataUserCreateTicket, setDataUserCreateTicket] = useState<any>(null);
  const {dataTicketRedux, recipientDetaiImpl, formData, modeler} =
    useAppSelector(state => state.ticket);

  const [recipientDetaiImplLast] = useState<IRecipientTicketImpl>(
    recipientDetaiImpl?.find(
      data => data.userTaskId === dataRouter?._userTaskId,
    ) as IRecipientTicketImpl,
  );

  const {dataAccount} = useAppSelector(state => state.account);
  const {accessToken} = useAppSelector(state => state.common);

  const ReviewTicketServices = useReviewServices();
  const TicketServices = useTicketServices();

  const dataInfo: any[] = useMemo(() => {
    return [
      {
        key: 'RV.createBy',
        type: 'avatar',
        value: {
          name: dataUserCreateTicket?.personalName || '---',
          email: dataTicketRedux.createdBy || '---',
          avatarUrl: dataUserCreateTicket?.imageUrl,
        },
      },
      {
        key: 'RV.ticketName',
        type: 'normal',
        value: {
          name: dataTicketRedux?.title || '---',
        },
      },
      {
        key: 'RV.process',
        type: 'normal',
        value: {
          name:
            (dataTicketRedux?.jsonXml &&
              JSON.parse(dataTicketRedux?.jsonXml).properties.name) ||
            '---',
        },
      },
      {
        key: 'RV.createDate',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(
            dataTicketRedux?.createdDate,
          ),
        },
      },
      {
        key: 'RV.infoForm',
        type: 'form',
        value: {
          name: formData?.formName,
          funcViewFrom: () => setIsShowForm(true),
        },
      },
      {
        key: 'RV.status',
        type: 'status',
        value: {
          status:
            dataTicketRedux.ticketStatus === Status.voided
              ? Status.voided
              : recipientDetaiImplLast?.recipientStatus || Status.processing,
        },
      },
    ];
  }, [dataTicketRedux, formData, modeler, dataUserCreateTicket]);

  useEffect(() => {
    getUserCreateTicket();
  }, []);

  useEffect(() => {
    const newError = error;
    if (typeSelectNoti.length === 0) {
      newError[`${TYPE.notiType}`] = 'CM.empty';
    } else {
      delete newError[`${TYPE.notiType}`];
    }

    setError(prev => ({...prev, ...newError}));
  }, [typeSelectNoti]);

  const getUserCreateTicket = async () => {
    try {
      const user = await TicketServices.getMultiLoginAccount([
        dataTicketRedux.createdBy,
      ]);
      if (user && user.length > 0) {
        setDataUserCreateTicket(user[0]);
      }
      console.log('user', user);
    } catch (error) {}
  };

  const renderRightAction = () => {
    return (
      <TopNavigationAction
        onPress={() => setisShow(true)}
        icon={<IconWorkflow width={20} height={20} />}
      />
    );
  };

  const handleChangeText = (type: TYPE, value: string) => {
    if (!value?.trim()) {
      setError(prevState => ({
        ...prevState,
        [type]: 'CM.empty',
      }));
    } else if (type === TYPE.email && !RegexUtils.FORMAT_EMAIL.test(value)) {
      setError(prevState => ({
        ...prevState,
        [type]: 'CM.validEmail',
      }));
    } else if (
      type === TYPE.phone &&
      !RegexUtils.REGEX_PHONE_NUMBER_9.test(value)
    ) {
      setError(prevState => ({
        ...prevState,
        [type]: 'CM.validPhone',
      }));
    } else {
      setError(prevState => {
        const newData = {...prevState};
        delete newData[type];
        return newData;
      });
    }
    setValueForm(prevState => {
      const newData = {
        ...prevState,
        [type]: value,
      };
      if (
        isEmpty(error) &&
        newData[TYPE.email]?.trim() &&
        newData[TYPE.phone]?.trim() &&
        newData[TYPE.name]?.trim()
      ) {
        setIsCanSubmit(true);
      } else {
        setIsCanSubmit(false);
      }
      return newData;
    });
  };

  const handleSubmit = async (type: 'reject' | 'finish') => {
    try {
      if (type === 'reject') {
        if (!note) {
          setIsShowNote(true);
          setNoteError('CM.empty');
          return;
        }
        setNoteError('');
        const body = {
          ...recipientDetaiImplLast?.recipient,
          reason: note,
        };
        const dataReject = await ReviewTicketServices.actionTicket(
          recipientDetaiImplLast?.id,
          recipientDetaiImplLast?.ticketId,
          recipientDetaiImplLast?.userTaskId,
          body,
          accessToken,
          'reject',
        );
        if (dataReject) {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.rejectSucesss'),
          });
          dispatch(
            ticketActions.setDataTicket({
              ...dataTicketRedux,
              ticketStatus: Status.rejected,
            }),
          );
          navigation.goBack();
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: t('CM.rejectFailed'),
          });
        }
      } else {
        const params = {
          name: modeler?.mailTemplate,
        };
        const notifyType = typeSelectNoti
          .map(noti => listNoti[noti.row].value)
          .join(',');
        let payload = {
          ticketId: dataTicketRedux.id,
          userTaskId: dataRouter?._userTaskId,
          recipientDetailsId: dataRouter?._recipientDetailsId,
          fullName: valueForm[TYPE.name],
          phoneNumber: valueForm[TYPE.phone],
          email: valueForm[TYPE.email],
          notifyType: notifyType,
        };
        const dataCreateEkyc = await ReviewTicketServices.createPersonalEkyc(
          {
            personalName: valueForm[TYPE.name],
            telephoneNumber: valueForm[TYPE.phone],
            email: valueForm[TYPE.email],
            messageType: notifyType,
            resourceType: 'internal',
            recipientDetailId: dataRouter?._recipientDetailsId,
            identifierType: '2', //1: chi ocr //2: ocr + ekyc // 3:ocr + ekyc + idcheck,
            ticketId: dataTicketRedux.id,
            userTaskId: dataRouter?._userTaskId,
            usesContent: {},
            syncToPartner: '0', //1 l c chuyn sang i tc/ 0 l khng chuyn,'
            versionId: modeler?.formVersion,
            formId: formData?.formId,
          },
          params,
        );

        if (dataCreateEkyc) {
          const data = await ReviewTicketServices.createInformationOCR(payload);
          if (data) {
            Toast.show({
              type: 'success',
              text1: t('CM.noti'),
              text2: t('RV.eid.submitSuccess'),
            });
            setDataQrCode(dataCreateEkyc.urlSuffix);
          } else {
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('RV.eid.submitError'),
            });
          }
        }
      }
    } catch (error) {
      if (type === 'reject') {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.rejectFailed'),
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('RV.eid.submitError'),
        });
      }
    }
  };

  const getDisable = () => {
    if (recipientDetaiImplLast?.recipientStatus === null) return false;
    if (recipientDetaiImplLast?.recipientStatus === Status.processing)
      return false;
    return true;
  };

  const getShowData = () => {
    if (recipientDetaiImplLast?.recipientStatus === Status.completed)
      return true;
    if (recipientDetaiImplLast?.recipientStatus === Status.accepted)
      return true;
    return false;
  };

  return (
    <SafeAreaView style={{flex: 1, backgroundColor: '#fff'}} edges={['bottom']}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('RV.eid.idPerson')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        renderContentRight={renderRightAction}
      />

      <KeyboardAwareScrollView>
        <View style={styles.ctnBody}>
          <View style={styles.ctnSubBody}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowInfo(!isShowInfo)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowInfo ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>{t('CM.infoCommon')}</TextCM>
            </Pressable>

            {isShowInfo && (
              <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                <InfoBodyScreen dataInfo={dataInfo} />
              </View>
            )}
          </View>
        </View>

        <View style={styles.ctnBody}>
          <View
            style={[
              styles.ctnSubBody,
              getDisable() && {
                marginBottom: 16,
              },
            ]}>
            <Pressable
              style={[styles.ctnHeader]}
              onPress={() => setIsShowInfoID(!isShowInfoID)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowInfoID ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>
                {t('RV.eid.infoPerson')}{' '}
                <TextCM style={styles.redDot}>*</TextCM>
              </TextCM>
            </Pressable>

            {isShowInfoID && (
              <View style={styles.ctnInput}>
                {!getDisable() && (
                  <>
                    <InputCM
                      isRequired={true}
                      label={t('RV.eid.name')}
                      placeholder={t('RV.eid.fillName')}
                      value={valueForm[`${TYPE.name}`]}
                      onChangeText={text => handleChangeText(TYPE.name, text)}
                      captionText={t(error[`${TYPE.name}`])}
                      status={error[`${TYPE.name}`] ? 'danger' : 'basic'}
                      type={TYPE.name}
                    />
                    <InputCM
                      isRequired={true}
                      label={t('RV.eid.phone')}
                      placeholder={t('RV.eid.fillPhone')}
                      value={valueForm[`${TYPE.phone}`]}
                      onChangeText={text => handleChangeText(TYPE.phone, text)}
                      captionText={t(error[TYPE.phone])}
                      status={error[`${TYPE.phone}`] ? 'danger' : 'basic'}
                      type={TYPE.phone}
                      keyBoardType="numeric"
                    />
                    <InputCM
                      isRequired={true}
                      label={'Email'}
                      placeholder={t('RV.eid.fillEmail')}
                      value={valueForm[`${TYPE.email}`]}
                      onChangeText={text => handleChangeText(TYPE.email, text)}
                      captionText={t(error[`${TYPE.email}`])}
                      status={error[`${TYPE.email}`] ? 'danger' : 'basic'}
                      type={TYPE.email}
                    />

                    <DropdownListCM
                      onSelect={setTypeSelectNoti}
                      valueSelect={typeSelectNoti}
                      placeholder={t('RV.eid.fill-notify-type')}
                      label={t('RV.eid.notify-type')}
                      isRequired={true}
                      size="medium"
                      listData={listNoti}
                      multiple={true}
                      caption={t(error[`${TYPE.notiType}`])}
                      status={error[`${TYPE.notiType}`] ? 'danger' : 'primary'}
                    />

                    {dataQrCode && (
                      <>
                        <TextCM style={styles.title}>{t('RV.eid.qr')}</TextCM>
                        <View style={styles.ctnQr}>
                          <QRCode
                            onError={(err: any) => console.log(err)}
                            value={getConfig().BASE_URL_EKYC + dataQrCode}
                          />
                        </View>
                      </>
                    )}
                  </>
                )}
                {getShowData() && (
                  <>
                    <Divider style={{height: 3}} />
                    <EkycData
                      ticketId={dataTicketRedux.id}
                      userTaskId={recipientDetaiImplLast?.userTaskId}
                      versionForm={recipientDetaiImplLast?.versionFormId}
                    />
                  </>
                )}
              </View>
            )}
          </View>
        </View>

        {getShowData() && (
          <EkycInfo
            ticketId={dataTicketRedux.id}
            userTaskId={recipientDetaiImplLast?.userTaskId}
          />
        )}

        {!getDisable() && (
          <View style={styles.ctnBody}>
            <View
              style={[
                styles.ctnSubBody,
                {
                  marginBottom: 16,
                },
              ]}>
              <Pressable
                style={styles.ctnHeader}
                onPress={() => setIsShowNote(!isShowNote)}>
                <Icon
                  fill={'#6F2CED'}
                  width={16}
                  height={16}
                  name={isShowNote ? 'arrow-circle-down' : 'arrow-circle-right'}
                />
                <TextCM style={styles.textTitle}>{t('CM.note')}</TextCM>
              </Pressable>

              {isShowNote && (
                <View
                  style={{paddingHorizontal: 10, paddingBottom: 10, rowGap: 4}}>
                  <TextFieldCM
                    isRequired={true}
                    label={t('CM.fillNote')}
                    value={note}
                    onChangeText={text => {
                      setNote(text);
                      setNoteError('');
                    }}
                    multiline
                    maxLength={400}
                    errorMessage={t(noteError)}
                  />
                </View>
              )}
            </View>
          </View>
        )}
      </KeyboardAwareScrollView>

      <View style={styles.ctnFooter}>
        {!getDisable() &&
          dataTicketRedux.ticketStatus !== Status.voided &&
          dataTicketRedux.ticketStatus !== Status.rejected && (
            <>
              <TouchableOpacity onPress={() => handleSubmit('reject')}>
                <IconRejectReview width={36} height={36} />
              </TouchableOpacity>
              <ButtonCM
                disabled={isEmpty(error) && isCanSubmit ? false : true}
                style={{flex: 1}}
                label={t('CM.confirm')}
                onPress={() => handleSubmit('finish')}
              />
            </>
          )}
      </View>

      <ModalFullScreen
        isVisible={isShow}
        children={<StepCM onClose={() => setisShow(false)} />}
      />

      <ModalFullScreen
        isVisible={isShowForm}
        children={
          <>
            <View style={{flex: 1}}>
              <Flow />
            </View>
            <ButtonCM
              style={{backgroundColor: '#f2ecfd', marginHorizontal: 16}}
              styleLabel={{color: '#6d1fad'}}
              label={t('CM.back')}
              onPress={() => setIsShowForm(false)}
            />
          </>
        }
      />
    </SafeAreaView>
  );
};

export default ReviewEIDScreen;
