import {View, TouchableOpacity} from 'react-native';
import styles from './styles';
import {useState} from 'react';
import {IconStar, IconStarOutline} from '../../../../../assets/images';
import {useTranslation} from 'react-i18next';
import React from 'react';

import {useAppDispatch, useAppSelector} from '../../../../../hooks/useRedux';
import TextFieldCM from '../../../../components/TextField';
import ModalConfirmCM from '../../../../components/ModalConfirm';
import {Color} from '../../../../../constants';

import {Spinner} from '@ui-kitten/components';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {ticketActions} from '../../../../../redux/slices';
import Toast from 'react-native-toast-message';
type Props = {
  isVisible: boolean;
  toogleRateNow: () => void;
};
const RateNow = ({isVisible, toogleRateNow}: Props) => {
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const [rate, setRate] = useState('');
  const [rateStar, setRateStar] = useState(-1);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const ListServices = useTicketServices();
  const handleStarPress = (index: number) => {
    setRateStar(index);
  };

  const handleSendRate = async () => {
    let formBody = {
      id: dataTicketRedux.id,
      rate: rateStar + 1,
      rateNote: rate,
      ticketStatus: 'completed',
    };
    try {
      setIsLoading(true);
      const dataResp = await ListServices.sendRateTicket(formBody);
      if (dataResp) {
        dispatch(
          ticketActions.setDataTicket({
            ...dataTicketRedux,
            rate: dataResp?.rate,
          }),
        );
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('TK.rateSuccess'),
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('TK.rateFaile'),
        });
      }
      toogleRateNow();
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading) {
    return (
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center'}}>
        <Spinner size="large" />
      </View>
    );
  }
  return (
    <ModalConfirmCM
      isVisible={isVisible}
      onClose={toogleRateNow}
      title={`${t('TK.rate_now')}`}
      type="rate"
      labelCancel={t('CM.labelCancel')}
      labelConfirm={t('TK.rate')}
      styleBtnRight={{backgroundColor: Color.completed}}
      onConfirm={() => handleSendRate()}
      renderContent={() => (
        <View style={{marginVertical: 6}}>
          <View style={styles.starContainer}>
            {new Array(5).fill(1).map((_, index) => (
              <TouchableOpacity
                key={index}
                onPress={() => handleStarPress(index)}>
                {index > rateStar ? (
                  <IconStarOutline width={55} height={50} fill={Color.gray} />
                ) : (
                  <IconStar width={55} height={50} fill={Color.yellow} />
                )}
              </TouchableOpacity>
            ))}
          </View>
          <TextFieldCM
            value={rate}
            onChangeText={setRate}
            multiline
            isFloatingLabel={true}
            label={t('TK.enter_comment')}
            maxLength={400}
          />
        </View>
      )}
    />
  );
};

export default RateNow;
