import {ScrollView, TouchableOpacity, View} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {IOrganization, IOrganizationNode} from '../share/interface';
import {useEffect, useState} from 'react';
import {
  CheckBox,
  Divider,
  Icon,
  Layout,
  MenuItem,
  OverflowMenu,
} from '@ui-kitten/components';
import styles from './styles';
import TextCM from '../../../../components/Text';
import {typeLimit} from '../share/helper';
import {OptionPermisson} from '../share/enum';
import ButtonCM from '../../../../components/Button';
import Toast from 'react-native-toast-message';
import {useTranslation} from 'react-i18next';

type Props = {
  toogleFunc?: () => void;
  toogleReload: () => void;
  fileId: string;
};

const SelectOrg = ({toogleFunc, toogleReload, fileId}: Props) => {
  const {t} = useTranslation();
  const {dataAccount} = useAppSelector(state => state.account);
  const [data, setData] = useState<IOrganizationNode[]>([]);
  const [listDataOrg, setListDataOrg] = useState<IOrganization[]>([]);
  const ReviewServices = useReviewServices();

  const [menuVisible, setMenuVisible] = useState(false);
  const [dataSelect, setDataSelect] = useState(typeLimit[0]);
  const [checkedItems, setCheckedItems] = useState<Set<string>>(new Set());
  const [expandedItems, setExpandedItems] = useState<Set<string>>(new Set());
  const toogleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  useEffect(() => {
    getDataOrg();
  }, []);

  const handleChangeAction = (value: any) => {
    setDataSelect(value);
    toogleMenu();
  };

  const getColor = () => {
    switch (dataSelect.value) {
      case OptionPermisson.editor:
        return {bg: '#ef5da81a', color: '#ef5da8'};
      case OptionPermisson.reviewer:
        return {bg: '#f7931e1a', color: '#f7931e'};
      case OptionPermisson.viewer:
        return {bg: '#1382fd1a', color: '#1382fd'};
      default:
    }
  };

  const getDataOrg = async () => {
    const sourceOrgIn = dataAccount?.orgIn?.split('/').slice(0, 3).join('/');
    const params = {
      orgIn: sourceOrgIn ?? '/1833/5390',
    };
    const data = await ReviewServices.getDataOrgByOrgin(params);
    setListDataOrg(data);
    const dataFilter = convertFlatToNestedData(data, null);
    setData(dataFilter);
  };
  const convertFlatToNestedData = (
    data: IOrganization[],
    parentId: any,
  ): IOrganizationNode[] => {
    const nodes = data.filter(
      node => node.parentId === parentId && !node.blocked,
    );
    let nodesData: IOrganizationNode[] = [];
    if (nodes && nodes.length) {
      nodes.forEach(node => {
        const nodeItem: IOrganizationNode = {
          item: node,
          children: [],
        };
        nodeItem.children = convertFlatToNestedData(data, node.id);
        nodesData = [...nodesData, nodeItem];
      });

      return nodesData;
    } else {
      return [];
    }
  };

  const handleCheckAll = (checked: boolean) => {
    const allIds = data.flatMap(node => getAllIds(node));
    setCheckedItems(checked ? new Set(allIds) : new Set());
  };

  const handleCheck = (node: IOrganizationNode, checked: boolean) => {
    const newCheckedItems = new Set(checkedItems);
    const idsToUpdate = getAllIds(node);

    if (checked) {
      idsToUpdate.forEach(id => newCheckedItems.add(id));
    } else {
      idsToUpdate.forEach(id => newCheckedItems.delete(id));
    }

    // Update parent nodes
    updateParentNodes(data, node, newCheckedItems);

    setCheckedItems(newCheckedItems);
  };

  const updateParentNodes = (
    nodes: IOrganizationNode[],
    currentNode: IOrganizationNode,
    checkedSet: Set<string>,
  ) => {
    for (const node of nodes) {
      if (
        node.children &&
        node.children.some(child => child.item.id === currentNode.item.id)
      ) {
        const allChildrenChecked = node.children.every(child =>
          checkedSet.has(child.item.id),
        );
        if (allChildrenChecked) {
          checkedSet.add(node.item.id);
        } else {
          checkedSet.delete(node.item.id);
        }
        updateParentNodes(data, node, checkedSet);
        break;
      } else if (node.children) {
        updateParentNodes(node.children, currentNode, checkedSet);
      }
    }
  };

  const handleExpand = (id: string) => {
    const newExpandedItems = new Set(expandedItems);
    if (newExpandedItems.has(id)) {
      newExpandedItems.delete(id);
    } else {
      newExpandedItems.add(id);
    }
    setExpandedItems(newExpandedItems);
  };

  const getAllIds = (node: IOrganizationNode): string[] => {
    let ids = [node.item.id];
    if (node.children) {
      node.children.forEach(child => {
        ids = [...ids, ...getAllIds(child)];
      });
    }
    return ids;
  };

  const isPartiallyChecked = (node: IOrganizationNode): boolean => {
    if (!node.children) return false;
    const childIds = getAllIds(node);
    const checkedChildIds = childIds.filter(id => checkedItems.has(id));
    return (
      checkedChildIds.length > 0 && checkedChildIds.length < childIds.length
    );
  };

  const renderTreeNode = (node: IOrganizationNode, level: number = 0) => {
    const isChecked = checkedItems.has(node.item.id);
    const hasChildren = node.children && node.children.length > 0;
    const isExpanded = expandedItems.has(node.item.id);
    const isPartial = isPartiallyChecked(node);

    return (
      <View
        key={node.item.id}
        style={{marginLeft: level === 0 ? 0 : 40, marginBottom: 10}}>
        <View style={styles.nodeContainer}>
          {hasChildren && (
            <TouchableOpacity onPress={() => handleExpand(node.item.id)}>
              <Icon
                name={
                  isExpanded ? 'chevron-down-outline' : 'chevron-right-outline'
                }
                fill="#8F9BB3"
                style={styles.icon}
              />
            </TouchableOpacity>
          )}
          <CheckBox
            checked={isChecked}
            indeterminate={isPartial}
            onChange={checked => handleCheck(node, checked)}
            style={styles.checkbox}>
            {node.item.name}
          </CheckBox>
        </View>
        {hasChildren && isExpanded && (
          <View style={{marginTop: 10}}>
            {node.children!.map(child => renderTreeNode(child, level + 1))}
          </View>
        )}
      </View>
    );
  };

  const allIds = data.flatMap(node => getAllIds(node));

  const renderOverFlowMenu = () => {
    return (
      <OverflowMenu
        anchor={() => (
          <TouchableOpacity
            style={[
              {
                backgroundColor: getColor()?.bg,
              },
              styles.ctnOverflow,
            ]}
            onPress={toogleMenu}>
            <TextCM style={{color: getColor()?.color}}>
              {t(dataSelect.label)}
            </TextCM>
            <Icon
              name={!menuVisible ? 'arrow-down' : 'arrow-up'}
              width={16}
              height={16}
              fill={getColor()?.color}
            />
          </TouchableOpacity>
        )}
        visible={menuVisible}
        onBackdropPress={toogleMenu}>
        {typeLimit.map(action => (
          <MenuItem
            key={action.value}
            title={props => <TextCM {...props}>{t(action.label)}</TextCM>}
            onPress={() => handleChangeAction(action)}
          />
        ))}
      </OverflowMenu>
    );
  };

  const handleSubmitOrg = async () => {
    const listDataSelect = listDataOrg.filter(org =>
      Array.from(checkedItems).includes(org.id),
    );

    const body = listDataSelect.map(org => ({
      assignee: org.name,
      assigneeId: org.id,
      perm: dataSelect.value,
      permType: 1,
    }));
    const params = {
      resourceId: fileId,
    };

    const dataResp = await ReviewServices.updatePermissionOrg(params, body);

    if (dataResp) {
      Toast.show({
        type: 'success',
        text1: t('CM.info'),
        text2: t('CM.permissionUpdatedSuccessfully'),
      });
      toogleReload();
      toogleFunc?.();
    }
  };

  return (
    <Layout style={{flex: 1}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('CM.share')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        onPressIconLeft={toogleFunc}
      />
      <Divider />
      <View style={{padding: 10, flex: 1}}>
        <View style={styles.ctnCheckBox}>
          <CheckBox
            checked={checkedItems.size === allIds.length}
            indeterminate={
              checkedItems.size > 0 && checkedItems.size < allIds.length
            }
            onChange={handleCheckAll}>
            {t('CM.all')}
          </CheckBox>
          {renderOverFlowMenu()}
        </View>
        <ScrollView style={{}}>
          {data.map(node => renderTreeNode(node))}
        </ScrollView>
      </View>
      <View style={{padding: 16}}>
        <ButtonCM label={t('CM.confirm')} onPress={handleSubmitOrg} />
      </View>
    </Layout>
  );
};

export default SelectOrg;
