import {FlatList, Image, Pressable, TouchableOpacity, View} from 'react-native';
import TextCM from '../../../../components/Text';
import {useTranslation} from 'react-i18next';
import styles from './styles';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {Avatar, Card, Icon, List, Modal} from '@ui-kitten/components';
import {IconPersonTime, IconPersonTimeWait} from '../../../../../assets/images';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {ITasksSLA} from '../../interface';
import {Status} from '../../../../../model/enum/common.enum';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {useEffect, useMemo, useState} from 'react';
import {IAccount} from '../../../../../model/interface/account.interface';
import {Font, String} from '../../../../../constants';
import {DateUtils} from '../../../../../utils';
import InfoBodyScreen from '../Info';
import React from 'react';
import {IAclListUserTask} from './interface';
const StepCM = ({onClose}: {onClose: () => void}) => {
  const {t} = useTranslation();

  const [listUsers, setListUser] = useState<IAccount[]>([]);
  const [isShowModal, setIsShowModal] = useState(false);
  const [dataSelect, setDataSelect] = useState<any>({});
  const [aclListUser, setAclListUser] = useState<IAclListUserTask[]>([]);
  const [listUserTask, setListUserTask] = useState<any[]>([]);
  const [showMoreOwner, setShowMoreOwner] = useState(false);
  const [authorStatusMap, setAuthorStatusMap] = useState<{
    [key: string]: boolean;
  }>({});
  const {dataListRecipientsSLA, dataTicketRedux, recipientDetaiImpl} =
    useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);

  const ListServices = useTicketServices();

  const toogleMoreOwner = () => setShowMoreOwner(!showMoreOwner);

  useEffect(() => {
    handleInitData();
  }, []);

  const handleInitData = async () => {
    const listUserTaskSla = dataListRecipientsSLA?.filter(
      (item, index, self) =>
        index === self.findIndex(t => t.recipientId === item.recipientId),
    );
    let listUserTask = listUserTaskSla?.map(item => {
      return {
        notionName: item.notionName ? item.notionName : 'User task',
        // ticketName: this.data.ticketName,
        recipientId: item.recipientId,
        status: item.status,
        startDate: item.startDate,
        finishDate: item.finishDate,
        role: item.role,
        delayTime: item.delayTime,
        estimateDate: item.estimateDate,
        totalTime: item.totalTime,
        statusSla: getStatusSLA(item),
        totalTimeExpect: item.totalTimeExpect,
      };
    });

    const dataAccount = await getMultiAccount();
    if (dataAccount) {
      const dataFilter = recipientDetaiImpl.map(task => {
        const data = dataAccount.find(
          (info: any) => info.email === task.recipient.contact.email,
        );

        if (data) {
          return {
            ...task,
            avatar: data.imageUrl,
            firstName: data.firstName,
            lastName: data.lastName,
            personalName: data.personalName,
          };
        }
        return task;
      });

      listUserTask = listUserTask.map(item => {
        const executor = dataFilter.filter(
          executor =>
            executor.userTaskId === item.recipientId &&
            executor.recipientStatus,
        )[0];
        const executorList = dataFilter.filter(
          executor =>
            executor.userTaskId === item.recipientId &&
            executor.status === 'active',
        );

        return {
          ...item,
          executor: executor,
          executorList: executorList,
        };
      });
      setListUserTask(listUserTask);
    }

    const dataAclListUser = await getAuthorizationOfTask();
    if (dataAclListUser) {
      listUserTask.forEach(userTask => {
        setAuthorStatusMap(prev => {
          const newData = {...prev};
          newData[userTask?.recipientId] = checkAuthor(
            userTask?.executorList,
            userTask.recipientId,
            dataAclListUser,
          );
          return newData;
        });
      });
    }
  };

  const checkAuthor = (executorList: any[], recipientId: any, perms: any[]) => {
    let checkExecutor = recipientDetaiImpl.some(
      item =>
        item.recipient.contact.email === dataAccount.email &&
        item.userTaskId === recipientId,
    );
    const hasAuthor = executorList.some(
      item => item.recipient.contact.email === dataAccount.email,
    );
    let checkAuthorPerm = perms.some(
      item =>
        item.personal.email === dataAccount.email &&
        (item.userTaskId === recipientId || !item.userTaskId),
    );
    return !(checkAuthorPerm || checkExecutor || hasAuthor);
  };

  const getNotionName = (notionName: string | undefined) => {
    if (notionName === 'eID') {
      return t('RV.notionName.eID');
    }
    if (notionName === 'eContract') {
      return t('RV.notionName.eContract');
    }
    return notionName || t('RV.notionName.userTask');
  };

  const getMultiAccount = async () => {
    try {
      const listEmail = recipientDetaiImpl.map(
        data => data.recipient.contact.email,
      ) as any;
      if (listEmail) {
        const data = await ListServices.getMultiLoginAccount(
          Array.from(new Set(listEmail)),
          accessToken,
        );
        if (data) {
          setListUser(data);
          return data;
        }
      }
    } catch (error) {}
  };

  const getAuthorizationOfTask = async () => {
    const listUserTaskId = recipientDetaiImpl.map(data => data.userTaskId);
    const body = {
      resourceId: dataTicketRedux.id,
      email: dataAccount.email || '',
      userTaskIds: [...new Set(listUserTaskId)],
      orgIn: dataAccount.orgIn || '',
    };
    const dataResp = await ListServices.getAclTicketByListUserTask(body);
    if (dataResp) {
      setAclListUser(dataResp);
      return dataResp;
    }
  };

  const getStatusSLA = (item: ITasksSLA) => {
    let finishDate;
    let estimateDate;
    if (
      dataTicketRedux.ticketStatus === Status.voided &&
      item.status == Status.processing
    ) {
      return 'stopped';
    }
    if (dataTicketRedux.ticketStatus === Status.rejected) {
      return 'rejected';
    }
    if (item.finishDate && item.estimateDate) {
      finishDate = new Date(item.finishDate);
      estimateDate = new Date(item.estimateDate);
    }
    if (item.status == 'accepted' && item.delayTime) {
      return 'done_late';
    } else if (item.status == 'accepted' && item.estimateDate) {
      return 'done_onTime';
    } else if (
      item.status == 'accepted' &&
      item.finishDate &&
      item.estimateDate &&
      finishDate < estimateDate
    ) {
      return 'done_before';
    } else {
      return item.status;
    }
  };

  const getValueUser = (data: any) => {
    return (
      <Pressable style={styles.ctnListUser}>
        {data?.map((user: any, index: any) => (
          <>
            {index <= 1 ? (
              <Image
                key={index}
                style={{position: 'absolute', left: index * 20}}
                width={28}
                height={28}
                borderRadius={14}
                source={{
                  uri: user?.['avatar'] || String.DEFAULT_IMAGE,
                }}
              />
            ) : (
              <View key={index} style={styles.ctnAdd}>
                <TextCM>{`+${data?.length - 2}`}</TextCM>
              </View>
            )}
          </>
        ))}
      </Pressable>
    );
  };

  const getValueListUser = () => {
    if (dataSelect?.executor) {
      return {
        key: 'RV.userAction',
        type: 'avatar',
        value: {
          name: dataSelect.executor?.personalName || '---',
          email: dataSelect.executor?.recipient?.contact?.email || '---',
          avatarUrl: dataSelect.executor?.avatar,
        },
      };
    }
    if (dataSelect.executorList) {
      if (dataSelect?.executorList?.length > 1) {
        return {
          key: 'RV.userAction',
          type: 'normal',
          value: {
            onlyIcon: getValueUser(dataSelect?.executorList),
          },
        };
      } else {
        const dataExcouter = dataSelect?.executorList[0];
        return {
          key: 'RV.userAction',
          type: 'avatar',
          value: {
            name: dataExcouter?.personalName || '---',
            email: dataExcouter?.recipient?.contact?.email || '---',
            avatarUrl: dataExcouter?.avatar,
          },
        };
      }
    }
  };

  const dataInfo: any[] = useMemo(() => {
    return [
      getValueListUser(),
      {
        key: 'HM.statusTicket',
        type: 'status',
        value: {
          status: dataSelect?.statusSla,
        },
      },
      {
        key: 'RV.startDate',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(dataSelect?.startDate),
        },
      },
      {
        key: 'RV.totalTimeExpect',
        type: 'normal',
        value: {
          name: dataSelect?.totalTimeExpect,
        },
      },
      {
        key: 'RV.estimateDate',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(dataSelect?.estimateDate),
        },
      },
      {
        key: 'RV.finishDate',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(dataSelect?.finishDate),
        },
      },
    ];
  }, [dataSelect]);

  const getData = (status: any) => {
    if (status === Status.rejected) {
      return {
        fill: '#e14437',
        icon: 'close-circle',
        bgColor: '#fdeded',
        borderColor: '#e14437',
      };
    }
    if (status === Status.processing) {
      return {
        fill: '#FC6B03',
        icon: 'alert-triangle',
        bgColor: '#FFF3EB',
        borderColor: '#e14437',
      };
    }
    return {
      fill: '#39AC6D',
      icon: 'checkmark-circle-2',
      bgColor: '#E8F7EF',
      borderColor: '#fff',
    };
  };

  const renderItem = ({item, index}: {item: any; index: number}) => {
    return (
      <>
        <View style={styles.ctnItem}>
          <View style={styles.ctnIcon}>
            <Icon
              fill={getData(item.status).fill}
              name={getData(item.status).icon}
              width={24}
              height={24}
            />
            {
              <View
                style={[
                  styles.tabHoz,
                  dataListRecipientsSLA.length - 1 === index && {
                    backgroundColor: '#F8FAFE',
                  },
                ]}></View>
            }
          </View>
          <View style={{flex: 1}}>
            <View
              style={[
                styles.ctnBody,
                {
                  borderColor: getData(item.status).borderColor,
                  opacity: !authorStatusMap[item.recipientId] ? 1 : 0.5,
                },
              ]}>
              <TextCM style={styles.textNotion}>
                {getNotionName(item.notionName)}
              </TextCM>
              {authorStatusMap[item.recipientId] && (
                <TextCM style={styles.textNote}>{t('RV.author')}</TextCM>
              )}
              <View
                style={{
                  flexDirection: 'row',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                }}>
                <View style={styles.ctnEmail}>
                  {item.executor && (
                    <>
                      <Avatar
                        size="small"
                        source={{
                          uri:
                            item.executor?.avatar ||
                            'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                        }}
                      />
                      <TextCM style={styles.textEmail}>
                        {item.executor?.recipient.contact.email}
                      </TextCM>
                    </>
                  )}

                  {!item.executor && item.executorList && (
                    <Pressable
                      onPress={() => {
                        toogleMoreOwner();
                        setDataSelect(item);
                      }}
                      style={{
                        position: 'relative',
                        flexDirection: 'row',
                        alignItems: 'center',
                      }}>
                      {item.executorList.map((executor: any, index: any) => (
                        <>
                          {index <= 1 && (
                            <Avatar
                              style={{position: 'absolute', left: index * 20}}
                              key={index}
                              size="small"
                              source={{
                                uri:
                                  executor?.avatar ||
                                  'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                              }}
                            />
                          )}
                        </>
                      ))}
                      {item.executorList.length > 2 && (
                        <View style={styles.ctnMoreAvatar}>
                          <TextCM>{`+${item.executorList.length - 2}`}</TextCM>
                        </View>
                      )}
                    </Pressable>
                  )}
                </View>
                <TouchableOpacity
                  onPress={() => {
                    if (authorStatusMap[item.recipientId]) return;
                    setDataSelect(item);
                    setIsShowModal(true);
                  }}
                  style={[
                    styles.ctnIconClick,
                    {
                      backgroundColor: getData(item.status).bgColor,
                    },
                  ]}>
                  {item.status !== Status.processing ? (
                    <IconPersonTime width={15} height={15} />
                  ) : (
                    <IconPersonTimeWait width={15} height={15} />
                  )}
                </TouchableOpacity>
              </View>
            </View>
            {item.status === Status.processing && (
              <TextCM style={styles.textNote}>
                {t('RV.descriptionProcessing')}
              </TextCM>
            )}
          </View>
        </View>
      </>
    );
  };
  return (
    <>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('RV.listStep')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        onPressIconLeft={onClose}
      />
      <FlatList
        style={{backgroundColor: '#F8FAFE'}}
        data={listUserTask}
        renderItem={renderItem}
      />

      <Modal
        animationType="fade"
        backdropStyle={{backgroundColor: 'rgba(0, 0, 0, 0.5)'}}
        onBackdropPress={() => setIsShowModal(false)}
        visible={isShowModal}
        style={{width: '90%'}}>
        <Card
          style={{}}
          header={
            <TextCM style={styles.txtTitle}>
              {getNotionName(dataSelect?.notionName)}
            </TextCM>
          }
          disabled={true}>
          {<InfoBodyScreen dataInfo={dataInfo} />}
        </Card>
      </Modal>

      <Modal
        animationType="fade"
        backdropStyle={{backgroundColor: 'rgba(0, 0, 0, 0.5)'}}
        onBackdropPress={toogleMoreOwner}
        visible={showMoreOwner}
        style={{width: '90%'}}>
        <Card
          style={{}}
          header={<TextCM style={styles.txtTitle}>{'List user'}</TextCM>}
          disabled={true}>
          <FlatList
            data={dataSelect.executorList}
            renderItem={({item}) => (
              <View style={styles.ctnView}>
                <Image
                  width={24}
                  height={24}
                  borderRadius={12}
                  source={{
                    uri: item.avatar || String.DEFAULT_IMAGE,
                  }}
                />
                <View style={{flex: 1}}>
                  {/* <TextCM
                    style={[
                      styles.txtValue,
                      {
                        fontFamily: Font.InterSemiBold600,
                      },
                    ]}>
                    {item?.firstName + ' ' + item?.lastName || '---'}
                  </TextCM> */}
                  <TextCM style={styles.txtValue}>
                    {item?.recipient?.contact?.email || '---'}
                  </TextCM>
                </View>
              </View>
            )}
          />
        </Card>
      </Modal>
    </>
  );
};

export default StepCM;
