import {View} from 'react-native';
import {IParty, IRecipient} from '../../interface';
import TextCM from '../../../../../components/Text';
import RecipientScreen from '../recipient';
import styles from './styles';
import {listTypeUserAction} from '../../helper';
import {useTranslation} from 'react-i18next';

type Props = {
  party: IParty;
  index: number;
  error: {[key: string]: string};
  setError: any;
};

const PartyScreen = ({party, index, error, setError}: Props) => {
  const {t} = useTranslation();
  return (
    <View>
      <View style={styles.ctnMain}>
        <TextCM style={styles.text}>
          {t('RV.econtract.join')} {index}
        </TextCM>
      </View>
      <View style={styles.ctnRecipient}>
        {listTypeUserAction.map((item: string) => {
          const listRecipients = (party.recipient as any)[item] as IRecipient[];
          return listRecipients.map((recipient, idx) => (
            <RecipientScreen
              error={error}
              setError={setError}
              key={idx}
              recipient={recipient}
              type={item}
            />
          ));
        })}
      </View>
    </View>
  );
};

export default PartyScreen;
