import {Image, View} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import InputCM from '../../../review-eid/components/InputCM';
import TextCM from '../../../../components/Text';
import {useAppSelector} from '../../../../../hooks/useRedux';
import ButtonCM from '../../../../components/Button';
import CustomSelect from './ScrollPagingSelect';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {useEffect, useState} from 'react';
import styles from './styles';
import {IUserModal} from '../../../review-ecollabo-flow/components/share/interface';
import Toast from 'react-native-toast-message';
import {Color} from '../../../../../constants';

type Props = {
  onClose: () => void;
  userTaskId: string;
  recipientDetailsId: string;
};

const AuthorityUserAction = ({
  onClose,
  userTaskId,
  recipientDetailsId,
}: Props) => {
  const {t} = useTranslation();
  const [selectedItem, setSelectedItem] = useState<IUserModal | null>(null);
  const {dataAccount} = useAppSelector(state => state.account);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);

  const TicketServices = useTicketServices();

  const [taskSLA, setTaskSLA] = useState<any>();

  useEffect(() => {
    getTaskSLA();
  }, []);

  const getTaskSLA = async () => {
    const dataResp = await TicketServices.getSla(dataTicketRedux.id);
    if (dataResp) {
      const taskSla = dataResp.find((d: any) => d.recipientId == userTaskId);
      setTaskSLA(taskSla);
    }
  };

  const handleSelect = (selectedOption: IUserModal | null) => {
    setSelectedItem(selectedOption);
  };

  const handleSubmit = async () => {
    const body = {
      ticketId: dataTicketRedux.id,
      recipientId: null,
      contactId: null,
      email: selectedItem?.email,
      name: selectedItem?.personalName,
      phone: selectedItem?.phone,
      reason: 'null',
    };
    const dataResp = await TicketServices.forwardActionUser(
      dataTicketRedux.id,
      userTaskId,
      recipientDetailsId,
      body,
    );
    if (dataResp) {
      onClose();
      Toast.show({
        type: 'success',
        text1: t('CM.info'),
        text2: t('CM.forwardSuccessfully'),
      });
    } else {
      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('CM.forwardFail'),
      });
    }
  };

  return (
    <View style={{flex: 1}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        contentStyle={{
          backgroundColor: '#fff',
        }}
        title={t('CM.authority')}
        onPressIconLeft={onClose}
      />

      <View style={styles.ctnBody}>
        <InputCM
          label={t('TK.nameJob')}
          value={taskSLA?.notionName}
          onChangeText={() => {}}
          disabled
        />

        <View style={styles.group}>
          <TextCM style={styles.textTitle}>{t('TK.userAction')}</TextCM>
          <View style={styles.ctnImage}>
            <Image
              width={32}
              height={32}
              borderRadius={16}
              source={{
                uri:
                  dataAccount?.imageUrl ||
                  'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
            <View
              style={{
                flex: 1,
              }}>
              <TextCM
                style={
                  styles.textname
                }>{`${dataAccount.firstName} ${dataAccount.lastName}`}</TextCM>
              <TextCM style={styles.textEmail}>{dataAccount.email}</TextCM>
            </View>
          </View>
        </View>

        <View style={styles.group}>
          <TextCM style={styles.textTitle}>
            {t('TK.selectUserAuthor')}{' '}
            <TextCM
              style={{
                color: '#e14337',
              }}>
              *
            </TextCM>
          </TextCM>
          <CustomSelect onSelect={handleSelect} />
        </View>
      </View>

      <View style={styles.ctnFooter}>
        <ButtonCM
          style={{flex: 1, backgroundColor: '#F2F5F8'}}
          label={t('CM.labelCancel')}
          styleLabel={{
            color: '#00204DB2',
          }}
          onPress={onClose}
        />

        <ButtonCM
          style={{flex: 1}}
          label={t('CM.confirm')}
          onPress={handleSubmit}
        />
      </View>
    </View>
  );
};

export default AuthorityUserAction;
