import {useCallback, useMemo, useRef, useState} from 'react';
import {IComments, IContactDTO} from './interface';
import {useFocusEffect} from '@react-navigation/native';
import {SizeDP} from '../../../../constants/Size';
import {IconGetRelative, IconSend} from '../../../../assets/images';
import {
  FlatList,
  Image,
  Keyboard,
  KeyboardAvoidingView,
  Platform,
  Pressable,
  TextInput,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import styles from './styles';
import CommentItem from './component/CommentItem';
import {useAppSelector} from '../../../../hooks/useRedux';
import {
  decodeUnicodeToEmoji,
  encodeEmojiToUnicode,
  extractSpanList,
  splitStringWithSpans,
} from './helper';
import ImageCropPicker from '../../../components/ImageCropPicker';
import {useTicketServices} from '../../../../hooks/apiHooks/useTicketServices';
import {Avatar, Icon, List, ListItem, Spinner} from '@ui-kitten/components';
import HeaderCM from '../../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import TextCM from '../../../components/Text';
import {IAccount} from '../../../../model/interface/account.interface';
import {Font} from '../../../../constants';
import {Snackbar} from 'react-native-paper';
import React from 'react';

type Props = {
  onClose: () => void;
};

const CommunicationTicketScreen = ({onClose}: Props) => {
  const {t} = useTranslation();
  const [listComments, setListComments] = useState<IComments[]>([]);
  const [listUsers, setListUsers] = useState<any[]>([]);
  const [dataUser, setDataUser] = useState<IAccount[]>([]);
  const [listUsersFilter, setListUsersFilter] = useState<any[]>([]);
  const [comment, setComment] = useState<string>('');
  const [isPrivate, setIsPrivate] = useState<boolean>(false);
  const [refreshing, setRefreshing] = useState(false);
  const [fileSelect, setFileSelect] = useState<any>();
  const [targetComment, setTargetComment] = useState<IComments | null>(null);
  const [visibleSnackBar, setVisibleSnackBar] = useState(false);

  const [actionType, setActionType] = useState<'new' | 'reply' | 'edit'>('new');
  const [showUserList, setShowUserList] = useState(false);
  const [showListDataLoading, setshowListDataLoading] = useState<
    'loading' | 'done' | 'error'
  >('loading');
  const [listContactDTO, setListContactDTO] = useState<IContactDTO[]>([]);
  const inputRef = useRef<TextInput>(null);

  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {dataAccount} = useAppSelector(state => state.account);
  const ListServices = useTicketServices();
  const onToggleSnackBar = () => setVisibleSnackBar(!visibleSnackBar);

  useFocusEffect(
    useCallback(() => {
      getListComments();
    }, []),
  );

  const getListComments = async () => {
    try {
      setshowListDataLoading('loading');
      const params = {
        userTaskId: 'all',
        ticketId: dataTicketRedux.id,
        sort: 'lastModifiedDate,desc',
        page: 0,
        size: 100000,
      };
      const data = await ListServices.getListComments(params);
      if (data && data.content) {
        setListComments(data.content);

        const filterEmail = data.content.map(
          (item: IComments) => item.createdBy,
        ) as string[];
        const emailUser = dataAccount.email || '';
        const filterUsers = [...new Set([...filterEmail, emailUser])].filter(
          email => email.length > 0,
        );
        const dataUser = await ListServices.getMultiLoginAccount(filterUsers);
        setDataUser(dataUser || []);
        await getListUsers();
      }
    } catch (error) {
      console.log('Error get list comments:', error);
      setshowListDataLoading('error');
    } finally {
      setRefreshing(false);
    }
  };

  const commentMap = useMemo(() => {
    const map = new Map<any, IComments[]>();
    listComments.forEach(comment => {
      const parentId = comment.commentId ?? 'root';
      const siblings = map.get(parentId) || [];
      map.set(parentId, [...siblings, comment]);
    });
    return map;
  }, [listComments]);

  const handleChangeComment = (comment: string) => {
    const textSplit = comment.split(' ');
    const valueContent = textSplit
      .filter(text => !text.startsWith('@'))
      .join(' ');
    const listGmail = textSplit.filter(text => text.startsWith('@'));
    const textGmail = listGmail
      .map(
        gmail =>
          `<span id="#${new Date().getTime()}" class="user_tag" contenteditable="false" data-user-id="${
            dataAccount.id ?? 5845
          }" data-email="${gmail.slice(1)}" data-org-in="${
            dataAccount.orgIn ?? '/1833/5390/5392'
          }" data-cust-id="${dataAccount.custId ?? 1833}">${gmail}</span>`,
      )
      .join(' ');

    return encodeEmojiToUnicode(`${textGmail} ${valueContent}`);
  };

  const sendComment = async (
    commentParentId: any,
    fileSelect: any,
    isPrivate: boolean,
  ) => {
    try {
      const commentPayload = {
        content: handleChangeComment(comment),
        userTaskId: 'all',
        ticketId: dataTicketRedux.id,
        name: `${dataAccount.firstName} ${dataAccount.lastName}`,
        createdBy: dataAccount.email,
        commentType: 'comment',
        avatar: dataAccount.imageUrl,
        commentId: commentParentId,
        userId: dataAccount.id,
        orgIn: dataAccount.orgIn,
        custId: dataAccount.custId,
        contactDTO: listContactDTO,
        isPrivate: isPrivate,
      };
      const formData = new FormData();
      formData.append(
        'uploadFile',
        fileSelect
          ? {
              uri: fileSelect?.path,
              type: fileSelect?.mime,
              name: fileSelect?.path.split('/').pop(),
            }
          : null,
      );
      formData.append('commentJson', JSON.stringify(commentPayload));
      const data = await ListServices.sendComment(formData);
      if (data) {
        setListContactDTO([]);
        setListComments([data, ...listComments]);
        setFileSelect(null);
        Keyboard.dismiss();
      }
    } catch (error) {}
  };

  const handleReply = useCallback(async () => {
    if (actionType === 'reply' || actionType === 'new') {
      if (comment.trim() || fileSelect) {
        const replyingTo =
          targetComment && targetComment?.commentId
            ? targetComment?.commentId
            : targetComment?.id;
        await sendComment(replyingTo, fileSelect, isPrivate);
      }
    } else if (actionType === 'edit') {
      if (comment.trim()) {
        if (targetComment) {
          await updatedComment(
            targetComment?.id,
            targetComment?.commentId,
            fileSelect,
            isPrivate,
          );
        }
      }
    }
    setComment('');
    setActionType('new');
    setTargetComment(null);
  }, [comment, actionType, targetComment, fileSelect, isPrivate]);

  const deleteComment = async (commentId: string) => {
    try {
      const data = await ListServices.deleteComment(commentId);
      if (data) {
        handleDelete(commentId);
      }
    } catch (error) {}
  };

  const handleDelete = (commentId: any) => {
    setListComments(prevComments => {
      return prevComments?.filter(comment => comment.id !== commentId);
    });
  };

  const updatedComment = async (
    id: string,
    commentId: any,
    fileSelect: any,
    isPrivate: boolean,
  ) => {
    try {
      const commentPayload = {
        ...targetComment,
        content: handleChangeComment(comment),
        isPrivate: isPrivate,
        lastModifiedDate: new Date().toISOString(),
      };
      const formData = new FormData();
      formData.append(
        'uploadFile',
        fileSelect
          ? {
              uri: fileSelect?.path,
              type: fileSelect?.mime,
              name: fileSelect?.path.split('/').pop(),
            }
          : null,
      );
      formData.append('commentJson', JSON.stringify(commentPayload));
      const data = await ListServices.updateComment(formData);

      if (data) {
        setListComments(prevComments => {
          const newData = prevComments.map(commentData =>
            commentData.id === id ? data : commentData,
          );
          return newData;
        });
        Keyboard.dismiss();
      }
    } catch (error) {}
  };

  const getListUsers = async () => {
    try {
      const data = await ListServices.getListAclUser(dataTicketRedux.id);
      if (data) {
        const listEmail: string[] =
          data.map((item: any) => item?.personal?.email) || [];
        const dataUser = await ListServices.getMultiLoginAccount([
          ...new Set(listEmail),
        ]);
        if (dataUser) {
          setListUsers(dataUser);
        }
      }
    } catch (error) {
      console.log('Error get list users:', error);
    } finally {
      setshowListDataLoading('done');
    }
  };

  const handleChangeText = (text: string) => {
    setComment(text);
    const lastWord = text.split(' ').pop();
    if (lastWord?.startsWith('@') && lastWord.length > 0) {
      if (lastWord?.slice(1)?.length <= 0) {
        setListUsersFilter(listUsers);
        setShowUserList(true);
      } else {
        const query = lastWord?.slice(1)?.toLowerCase();
        setListUsersFilter(
          listUsers.filter(user => user?.email?.toLowerCase().includes(query)),
        );
        setShowUserList(true);
      }
    } else {
      setShowUserList(false);
    }
  };

  const handleReplyClick = useCallback(
    (action: 'reply' | 'edit', commentEdit: IComments) => {
      setActionType(action);
      if (action === 'reply') {
        const dataUser = listUsers.find(
          user => user.email == commentEdit.createdBy,
        );
        const userContactDto: IContactDTO = {
          id: dataUser?.resourceId,
          name: dataUser?.personalName,
          email: dataUser?.email,
          orgIn: dataUser?.orgIn,
          custId: dataUser?.custId,
          haveAvatar: dataUser?.imageUrl ? true : false,
          avatar: dataUser?.imageUrl,
          disabled:
            dataUser?.resourceId === dataAccount.id ||
            listContactDTO.some(ut => ut?.id === dataUser?.resourceId),
        };
        setListContactDTO(prev => [...prev, userContactDto]);
        handleChangeText(`@${commentEdit.createdBy} `);
      } else {
        handleChangeText(
          handleFilterCommentReply(decodeUnicodeToEmoji(commentEdit.content)),
        );
      }

      setTargetComment(commentEdit);
      inputRef.current?.focus();
    },
    [],
  );

  const handleFilterCommentReply = (data: string) => {
    const listSplit = splitStringWithSpans(data);
    const filterData = listSplit.map(text =>
      !text.includes('class="user_tag"')
        ? text
        : `${extractSpanList(text)?.[0]?.textContent}`,
    );
    return filterData.join(' ');
  };

  const choosePhoto = async () => {
    try {
      const data = (await ImageCropPicker.ChoosePhoto({
        width: 500,
        height: 500,
      })) as any;
      setFileSelect(data);
    } catch (error) {
      console.log(error);
    }
  };

  const handleUserSelect = (dataUser: any) => {
    const userContactDto: IContactDTO = {
      id: dataUser.resourceId,
      name: dataUser.personalName,
      email: dataUser.email,
      orgIn: dataUser.orgIn,
      custId: dataUser.custId,
      haveAvatar: dataUser.imageUrl ? true : false,
      avatar: dataUser.imageUrl,
      disabled:
        dataUser.resourceId === dataAccount.id ||
        listContactDTO.some(ut => ut.id === dataUser.resourceId),
    };
    setListContactDTO(prev => [...prev, userContactDto]);
    const words = comment.split(' ');
    words[words.length - 1] = `@${dataUser.email} `;
    setComment(words.join(' '));
    setShowUserList(false);
    inputRef.current?.focus();
  };

  const renderUserItem = ({item}: {item: any}) => {
    return (
      <ListItem
        onPress={() => handleUserSelect(item)}
        children={
          <View style={styles.ctnViewTag}>
            <Image
              width={24}
              height={24}
              borderRadius={12}
              source={{
                uri:
                  item?.imageUrl ||
                  'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
            <View>
              <TextCM
                style={{
                  fontSize: 14,
                  fontFamily: Font.InterSemiBold600,
                }}>
                {item.personalName}
              </TextCM>
              <TextCM>{item.email}</TextCM>
            </View>
          </View>
        }
      />
    );
  };

  const handleFocused = (text: string) => {
    const lastText = text?.split(' ').pop();
    if (lastText?.startsWith('@')) {
      setShowUserList(true);
    }
  };

  const onRefreshing = () => {
    setRefreshing(true);
    setshowListDataLoading('loading');
    getListComments();
  };

  return (
    <>
      <KeyboardAvoidingView
        keyboardVerticalOffset={50}
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
        style={{flex: 1}}>
        <TouchableWithoutFeedback>
          <>
            <HeaderCM
              style={{
                backgroundColor: '#fff',
              }}
              titleStyle={{
                color: '#00204D',
              }}
              fillIconBackLeft="#00204DB2"
              contentStyle={{
                backgroundColor: '#fff',
              }}
              title={t('TK.comment')}
              onPressIconLeft={onClose}
            />
            <View style={{flex: 1, padding: 16}}>
              <View style={{flex: 1, position: 'relative'}}>
                {showListDataLoading === 'done' && (
                  <FlatList
                    style={{flex: 1}}
                    refreshing={refreshing}
                    onRefresh={onRefreshing}
                    showsVerticalScrollIndicator={false}
                    contentContainerStyle={{rowGap: SizeDP(16)}}
                    data={commentMap?.get('root') || []}
                    renderItem={({item}) => (
                      <CommentItem
                        data={item}
                        user={dataUser.find(
                          user => user.email === item.createdBy,
                        )}
                        listDataUser={dataUser}
                        setComment={setComment}
                        deleteComment={deleteComment}
                        allComments={commentMap}
                        onReplyClick={handleReplyClick}
                        level={0}
                      />
                    )}
                    ListEmptyComponent={() => (
                      <View style={styles.emptyComment}>
                        <Icon
                          name="message-circle-outline"
                          width={30}
                          height={30}
                        />
                        <TextCM style={{textAlign: 'center'}}>
                          {t('TK.commentDescription')}
                        </TextCM>
                      </View>
                    )}
                  />
                )}

                {showListDataLoading === 'loading' && <Spinner size="small" />}

                {showListDataLoading === 'error' && (
                  <View>
                    <TextCM>{t('CM.emptyData')}</TextCM>
                  </View>
                )}

                {showUserList && listUsersFilter.length > 0 && (
                  <View style={styles.ctnModalListUser}>
                    <View style={styles.bodyModalListUser}>
                      <List
                        keyboardShouldPersistTaps="handled"
                        data={listUsersFilter}
                        renderItem={renderUserItem}
                      />
                    </View>
                  </View>
                )}
              </View>
              <View style={[styles.ctnSend]}>
                <View style={styles.ctnImg}>
                  {fileSelect && (
                    <>
                      {fileSelect.mime.includes('image') && (
                        <View style={styles.ctnImage}>
                          <Image
                            style={styles.image}
                            source={{uri: fileSelect.sourceURL}}
                          />
                          <Pressable
                            onPress={() => setFileSelect(null)}
                            style={styles.closeImage}>
                            <Icon width={12} height={12} name="close-outline" />
                          </Pressable>
                        </View>
                      )}
                    </>
                  )}
                  <View style={styles.ctnInput}>
                    <TextInput
                      ref={inputRef}
                      style={{
                        padding: SizeDP(12),
                        flex: 1,
                      }}
                      allowFontScaling={false}
                      multiline
                      placeholder={
                        actionType === 'new'
                          ? t('TK.addComment')
                          : actionType === 'reply'
                          ? t('TK.replyComment')
                          : t('TK.editComment')
                      }
                      value={comment}
                      onChangeText={handleChangeText}
                      onFocus={e => handleFocused(e.nativeEvent.text)}
                    />
                    <TouchableOpacity
                      onPress={choosePhoto}
                      hitSlop={SizeDP(15)}>
                      <IconGetRelative width={SizeDP(20)} height={SizeDP(20)} />
                    </TouchableOpacity>
                    <Pressable
                      style={{
                        marginHorizontal: 12,
                      }}
                      onPress={() => {
                        setIsPrivate(!isPrivate);
                        onToggleSnackBar();
                      }}>
                      <Icon
                        width={20}
                        height={20}
                        fill={'#39AC6D'}
                        name={!isPrivate ? 'globe-outline' : 'people-outline'}
                      />
                    </Pressable>
                    <Snackbar
                      style={{marginBottom: 20}}
                      onDismiss={onToggleSnackBar}
                      visible={visibleSnackBar}
                      duration={1000}>
                      {isPrivate
                        ? t('TK.commentPrivate')
                        : t('TK.commentPublic')}
                    </Snackbar>
                  </View>
                </View>
                <TouchableOpacity onPress={handleReply} hitSlop={SizeDP(15)}>
                  <IconSend width={SizeDP(24)} height={SizeDP(24)} />
                </TouchableOpacity>
              </View>
            </View>
          </>
        </TouchableWithoutFeedback>
      </KeyboardAvoidingView>
    </>
  );
};

export default CommunicationTicketScreen;
