import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import {Alert, Pressable, TouchableOpacity, View} from 'react-native';
import ButtonCM from '../../components/Button';
import {IconRejectReview, IconWorkflow} from '../../../assets/images';
import styles from './styles';
import {useEffect, useMemo, useState} from 'react';
import {SizeDP} from '../../../constants/Size';
import {useNavigation, useRoute} from '@react-navigation/native';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import {IPostMessageUserTask, STATUS_TASK} from './interface';
import {IJob, ITask} from './tab-view/components/BackgroundFlow/interface';
import {IRecipientTicketImpl} from '../../../model/interface/service.interface';
import NavigationName from '../../../constants/NavigationName';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import {Icon, List, TopNavigationAction} from '@ui-kitten/components';
import ModalFullScreen from '../../components/ModalFullScreen';
import StepCM from '../review-eid/components/Step';
import {DateUtils} from '../../../utils';
import {Status} from '../../../model/enum/common.enum';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import TextCM from '../../components/Text';
import InfoBodyScreen from '../review-eid/components/Info';
import Flow from '../ticket-detail/components/TabView/components/Flow';
import TextFieldCM from '../../components/TextField';
import {useReviewEcontractServices} from '../../../hooks/apiHooks/useReviewEcontractServices';
import Toast from 'react-native-toast-message';
import {ticketActions} from '../../../redux/slices';
import Document from './tab-view/components/Document';
import TaskItem from './tab-view/listTasks';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';
import ViewFormScreen from '../../components/ViewFormScreen';
import ChatAI from '../../components/ChatAI';
const ReviewFlow = () => {
  const router = useRoute<any>();
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const navigation = useNavigation<any>();
  const dataUserTask: IPostMessageUserTask = router?.params?.dataUserTask ?? {};
  const [dataFormByUsertaskId, setDataFormByUsertaskId] = useState<any>();
  const [isShow, setisShow] = useState(false);
  const [isShowForm, setIsShowForm] = useState(false);
  const [isShowFormUserTask, setIsShowFormUserTask] = useState(true);
  const [isShowFormNegotiation, setIsShowFormNegotiation] = useState(false);
  const [isShowListFile, setIsShowListFile] = useState(true);
  const [isShowTasks, setIsShowTasks] = useState(true);
  const [isShowInfo, setIsShowInfo] = useState(false);
  const [isShowNote, setIsShowNote] = useState(true);
  const [note, setNote] = useState('');
  const [noteError, setNoteError] = useState('');
  const [listTask, setListTask] = useState<ITask[]>([]);
  const [listJobs, setListJobs] = useState<IJob[]>([]);
  const [loadingBtn, setLoadingBtn] = useState(false);
  const [loadingBtnReject, setLoadingBtnReject] = useState(false);
  const [dataUserCreateTicket, setDataUserCreateTicket] = useState<any>(null);
  const {accessToken} = useAppSelector(state => state.common);
  const {dataTicketRedux, formData, modeler, recipientDetaiImpl} =
    useAppSelector(state => state.ticket);
  const {dataAccount} = useAppSelector(state => state.account);
  const ReviewTicketServices = useReviewServices();
  const ReviewEcontractServices = useReviewEcontractServices();
  const TicketServices = useTicketServices();
  const [recipientDetaiImplLast] = useState<IRecipientTicketImpl>(
    recipientDetaiImpl?.find(
      data => data.userTaskId === dataUserTask?._userTaskId,
    ) as IRecipientTicketImpl,
  );

  const dataInfo: any[] = useMemo(() => {
    return [
      {
        key: 'RV.createBy',
        type: 'avatar',
        value: {
          name: dataUserCreateTicket?.personalName || '---',
          email: dataTicketRedux.createdBy || '---',
          avatarUrl: dataUserCreateTicket?.imageUrl,
        },
      },
      {
        key: 'RV.ticketName',
        type: 'normal',
        value: {
          name: dataTicketRedux?.title || '---',
        },
      },
      {
        key: 'RV.process',
        type: 'normal',
        value: {
          name:
            (dataTicketRedux?.jsonXml &&
              JSON.parse(dataTicketRedux?.jsonXml).properties.name) ||
            '---',
        },
      },
      {
        key: 'RV.createDate',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(
            dataTicketRedux?.createdDate,
          ),
        },
      },
      {
        key: 'RV.infoForm',
        type: 'form',
        value: {
          name: formData?.formName,
          funcViewFrom: () => setIsShowForm(true),
        },
      },
      {
        key: 'RV.status',
        type: 'status',
        value: {
          status:
            dataTicketRedux.ticketStatus === Status.voided
              ? Status.voided
              : recipientDetaiImplLast?.recipientStatus || Status.processing,
        },
      },
    ];
  }, [dataTicketRedux, formData, modeler, dataUserCreateTicket]);

  useEffect(() => {
    getFormUserTask();
    getListTask();
    getUserCreateTicket();
  }, []);

  const getFormUserTask = async () => {
    try {
      const data = await ReviewEcontractServices.getDataFormFill({
        versionId: recipientDetaiImplLast.versionFormId,
      });
      if (data) {
        setDataFormByUsertaskId(data);
      }
    } catch (error) {}
  };

  const getUserCreateTicket = async () => {
    try {
      const user = await TicketServices.getMultiLoginAccount([
        dataTicketRedux.createdBy,
      ]);
      if (user && user.length > 0) {
        setDataUserCreateTicket(user[0]);
      }
      console.log('user', user);
    } catch (error) {}
  };

  const handleSubmit = async (isViewFormScreen: boolean) => {
    if (!isViewFormScreen) {
      if (dataFormByUsertaskId?.versionId) {
        setIsShowFormNegotiation(true);
        return;
      }
    }

    if (listTask.length > 0) {
      const dataCheckTask = await checkTaskComplete(listTask);
      if (!dataCheckTask) return;
    }
    try {
      setLoadingBtn(true);
      const bodySubmit = {
        ...recipientDetaiImplLast.recipient,
        reason: note,
      };
      const dataFinish = await ReviewTicketServices.actionTicket(
        recipientDetaiImplLast.id,
        recipientDetaiImplLast.ticketId,
        recipientDetaiImplLast.userTaskId,
        bodySubmit,
        accessToken,
        'finish',
      );
      if (dataFinish) {
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('CM.actionUserStaskSuccess'),
        });
        navigation.goBack();
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.actionUserStaskFailure'),
        });
      }
    } catch (error) {
    } finally {
      setLoadingBtn(false);
    }
  };

  const checkTaskComplete = async (listTask: ITask[]): Promise<boolean> => {
    if (listTask) {
      const listTaskId = listTask.map(r => r.taskId);
      const dataJob = await ReviewTicketServices.getListTaskByListId(
        listTaskId,
      );
      if (dataJob.find((d: any) => d.statusType !== 'completed')) {
        Alert.alert(t('CM.needDoneTask'));
        return false;
      } else {
        return true;
      }
    } else {
      return true;
    }
  };

  const renderRightAction = () => {
    return (
      <TopNavigationAction
        onPress={() => setisShow(true)}
        icon={<IconWorkflow width={20} height={20} />}
      />
    );
  };

  const handleViewForm = () => {
    setIsShowFormNegotiation(true);
  };

  const handleReject = async () => {
    if (!note) {
      setIsShowNote(true);
      setNoteError('CM.empty');
      return;
    }
    setNoteError('');
    try {
      setLoadingBtnReject(true);
      const body = {
        ...recipientDetaiImplLast?.recipient,
        reason: note,
      };
      const dataReject = await ReviewTicketServices.actionTicket(
        recipientDetaiImplLast?.id,
        recipientDetaiImplLast?.ticketId,
        recipientDetaiImplLast?.userTaskId,
        body,
        accessToken,
        'reject',
      );
      if (dataReject) {
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('CM.rejectSucesss'),
        });
        dispatch(
          ticketActions.setDataTicket({
            ...dataTicketRedux,
            ticketStatus: Status.rejected,
          }),
        );
        navigation.goBack();
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.rejectFailed'),
        });
      }
    } catch (error) {
    } finally {
      setLoadingBtnReject(false);
    }
  };

  const getListTask = async () => {
    const dataResp =
      (await ReviewTicketServices.getListTaskByTicketIdAndUsertaskId(
        dataTicketRedux.id,
        dataUserTask._userTaskId,
      )) as ITask[];
    if (dataResp) {
      let emails = dataResp.map(val => val.owner);
      const listId = dataResp.map(data => data.taskId);
      const dataAccount = await TicketServices.getMultiLoginAccount([
        ...new Set(emails),
      ]);

      const filterTask = dataResp.map(item => ({
        ...item,
        avatar: dataAccount?.find((d: any) => d.email === item.owner)?.imageUrl,
        name:
          '' +
          dataAccount?.find((d: any) => d.email === item.owner)?.firstName +
          ' ' +
          dataAccount?.find((d: any) => d.email === item.owner)?.lastName,
      }));
      setListTask(filterTask);
      const dataJob = await ReviewTicketServices.getListTaskByListId(listId);
      if (dataJob) {
        const filterDataJob = dataJob.map((job: any) => ({
          ...job,
          owner: filterTask.find(d => d.listTaskId == job.listTaskId)?.owner,
          fullName: filterTask.find(d => d.listTaskId == job.listTaskId)?.name,
          avatar: filterTask.find(d => d.listTaskId == job.listTaskId)?.avatar,
          attrJson: JSON.parse(job.attr),
        }));
        await getAvatarListAssignee(filterDataJob);
      }
    }
  };

  const getAvatarListAssignee = async (listJob: any) => {
    const allUserIds = listJob.flatMap((task: any) =>
      task.assignTaskList.map((assign: any) => assign.userId),
    );
    const dataResp = await ReviewTicketServices.findUserByOrgIn(
      0,
      10000,
      '',
      null,
      allUserIds,
    );
    if (dataResp) {
      const listJobFilter = listJob.map((task: any) => {
        if (task.assignTaskList.length) {
          task.assignTaskList = task.assignTaskList.map((d: any) => {
            return {
              ...d,
              avatar: dataResp.data?.find((h: any) => h.resourceId == d.userId)
                ?.imageUrl,
              email: dataResp.data?.find((h: any) => h.resourceId == d.userId)
                ?.email,
              name: dataResp.data?.find((h: any) => h.resourceId == d.userId)
                ?.personalName,
            };
          });
        }
        return task;
      });
      setListJobs(listJobFilter);
    }
  };

  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('RV.infoDoc')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        renderContentRight={renderRightAction}
      />
      <KeyboardAwareScrollView>
        <View style={styles.ctnBody}>
          <View style={styles.ctnSubBody}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowInfo(!isShowInfo)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowInfo ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>{t('CM.infoCommon')}</TextCM>
            </Pressable>

            {isShowInfo && (
              <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                <InfoBodyScreen dataInfo={dataInfo} />
              </View>
            )}
          </View>
        </View>

        {listTask.length > 0 && (
          <View style={styles.ctnBody}>
            <View style={[styles.ctnSubBody]}>
              <Pressable
                style={styles.ctnHeader}
                onPress={() => setIsShowTasks(!isShowTasks)}>
                <Icon
                  fill={'#6F2CED'}
                  width={16}
                  height={16}
                  name={
                    isShowTasks ? 'arrow-circle-down' : 'arrow-circle-right'
                  }
                />
                <TextCM style={styles.textTitle}>{t('RV.taskNeedDone')}</TextCM>
              </Pressable>

              {isShowTasks && (
                <List
                  contentContainerStyle={{
                    rowGap: 10,
                    backgroundColor: '#fff',
                    paddingBottom: 10,
                  }}
                  scrollEnabled={false}
                  data={listJobs}
                  renderItem={({item, index}) => (
                    <TaskItem key={index} dataTask={item} />
                  )}
                />
              )}
            </View>
          </View>
        )}

        {dataFormByUsertaskId && (
          <View style={styles.ctnBody}>
            <View style={[styles.ctnSubBody]}>
              <Pressable
                style={styles.ctnHeader}
                onPress={() => setIsShowFormUserTask(!isShowFormUserTask)}>
                <Icon
                  fill={'#6F2CED'}
                  width={16}
                  height={16}
                  name={
                    isShowFormUserTask
                      ? 'arrow-circle-down'
                      : 'arrow-circle-right'
                  }
                />
                <TextCM style={styles.textTitle}>
                  {t('RV.econtract.formNeedAction')}
                </TextCM>
              </Pressable>

              {isShowFormUserTask && (
                <View style={styles.ctnFormName}>
                  <TextCM style={styles.textTitle}>
                    {t('RV.econtract.fillForm')}{' '}
                  </TextCM>
                  <TouchableOpacity onPress={handleViewForm}>
                    <TextCM style={styles.formName}>
                      {dataFormByUsertaskId?.formName}
                    </TextCM>
                  </TouchableOpacity>
                </View>
              )}
            </View>
          </View>
        )}

        <View style={styles.ctnBody}>
          <View style={[styles.ctnSubBody]}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowListFile(!isShowListFile)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={
                  isShowListFile ? 'arrow-circle-down' : 'arrow-circle-right'
                }
              />
              <TextCM style={styles.textTitle}>{t('RV.fileRelative')}</TextCM>
            </Pressable>

            {isShowListFile && <Document dataUserTask={dataUserTask} />}
          </View>
        </View>

        {dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
          <View style={styles.ctnBody}>
            <View
              style={[
                styles.ctnSubBody,
                {
                  marginBottom: 16,
                },
              ]}>
              <Pressable
                style={styles.ctnHeader}
                onPress={() => setIsShowNote(!isShowNote)}>
                <Icon
                  fill={'#6F2CED'}
                  width={16}
                  height={16}
                  name={isShowNote ? 'arrow-circle-down' : 'arrow-circle-right'}
                />
                <TextCM style={styles.textTitle}>{t('CM.note')}</TextCM>
              </Pressable>

              {isShowNote && (
                <View
                  style={{paddingHorizontal: 10, paddingBottom: 10, rowGap: 4}}>
                  <TextFieldCM
                    isRequired={true}
                    label={t('CM.fillNote')}
                    value={note}
                    onChangeText={text => {
                      setNote(text);
                      setNoteError('');
                    }}
                    multiline
                    maxLength={400}
                    errorMessage={t(noteError)}
                  />
                </View>
              )}
            </View>
          </View>
        )}
      </KeyboardAwareScrollView>
      <View style={styles.ctnFooter}>
        {dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
          <>
            <TouchableOpacity
              disabled={loadingBtnReject}
              onPress={handleReject}>
              <IconRejectReview width={SizeDP(36)} height={SizeDP(36)} />
            </TouchableOpacity>
            <ButtonCM
              style={{flex: 1}}
              label={t('CM.confirm')}
              loading={loadingBtn}
              onPress={() => handleSubmit(false)}
            />
          </>
        )}
      </View>

      <ModalFullScreen
        isVisible={isShow}
        children={<StepCM onClose={() => setisShow(false)} />}
      />
      <ModalFullScreen
        isVisible={isShowFormNegotiation}
        children={
          <ViewFormScreen
            versionId={dataFormByUsertaskId?.versionId}
            readOnly={
              dataUserTask._status === STATUS_TASK.TASK_SEARCH ? false : true
            }
            data={{
              _userTaskId: dataUserTask?._userTaskId,
              _ticketId: dataTicketRedux?.id,
              formId: dataFormByUsertaskId?.formId,
            }}
            isOnlyView={
              dataUserTask._status === STATUS_TASK.TASK_SEARCH ? false : true
            }
            handleSubmit={() => handleSubmit(true)}
            onClose={() => setIsShowFormNegotiation(false)}
            nameHeader={dataFormByUsertaskId?.formName}
          />
        }
      />
      <ModalFullScreen
        isVisible={isShowForm}
        children={
          <>
            <View style={{flex: 1}}>
              <Flow />
            </View>
            <ButtonCM
              style={{backgroundColor: '#f2ecfd', marginHorizontal: 16}}
              styleLabel={{color: '#6d1fad'}}
              label={t('CM.back')}
              onPress={() => setIsShowForm(false)}
            />
          </>
        }
      />

      <ChatAI
        ticketId={dataTicketRedux.id}
        userTaskId={dataUserTask?._userTaskId}
      />
    </SafeAreaView>
  );
};

export default ReviewFlow;
