export enum ACTION_TYPES {
  SHOW_EXECUTOR_USER = 'SHOW_EXECUTOR_USER',
  SHOW_EID_INFO = 'SHOW_EID_INFO',
  SHOW_ELMS_INFO = 'SHOW_ELMS_INFO',
  SHOW_ECONTRACT_INFO = 'SHOW_ECONTRACT_INFO',
  CREATE_ECONTRACT_ENV = 'CREATE_ECONTRACT_ENV',
  SHOW_USERTASK_INFO = 'SHOW_USERTASK_INFO',
  SHOW_COLLABO_INFO = 'SHOW_COLLABO_INFO',
}

export enum STATUS_TASK {
  TASK_SEARCH = 'task-search',
  TASK_SUCCESS = 'task-success',
  TASK_REJECTED = 'task-reject',
}

export interface IPostMessage<T> {
  action: ACTION_TYPES;
  data: T;
}

export interface IPostMessageUserTask {
  _status: STATUS_TASK;
  _typeView: string;
  _userTaskId: string;
  checkDisbursement: boolean;
  modelerId: string;
  reason: any;
}

export interface IPostMessageCollabo {
  _status: string;
  _typeView: string;
  _userTaskId: string;
  modelerId: string;
  recipientDetailsId: string;
}

export interface IPostMessageEcontractTask {
  envelopeId: string;
}

export interface IPostMessageUser {
  avatar: string;
  createdBy: string;
  createdDate: string;
  cust: boolean;
  custId: number;
  firstName: string;
  formId: any;
  id: string;
  implementerId: string;
  isCust: boolean;
  isSystem: boolean;
  lastModifiedBy: string;
  lastModifiedDate: string;
  lastName: string;
  orgIn: string;
  password: string;
  recipient: any;
  recipientStatus: any;
  status: string;
  system: boolean;
  ticketId: string;
  ticketRecipientId: string;
  type: string;
  userTaskId: string;
  version: number;
  versionFormId: any;
  personalName?: string;
}
