import {View} from 'react-native';
import {IRecipient, KeysOfIRecipient} from '../../interface';
import InputCM from '../../../../review-eid/components/InputCM';
import TextCM from '../../../../../components/Text';
import DropdownListCM from '../../../../../components/DropDownList';
import {notifyType} from '../../helper';
import {useEffect, useState} from 'react';
import {IndexPath} from '@ui-kitten/components';
import styles from './styles';
import {RegexUtils} from '../../../../../../utils';
import {useTranslation} from 'react-i18next';
type Props = {
  recipient: IRecipient;
  type: string;
  error: {[key: string]: string};
  setError: any;
};

const RecipientScreen = ({recipient, type, error, setError}: Props) => {
  const {t} = useTranslation();
  const [selectNotiType, setSelectNotiType] = useState<IndexPath[]>(
    recipient.notifyType.map(
      noti => new IndexPath(notifyType.findIndex(item => item.value === noti)),
    ),
  );

  const [checkRequired, setCheckRequired] = useState({
    [`name_${recipient.id}`]: true,
    [`email_${recipient.id}`]: true,
    [`phone_${recipient.id}`]: false,
  });

  useEffect(() => {
    const newError = error;
    if (selectNotiType.length === 0) {
      newError[`notifyType_${recipient.id}`] = 'CM.empty';
      recipient['notifyType'] = [];
    } else {
      delete newError[`notifyType_${recipient.id}`];
      recipient['notifyType'] = selectNotiType.map(
        index => notifyType[index.row].value,
      );
      if (selectNotiType.length === 2) {
        setCheckRequired({
          [`name_${recipient.id}`]: true,
          [`email_${recipient.id}`]: true,
          [`phone_${recipient.id}`]: true,
        });
      } else if (selectNotiType[0].row === 0) {
        // sms
        setCheckRequired({
          [`name_${recipient.id}`]: true,
          [`email_${recipient.id}`]: false,
          [`phone_${recipient.id}`]: true,
        });
        if (!recipient.phone) {
          setError((prev: any) => ({
            ...prev,
            [`phone_${recipient.id}`]: 'CM.empty',
          }));
        } else if (!RegexUtils.REGEX_PHONE_NUMBER_9.test(recipient.phone)) {
          setError((prev: any) => ({
            ...prev,
            [`phone_${recipient.id}`]: 'CM.validPhone',
          }));
        }
      } else if (selectNotiType[0].row === 1) {
        // email
        setCheckRequired({
          [`name_${recipient.id}`]: true,
          [`email_${recipient.id}`]: true,
          [`phone_${recipient.id}`]: false,
        });

        if (!recipient.email) {
          setError((prev: any) => ({
            ...prev,
            [`email_${recipient.id}`]: 'CM.empty',
          }));
        } else if (!RegexUtils.FORMAT_EMAIL.test(recipient.email)) {
          setError((prev: any) => ({
            ...prev,
            [`email_${recipient.id}`]: 'CM.validEmail',
          }));
        }
      }
    }
    setError((prev: any) => ({...prev, ...newError}));
  }, [selectNotiType]);

  const handleChangeText = (
    type: KeysOfIRecipient,
    recipientId: string,
    value: string,
  ) => {
    recipient[type] = value;

    if (!value.trim()) {
      setError((prevErr: any) => {
        const updatedErr = {...prevErr};
        if (!checkRequired[`${type}_${recipientId}`]) {
          delete updatedErr[`${type}_${recipientId}`];
        } else {
          updatedErr[`${type}_${recipientId}`] = 'CM.empty';
        }
        return updatedErr;
      });
    } else if (type === 'email' && !RegexUtils.FORMAT_EMAIL.test(value)) {
      setError((prevState: any) => ({
        ...prevState,
        [`${type}_${recipientId}`]: 'CM.validEmail',
      }));
    } else if (
      type === 'phone' &&
      !RegexUtils.REGEX_PHONE_NUMBER_9.test(value)
    ) {
      setError((prevState: any) => ({
        ...prevState,
        [`${type}_${recipientId}`]: 'CM.validPhone',
      }));
    } else {
      setError((prevErr: any) => {
        const updatedErr = {...prevErr};
        delete updatedErr[`${type}_${recipientId}`];
        return updatedErr;
      });
    }
  };

  return (
    <View>
      <TextCM style={styles.text}>{t(`RV.econtract.${type}`)}</TextCM>
      <InputCM
        isRequired={checkRequired[`name_${recipient.id}`]}
        label={t(`RV.econtract.name`)}
        value={recipient.name}
        captionText={t(error[`name_${recipient.id}`])}
        status={error[`name_${recipient.id}`] ? 'danger' : 'basic'}
        onChangeText={text => handleChangeText(`name`, recipient.id, text)}
      />
      <InputCM
        isRequired={checkRequired[`email_${recipient.id}`]}
        label={'Email'}
        keyBoardType="email-address"
        value={recipient.email}
        captionText={t(error[`email_${recipient.id}`])}
        status={error[`email_${recipient.id}`] ? 'danger' : 'basic'}
        onChangeText={text => handleChangeText(`email`, recipient.id, text)}
      />
      <InputCM
        isRequired={checkRequired[`phone_${recipient.id}`]}
        label={t(`RV.econtract.phone`)}
        value={recipient.phone}
        keyBoardType="numeric"
        captionText={t(error[`phone_${recipient.id}`])}
        status={error[`phone_${recipient.id}`] ? 'danger' : 'basic'}
        onChangeText={text => handleChangeText(`phone`, recipient.id, text)}
      />

      <DropdownListCM
        valueSelect={selectNotiType}
        onSelect={setSelectNotiType}
        label={t(`RV.econtract.notify-type`)}
        isRequired={true}
        multiple={true}
        size="medium"
        caption={t(error[`notifyType_${recipient.id}`])}
        status={error[`notifyType_${recipient.id}`] ? 'danger' : 'basic'}
        listData={notifyType}
      />
    </View>
  );
};

export default RecipientScreen;
