import {View, Image} from 'react-native';
import DropdownListCM from '../../../../components/DropDownList';
import TextCM from '../../../../components/Text';
import {optionPermisson} from '../share/helper';
import {IDataUpdate, IResourceShare} from '../share/interface';
import {OptionPermisson, PermType} from '../share/enum';
import {useState} from 'react';
import {Divider, Icon, IndexPath} from '@ui-kitten/components';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import ModalFullScreen from '../../../../components/ModalFullScreen';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import TextFieldCM from '../../../../components/TextField';
import ButtonCM from '../../../../components/Button';
import styles from './styles';
import {useTranslation} from 'react-i18next';
import ModalConfirmCM from '../../../../components/ModalConfirm';
import {IconCancel} from '../../../../../assets/images';

type Props = {
  dataResource: IResourceShare;
  dataUser: any;
  setListDataUpdate: any;
  toogleReload: () => void;
};

const ListResource = ({
  dataResource,
  dataUser,
  setListDataUpdate,
  toogleReload,
}: Props) => {
  const {t} = useTranslation();
  const [listPermissionState, setListPermissionState] = useState<IndexPath>(
    new IndexPath(
      optionPermisson.findIndex(
        (data: any) => data.value === dataResource.perm,
      ),
    ),
  );

  const [visible, setVisible] = useState(false);
  const [visibleModalDelete, setVisibleModalDelete] = useState(false);
  const [description, setDescription] = useState('');

  const onToggleSnackBar = () => setVisible(!visible);
  const onToogleModalDelete = () => setVisibleModalDelete(!visibleModalDelete);

  const TicketServices = useTicketServices();

  const handleDeletePer = async () => {
    const dataResp = await TicketServices.deleteAclsById(dataResource.id);
    if (dataResp) {
      toogleReload();
    }
    onToogleModalDelete();
  };

  const handleChangeLimitPer = async (data: IndexPath) => {
    const listPer =
      dataResource.permType !== PermType.orgin
        ? optionPermisson
        : optionPermisson.filter(
            per => per.value !== OptionPermisson.permission,
          );
    const valueSelect = listPer[data.row];
    if (valueSelect.value === OptionPermisson.delete) {
      onToogleModalDelete();
      return;
    }

    if (valueSelect.value === OptionPermisson.permission) {
      setDescription('');
      onToggleSnackBar();
      return;
    }
    setListPermissionState(data);
    const dataUpdate: IDataUpdate = {
      assignee: dataResource.assignee,
      assigneeId: dataResource.assigneeId,
      perm: valueSelect.value,
      permType: dataResource.permType,
      name: dataResource.name,
      avatar: dataResource.avatar,
    };
    setListDataUpdate((prev: IDataUpdate[]) => {
      const dataFind = prev.find(
        data => data.assigneeId === dataResource.assigneeId,
      );
      if (dataFind) {
        return prev.map(data =>
          data.assigneeId === dataResource.assigneeId ? dataUpdate : data,
        );
      } else {
        return [...prev, dataUpdate];
      }
    });
  };

  const getName = () => {
    if (dataResource?.name?.trim()) {
      return dataResource?.name?.trim();
    }
    if (dataUser) {
      return '' + dataUser?.firstName + ' ' + dataUser?.lastName;
    }
  };

  const handleSend = async () => {
    const valueSelect = optionPermisson[listPermissionState.row];
    const params = {
      resourceId: dataResource.resourceId,
    };
    const body = {
      aclForm: {
        assignee: dataResource.assignee,
        assigneeId: dataResource.assigneeId,
        perm: valueSelect.value,
        permType: dataResource.permType,
        name: dataResource.name,
      },
      description: description,
    };

    const dataResp = await TicketServices.pushlishFile(params, body);
    if (dataResp) {
      onToggleSnackBar();
      setListPermissionState(
        new IndexPath(
          optionPermisson.findIndex(
            (data: any) => data.value === dataResource.perm,
          ),
        ),
      );
    }
  };

  const handleBack = () => {
    setListPermissionState(
      new IndexPath(
        optionPermisson.findIndex(
          (data: any) => data.value === dataResource.perm,
        ),
      ),
    );
    onToggleSnackBar();
  };

  return (
    <>
      <View style={styles.ctnMain}>
        <View style={styles.ctnBody}>
          {dataResource.permType !== PermType.orgin ? (
            <Image
              width={32}
              height={32}
              borderRadius={16}
              source={{
                uri:
                  dataResource.avatar ||
                  'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
          ) : (
            <View style={styles.ctnIcon}>
              <Icon fill={'#fff'} width={26} height={26} name="people" />
            </View>
          )}
          <View style={{flex: 1}}>
            {dataResource.permType !== PermType.orgin && (
              <View>
                <TextCM style={styles.ctnName}>{getName()}</TextCM>
              </View>
            )}
            <View>
              <TextCM style={styles.ctnAssignee}>
                {dataResource.assignee}
              </TextCM>
            </View>
          </View>
        </View>
        <View style={{flex: 0.7}}>
          {dataResource.perm === OptionPermisson.owner ? (
            <View style={styles.ctnOwner}>
              <TextCM style={styles.textOwner}>{t('RV.owner')}</TextCM>
            </View>
          ) : (
            <DropdownListCM
              isi18n={true}
              size="medium"
              onSelect={handleChangeLimitPer}
              valueSelect={listPermissionState}
              listData={
                dataResource.permType !== PermType.orgin
                  ? optionPermisson
                  : optionPermisson.filter(
                      per => per.value !== OptionPermisson.permission,
                    )
              }
            />
          )}
        </View>

        <ModalFullScreen
          isVisible={visible}
          children={
            <>
              <HeaderCM
                style={{
                  backgroundColor: '#fff',
                }}
                titleStyle={{
                  color: '#00204D',
                }}
                fillIconBackLeft="#00204DB2"
                title={t('CM.share')}
                contentStyle={{
                  backgroundColor: '#fff',
                }}
                onPressIconLeft={handleBack}
              />
              <Divider />
              <KeyboardAwareScrollView style={{padding: 16, rowGap: 10}}>
                <View
                  style={[
                    {
                      marginBottom: 16,
                    },
                    styles.ctnBody,
                  ]}>
                  {dataResource.permType !== PermType.orgin ? (
                    <Image
                      width={32}
                      height={32}
                      borderRadius={16}
                      source={{
                        uri:
                          dataResource.avatar ||
                          'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                      }}
                    />
                  ) : (
                    <View style={styles.ctnIcon}>
                      <Icon
                        fill={'#fff'}
                        width={26}
                        height={26}
                        name="people"
                      />
                    </View>
                  )}
                  <View style={{flex: 1}}>
                    {dataResource.permType !== PermType.orgin && (
                      <View>
                        <TextCM style={styles.ctnName}>{getName()}</TextCM>
                      </View>
                    )}
                    <View>
                      <TextCM style={styles.ctnAssignee}>
                        {dataResource.assignee}
                      </TextCM>
                    </View>
                  </View>
                </View>

                <TextFieldCM
                  value={description}
                  onChangeText={setDescription}
                  label={t('RV.fillNote')}
                  maxLength={100}
                  multiline
                />
              </KeyboardAwareScrollView>
              <View style={styles.ctnDes}>
                <Icon
                  fill={'#6F2CED'}
                  name="alert-circle"
                  width={16}
                  height={16}
                />
                <TextCM style={styles.textDes}>{t('RV.noteDes')}</TextCM>
              </View>
              <View style={{paddingHorizontal: 16}}>
                <ButtonCM label={t('RV.sendInvent')} onPress={handleSend} />
              </View>
            </>
          }
        />
      </View>

      <ModalFullScreen
        isVisible={visibleModalDelete}
        animationType="fade"
        backdropStyle={{backgroundColor: '#000810B2'}}
        children={
          <View style={styles.ctnModal}>
            <View style={styles.ctnBodyModal}>
              <View style={{alignItems: 'center', justifyContent: 'center'}}>
                <IconCancel width={56} height={56} />
              </View>
              <TextCM style={styles.textTitle}>
                {t('RV.confirmDeletePer')}
              </TextCM>
              <TextCM style={styles.textDesModal}>
                {t('RV.comfirmDelPerDes')}
              </TextCM>

              <View style={styles.viewBtn}>
                <ButtonCM
                  label={t('CM.labelCancel')}
                  onPress={onToogleModalDelete}
                  style={[styles.btn, styles.btnLeft]}
                  styleLabel={styles.labelStyleLeft}
                />
                <ButtonCM
                  label={t('CM.confirm')}
                  onPress={handleDeletePer}
                  style={[styles.btn]}
                />
              </View>
            </View>
          </View>
        }
      />
    </>
  );
};

export default ListResource;
