import React, {useState, useCallback, useEffect} from 'react';
import {
  View,
  FlatList,
  ScrollView,
  Pressable,
  TouchableOpacity,
} from 'react-native';
import {
  Text,
  Card,
  Layout,
  CheckBox,
  Button,
  ListItem,
  Icon,
  OverflowMenu,
  MenuItem,
} from '@ui-kitten/components';
import {Divider} from 'react-native-paper';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {IUserModal} from '../share/interface';
import useDebounce from '../../../../../hooks/useDebounce';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import styles from './styles';
import {RegexUtils} from '../../../../../utils';
import InputCM from '../../../review-eid/components/InputCM';
import TextCM from '../../../../components/Text';
import {typeLimit} from '../share/helper';
import {OptionPermisson} from '../share/enum';
import {useTranslation} from 'react-i18next';
type Props = {
  toogleFunc: () => void;
  toogleReload: () => void;
  fileId: string;
};

const SelectEmail = ({toogleFunc, toogleReload, fileId}: Props) => {
  const {t} = useTranslation();
  const [searchQuery, setSearchQuery] = useState('');
  const [error, setError] = useState('');
  const [selectedEmails, setSelectedEmails] = useState<IUserModal[]>([]);

  const [page, setPage] = useState(0);
  const [totalRecords, setTotalRecords] = useState(0);
  const [refreshing, setRefreshing] = useState(false);
  const [listDataUser, setListDataUser] = useState<IUserModal[]>([]);
  const searchDebounce = useDebounce(searchQuery, 500);
  const [menuVisible, setMenuVisible] = useState(false);
  const [dataSelect, setDataSelect] = useState(typeLimit[0]);
  const TicketReviewServices = useReviewServices();

  useEffect(() => {
    if (searchDebounce) {
      getUserModal(0, searchDebounce);
    } else {
      setListDataUser([]);
    }
  }, [searchDebounce]);

  const getUserModal = async (page: number, searchDebounce: string) => {
    try {
      const params = {
        page: page,
        size: 10,
      };
      const body = {
        q: searchDebounce.trim().toLowerCase(),
        terms: {status: 0},
      };
      const data = await TicketReviewServices.getUserInfoModel(params, body);
      if (data) {
        if (page === 0) {
          setListDataUser(data.data);
        } else {
          setListDataUser([...listDataUser, ...data.data]);
        }
        setTotalRecords(data.totalRecords);
      }
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const onRefresh = async () => {
    setRefreshing(true);
    setPage(0);
    getUserModal(0, searchDebounce);
  };

  const fetchMoreData = () => {
    if (listDataUser.length < totalRecords) {
      getUserModal(page + 1, searchDebounce);
      setPage(page + 1);
    }
  };

  const toggleEmailSelection = useCallback((email: IUserModal) => {
    setSelectedEmails(prev => {
      const isSelected = prev.some(e => e.id === email.id);
      if (isSelected) {
        return prev.filter(e => e.id !== email.id);
      } else {
        return [...prev, email];
      }
    });
  }, []);

  const removeSelectedEmail = useCallback((emailId: string) => {
    setSelectedEmails(prev => prev.filter(e => e.id !== emailId));
  }, []);

  const addNewEmail = useCallback((address: string) => {
    if (!RegexUtils.FORMAT_EMAIL.test(address)) {
      setError('CM.validEmail');
      return;
    }
    setError('');
    const newEmail: any = {
      id: `new-${Date.now()}`,
      email: address,
      isAdd: true,
    };
    setSelectedEmails(prev => [...prev, newEmail]);
    setSearchQuery('');
  }, []);

  const handleInputSubmit = useCallback(() => {
    if (searchQuery && !listDataUser.some(e => e.email === searchQuery)) {
      addNewEmail(searchQuery);
    }
  }, [searchQuery, addNewEmail]);

  const renderEmailItem = ({item}: {item: IUserModal}) => (
    <Card style={styles.card}>
      <View style={styles.cardContent}>
        <View>
          <Text category="s1">{item.email}</Text>
        </View>
        <CheckBox
          checked={selectedEmails.some(e => e.id === item.id)}
          onChange={() => toggleEmailSelection(item)}
        />
      </View>
    </Card>
  );

  const renderItem = ({item}: {item: IUserModal}): React.ReactElement => (
    <ListItem
      style={styles.ctnItem}
      key={item.id}
      title={`${item.email}`}
      accessoryRight={() => (
        <Pressable onPress={() => removeSelectedEmail(item.id)}>
          <Icon name="close" width={16} height={16} />
        </Pressable>
      )}
    />
  );

  const submit = async () => {
    if (selectedEmails.length === 0) {
      toogleFunc();
      return;
    }
    const body = selectedEmails.map(email => ({
      assignee: email.email,
      assigneeId: !email?.id?.includes('new') ? email.resourceId : null,
      perm: dataSelect.value,
      permType: !email?.id?.includes('new') ? 2 : 3, // 2 user of org,  3 user not in org
    }));
    const params = {
      resourceId: fileId,
    };

    const data = await TicketReviewServices.updatePermissionOrg(params, body);
    if (data) {
      toogleReload();
      toogleFunc();
    }
  };

  const toogleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  const handleChangeAction = (value: any) => {
    setDataSelect(value);
    toogleMenu();
  };

  const getColor = () => {
    switch (dataSelect.value) {
      case OptionPermisson.editor:
        return {bg: '#ef5da81a', color: '#ef5da8'};
      case OptionPermisson.reviewer:
        return {bg: '#f7931e1a', color: '#f7931e'};
      case OptionPermisson.viewer:
        return {bg: '#1382fd1a', color: '#1382fd'};
      default:
    }
  };

  const renderOverFlowMenu = () => {
    return (
      <OverflowMenu
        anchor={() => (
          <TouchableOpacity
            style={[
              {
                backgroundColor: getColor()?.bg,
              },
              styles.ctnOverflow,
            ]}
            onPress={toogleMenu}>
            <TextCM style={{color: getColor()?.color}}>
              {t(dataSelect.label)}
            </TextCM>
            <Icon
              name={!menuVisible ? 'arrow-down' : 'arrow-up'}
              width={16}
              height={16}
              fill={getColor()?.color}
            />
          </TouchableOpacity>
        )}
        visible={menuVisible}
        onBackdropPress={toogleMenu}>
        {typeLimit.map(action => (
          <MenuItem
            key={action.value}
            title={props => <TextCM {...props}>{t(action.label)}</TextCM>}
            onPress={() => handleChangeAction(action)}
          />
        ))}
      </OverflowMenu>
    );
  };

  return (
    <>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('CM.share')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        onPressIconLeft={toogleFunc}
      />
      <Divider />
      <View style={styles.container}>
        <View style={styles.ctnListEmailSelect}>
          <View style={{flex: 1}} />
          {renderOverFlowMenu()}
        </View>
        <InputCM
          placeholder={t('RV.fillEmail')}
          value={searchQuery}
          onChangeText={setSearchQuery}
          onSubmitEditing={handleInputSubmit}
          style={styles.input}
          captionText={t(error)}
          onBlur={e => addNewEmail(searchQuery)}
        />
        <View style={styles.chipContainer}>
          <FlatList horizontal data={selectedEmails} renderItem={renderItem} />
        </View>
        {searchQuery.length > 0 && (
          <>
            <FlatList
              style={{flex: 1}}
              onRefresh={onRefresh}
              refreshing={refreshing}
              onEndReached={fetchMoreData}
              onEndReachedThreshold={0.5}
              data={listDataUser}
              renderItem={renderEmailItem}
              keyExtractor={item => item.id}
              ListEmptyComponent={() => (
                <Text style={styles.noResults}>{t('CM.emptyData')}</Text>
              )}
            />
          </>
        )}
      </View>
      <View style={styles.ctnFooter}>
        {/* <Button
          onPress={() => addNewEmail(searchQuery)}
          style={styles.addButton}>
          {t('RV.addNewEmail')}
        </Button> */}
        <Button onPress={submit} style={styles.addButton}>
          {t('CM.confirm')}
        </Button>
      </View>
    </>
  );
};

export default SelectEmail;
