import {TouchableOpacity, View} from 'react-native';
import {FILE_ITEM_STATUS, IDetailFile} from '../../interface';
import TextCM from '../../../../../components/Text';
import {Divider, Icon} from '@ui-kitten/components';
import {IconDocx} from '../../../../../../assets/images';
import {IPostMessageCollabo} from '../../../../review-flow/interface';
import ModalFullScreen from '../../../../../components/ModalFullScreen';
import {useState} from 'react';
import {useReviewEcontractServices} from '../../../../../../hooks/apiHooks/useReviewEcontractServices';
import styles from './styles';
import ViewAndEdit from './components/viewEdit';
import {useTranslation} from 'react-i18next';
import ShareData from '../../../../review-ecollabo-flow/components/share';
import {useAppSelector} from '../../../../../../hooks/useRedux';
import {Status} from '../../../../../../model/enum/common.enum';
type Props = {
  data: IDetailFile;
  dataFlow?: IPostMessageCollabo | any;
  deleteFile?: (fileId: string) => void;
  typeReview: 'eCollabo' | 'eContract';
};

const ListItemField = ({data, dataFlow, deleteFile, typeReview}: Props) => {
  const {t} = useTranslation();
  const [isViewFile, setIsViewFile] = useState(false);
  const [isShare, setIsShare] = useState(false);
  const [url, setUrl] = useState('');
  const [dataFileState, setDataFileState] = useState<IDetailFile>(data);
  const ReviewServices = useReviewEcontractServices();

  const {dataTicketRedux} = useAppSelector(state => state.ticket);

  const toogleViewFile = () => {
    setIsViewFile(!isViewFile);
  };

  const toogleShare = () => {
    setIsShare(!isShare);
  };

  const getSize = (size: number) => {
    if (size && size >= 1024) {
      return Math.floor(size / 1024) + ' KB';
    } else if (size && size < 1024) {
      return size + ' MB';
    } else {
      return '---';
    }
  };

  const getStatus = (status: any) => {
    return t('RV.status-collabo.' + Object.keys(FILE_ITEM_STATUS)[status]);
  };

  const getColorStatus = (status: any) => {
    switch (status) {
      case FILE_ITEM_STATUS.DRAFT:
        return {
          backgroundColor: 'rgb(255, 243, 235)',
          color: 'rgb(252, 107, 3)',
        };
      case FILE_ITEM_STATUS.EDITING:
        return {
          backgroundColor: '#efeffd',
          color: '#5d5fef',
        };
      case FILE_ITEM_STATUS.DONE:
        return {
          backgroundColor: '#06a87726',
          color: '#39ac6d',
        };
      case FILE_ITEM_STATUS.SIGN:
        return {
          backgroundColor: '#ef5da81a',
          color: '#943cdd',
        };
      case FILE_ITEM_STATUS.REQUEST_EDIT:
        return {
          backgroundColor: '#f6eefc',
          color: '#943cdd',
        };
      case FILE_ITEM_STATUS.STOP:
        return {
          backgroundColor: '#fceeed',
          color: '#e14337',
        };
      default:
        return {
          backgroundColor: 'rgb(255, 243, 235)',
          color: 'rgb(252, 107, 3)',
        };
    }
  };

  const viewFile = async (fileId: string) => {
    const data = await ReviewServices.getLinkIframeEcollabo(fileId);
    if (data) {
      setUrl(data.iframe);
      toogleViewFile();
    }
  };

  return (
    <View style={styles.ctnMain}>
      <View style={styles.ctnLeft}>
        <IconDocx width={32} height={32} />
      </View>
      <View style={styles.ctnRight}>
        <TouchableOpacity onPress={() => viewFile(dataFileState.fileId)}>
          <TextCM style={styles.fileName}>{dataFileState?.fileName}</TextCM>
        </TouchableOpacity>
        <View style={styles.ctnBody}>
          <TextCM style={styles.srcSys}>
            {dataFileState?.sourceSystem || 'T eRequest'}
          </TextCM>
          <TextCM></TextCM>
          <TextCM style={styles.srcSys}>
            {getSize(dataFileState?.fileSize)}
          </TextCM>
        </View>
        <View style={styles.ctnAction}>
          <View
            style={[
              styles.ctnStatus,
              {
                backgroundColor: getColorStatus(dataFileState?.status)
                  .backgroundColor,
              },
            ]}>
            <TextCM
              style={[
                {
                  textAlign: 'center',
                  color: getColorStatus(dataFileState?.status).color,
                },
                styles.srcSys,
              ]}>
              {getStatus(dataFileState?.status)}
            </TextCM>
          </View>
          <View style={styles.action}></View>
        </View>
        {dataTicketRedux.ticketStatus !== Status.voided &&
          dataTicketRedux.ticketStatus !== Status.rejected && (
            <View style={styles.action}>
              {typeReview === 'eCollabo' &&
                dataFlow?._status != 'task-success' && (
                  <TouchableOpacity hitSlop={10} onPress={toogleShare}>
                    <Icon
                      fill={'#00204DB2'}
                      width={16}
                      height={16}
                      name="share-outline"
                    />
                  </TouchableOpacity>
                )}

              <TouchableOpacity
                hitSlop={10}
                onPress={() => viewFile(dataFileState.fileId)}>
                <Icon
                  fill={'#00204DB2'}
                  width={16}
                  height={16}
                  name={
                    dataFileState.status === FILE_ITEM_STATUS.EDITING
                      ? 'edit-2-outline'
                      : 'eye-outline'
                  }
                />
              </TouchableOpacity>
              {dataFileState.sourceSystem.includes('eRequest') &&
                dataFileState.status != FILE_ITEM_STATUS.DONE && (
                  <TouchableOpacity
                    hitSlop={10}
                    onPress={() => deleteFile?.(dataFileState.fileId)}>
                    <Icon
                      fill={'#00204DB2'}
                      width={16}
                      height={16}
                      name="trash"
                    />
                  </TouchableOpacity>
                )}
            </View>
          )}
        <Divider />
      </View>
      {isViewFile && (
        <ModalFullScreen
          isVisible={isViewFile}
          children={
            <ViewAndEdit
              changeDataFile={setDataFileState}
              isView={dataFileState.status}
              url={url}
              toogleViewFile={toogleViewFile}
              fileId={dataFileState.fileId}
              fileName={dataFileState?.fileName}
            />
          }
        />
      )}

      <ModalFullScreen
        isVisible={isShare}
        children={
          <ShareData dataDetailFile={dataFileState} toogleFunc={toogleShare} />
        }
      />
    </View>
  );
};

export default ListItemField;
