//import liraries
import React, {useEffect, useState} from 'react';
import {ScrollView, View} from 'react-native';
import {DateUtils} from '../../../../../../../utils';
import {ITicket} from '../../../../../../../model/interface/service.interface';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {IconEmpty} from '../../../../../../../assets/images';
import {SizeDP} from '../../../../../../../constants/Size';
import TableCM from '../../../../../../components/Table';
import PagingCM from '../../../../../../components/Paging';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../../../hooks/apiHooks/useTicketServices';
import TextCM from '../../../../../../components/Text';
import Color from '../../../../../../../constants/Color';
import {Status} from '../../../../../../../model/enum/common.enum';
import styles from './styles';
// create a component
const TicketRelative = () => {
  const {t} = useTranslation();
  const [listTicketRelative, setListTicketRelative] = useState<any[]>([]);
  const [totals, setTotals] = useState<number>(1);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const ListServices = useTicketServices();
  const pageSize = 8;
  const [currentPage, setCurrentPage] = useState(1);
  const {accessToken} = useAppSelector(state => state.common);

  useEffect(() => {
    handleGetListTicketRelatives(currentPage);
  }, [currentPage]);

  const handleGetListTicketRelatives = async (page: number) => {
    try {
      const dataTicketRelative = await ListServices.getTicketRelatives(
        dataTicketRedux.id,
        page - 1,
        pageSize,
        accessToken,
      );
      convertDataFileToTable(dataTicketRelative.content);
      setTotals(dataTicketRelative.totalElements);
    } catch (error) {
    } finally {
    }
  };

  const renderStatusTicket = (status: any) => {
    switch (status) {
      case Status.completed:
        return renderContent(
          Color.completed,
          Color.completed01,
          Status.completed,
        );
      case Status.draft:
        return renderContent(Color.draft, Color.draft01, Status.draft);
      case Status.voided:
        return renderContent(Color.voided, Color.voided01, Status.voided);
      case Status.rejected:
        return renderContent(Color.rejected, Color.rejected01, Status.rejected);
      case Status.processing:
        return renderContent(
          Color.processing,
          Color.processing01,
          Status.processing,
        );
    }
  };

  const renderContent = (color: string, bgColor: string, status: Status) => {
    return (
      <View
        style={[
          {
            backgroundColor: bgColor,
          },
          styles.ctnContent,
        ]}>
        <TextCM style={{color: color}}>{t(`TK.${status}`)}</TextCM>
      </View>
    );
  };

  const convertDataFileToTable = (data: ITicket[]) => {
    const newData = data.map((infoFile: ITicket, index: number) => ({
      STT: String((currentPage - 1) * pageSize + 1 + index),
      title: infoFile?.title,
      serviceName: infoFile.serviceName,
      ticketStatus: renderStatusTicket(infoFile.ticketStatus),
      createdBy: infoFile?.createdBy,
      createdDate: DateUtils.convertDateToDDMMYYYY_H_M(infoFile.createdDate),
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
    }));
    setListTicketRelative(newData);
  };

  return (
    <View style={{flex: 1}}>
      {listTicketRelative.length > 0 ? (
        <ScrollView>
          <View style={{flex: 1}}>
            <View
              style={{
                flex: 1,
                paddingVertical: SizeDP(10),
                marginLeft: SizeDP(10),
              }}>
              <TableCM
                tableData={listTicketRelative}
                tableHeaders={[
                  'STT',
                  'title',
                  'serviceName',
                  'ticketStatus',
                  'createdBy',
                  'createdDate',
                  'lastModifiedDate',
                ]}
              />
            </View>
            {totals > 0 && (
              <PagingCM
                collectionSize={totals}
                currentPage={currentPage}
                pageSize={pageSize}
                onPageChange={currentPage => setCurrentPage(currentPage)}
              />
            )}
          </View>
        </ScrollView>
      ) : (
        <View style={{alignItems: 'center', justifyContent: 'center', flex: 1}}>
          <IconEmpty width={SizeDP(200)} height={SizeDP(150)} />
          <TextCM>{t('CM.emptyData')}</TextCM>
        </View>
      )}
    </View>
  );
};

export default TicketRelative;
