import {useEffect, useRef, useState} from 'react';
import BackgroundFlow from './components/BackgroundFlow';
import Flow from './components/Flow';
import HistoryTicket from './components/HistoryTicket';
import TicketRelative from './components/TicketRelative';
import styles from './styles';
import Document from './components/Document';
import SLA from './components/SLA';
import TextCM from '../../../../components/Text';
import {Dimensions, FlatList, TouchableOpacity, View} from 'react-native';
import {IScreen, SizeDP} from '../../../../../constants/Size';
import {useTranslation} from 'react-i18next';
const TabViewCM = ({
  selectTabBottom,
  userTaskId,
  recipientDetailsId,
  isRealoadWebview,
  notifyActionType,
  typeView
}: {
  selectTabBottom: number;
  userTaskId: string;
  recipientDetailsId: string;
  isRealoadWebview: boolean;
  notifyActionType: any;
  typeView: 'view' | 'quick'
}) => {
  const [tab, setTab] = useState<any>({
    key: 'background',
    title: 'Tng quan',
  });
  const flatListRef = useRef<any>(null);

  const [screenDevices, setScreenDevices] = useState<IScreen>({
    width: Dimensions.get('screen').width,
    height: Dimensions.get('screen').height,
  });

  useEffect(() => {
    const sub = Dimensions.addEventListener('change', ({screen}) => {
      setScreenDevices({
        width: screen.width,
        height: screen.height,
      });
    });

    return () => sub.remove();
  }, []);

  const {t} = useTranslation();
  const [routes] = useState([
    {key: 'background', title: 'Tng quan'},
    {key: 'flow', title: 'Thng tin giao dch'},
    {key: 'hsitoryTicket', title: 'Lch s giao dch'},
    {key: 'ticketRelative', title: 'Giao dch lin quan'},
    {key: 'sla', title: 'Thng tin SLA'},
    {key: 'document', title: 'Ti liu nh km'},
  ]);

  const renderItems = (item: any) => {
    switch (item.key) {
      case 'background':
        return (
          <BackgroundFlow
            userTaskId={userTaskId}
            recipientDetailsId={recipientDetailsId}
            selectTabBottom={selectTabBottom}
            isRealoadWebview={isRealoadWebview}
            notifyActionType={notifyActionType}
            typeView={typeView}
          />
        );
      case 'flow':
        return <Flow />;
      case 'document':
        return <Document />;
      case 'hsitoryTicket':
        return <HistoryTicket />;
      case 'ticketRelative':
        return <TicketRelative />;
      case 'sla':
        return <SLA />;
      default:
        return <View></View>;
    }
  };

  const renderTabView = ({item, index}: {item: any; index: number}) => {
    return (
      <TouchableOpacity
        style={[styles.ctnMainTab, tab.key === item.key && styles.activeTab]}
        onPress={() => {
          setTab(item);
          scrollToItem(index);
        }}>
        <TextCM
          style={[styles.tabBar, tab.key === item.key && styles.colorActive]}>
          {t(`TK.${item.key}`)}
        </TextCM>
      </TouchableOpacity>
    );
  };

  const scrollToItem = (index: number) => {
    let randomIndex = Math.floor(index);
    flatListRef?.current?.scrollToIndex({
      animated: true,
      index: '' + randomIndex,
      viewOffset: screenDevices.width / 3,
    });
  };

  return (
    <View style={{flex: 1}}>
      <View>
        <FlatList
          ref={flatListRef}
          horizontal
          data={routes}
          keyExtractor={(item, index) => String(item.key)}
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
          initialScrollIndex={0}
          renderItem={renderTabView}
        />
      </View>
      <View style={{height: SizeDP(6), backgroundColor: '#F8FAFE'}} />
      <View style={{flex: 1}}>{renderItems(tab)}</View>
    </View>
  );
};

export default TabViewCM;
