//import liraries
import React, {useEffect, useState} from 'react';
import {
  ScrollView,
  TouchableOpacity,
  useWindowDimensions,
  View,
} from 'react-native';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import TableCM from '../../../../../../components/Table';
import {DateUtils} from '../../../../../../../utils';
import {useTranslation} from 'react-i18next';
import {Divider, Icon} from '@ui-kitten/components';
import TextCM from '../../../../../../components/Text';
import styles from './styles';
import {Status} from '../../../../../../../model/enum/common.enum';
import {useTicketServices} from '../../../../../../../hooks/apiHooks/useTicketServices';
// create a component
const SLA = () => {
  const {t} = useTranslation();
  const [dataSLA, setDataSLA] = useState<any[]>();
  const [widthOfFinish, setWidthOfFinish] = useState(0);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const ListServices = useTicketServices();
  const {width, height} = useWindowDimensions();

  useEffect(() => {
    getSLA();
  }, []);

  const getSLA = async () => {
    const data = await ListServices.getSla(dataTicketRedux.id);
    if (data) {
      convertDataToTable(data);
    }
  };

  const getColorStatusOfSLA = (data: any) => {
    const status = getStatusSLA(data);
    switch (status) {
      case Status.processing:
        return {
          color: '#7F43EF',
          bgColor: '#F2ECFD',
        };
      case Status.accepted:
      case Status.completed:
      case 'onTime':
        return {
          color: '#39AC6D',
          bgColor: '#E8F7EF',
        };
      case Status.voided:
      case Status.rejected:
      case 'late':
        return {
          color: '#F05959',
          bgColor: 'rgba(251, 213, 213, 1)',
        };
      default:
        return {};
    }
  };

  const convertDataToTable = (dataSLA: any) => {
    const newData = dataSLA.map((data: any, index: number) => ({
      STT: String(index + 1),
      notionName: data.notionName || '---',
      createdBy: data.owner,
      status: (
        <View
          style={{
            backgroundColor: getColorStatusOfSLA(data)?.bgColor,
            borderRadius: 4,
            paddingHorizontal: 4,
            paddingVertical: 2,
          }}>
          <TextCM
            style={{
              color: getColorStatusOfSLA(data)?.color,
              fontSize: 12,
            }}>
            {t(`TK.${getStatusSLA(data)}`)}
          </TextCM>
        </View>
      ),
      actualStartSate: data.estimateDate
        ? DateUtils.convertDateToDDMMYYYY_H_M(data.estimateDate)
        : '---',
      eventStartDate: data.startDate
        ? DateUtils.convertDateToDDMMYYYY_H_M(data.startDate)
        : '---',
      executionTime: data.totalTime ? data.totalTime : '---',
      sla: (
        <TextCM
          style={{
            color: getColorStatusOfSLA(data)?.color,
            fontSize: 12,
          }}>
          {convertTimeSla(
            data?.estimateDate,
            data?.startDate,
            data?.finishDate,
          )}
        </TextCM>
      ),
    }));
    setDataSLA(newData);
  };

  const getStatusSLA = (item: any) => {
    if (
      dataTicketRedux.ticketStatus === Status.voided &&
      item.status === Status.processing
    ) {
      return Status.voided;
    }
    if (item.status != 'accepted' && dataTicketRedux.status == 'rejected') {
      return 'rejected';
    } else if (item.status == 'accepted' && item.delayTime) {
      return 'late';
    } else if (item.status == 'accepted' && item.estimateDate) {
      return 'onTime';
    } else {
      return item.status;
    }
  };

  const getPositionOfFinishDateAndEstimateDate = () => {
    if (
      new Date(dataTicketRedux.finishDate) <
      new Date(dataTicketRedux.estimateDate)
    ) {
      const ratio =
        differenceTime(
          dataTicketRedux.finishDate,
          dataTicketRedux.createdDate,
        ) /
        differenceTime(
          dataTicketRedux.estimateDate,
          dataTicketRedux.createdDate,
        );
      return {
        positionOfEstimateDate: {
          top: -20,
          right: (width * 0.1) / 2 - 10,
        },
        positionOfPopupEstimateDate: {
          top: -58,
          right: (width * 0.1) / 2 - 10,
        },

        positionOfFinishDate: {
          bottom: -20,
          right: width * 0.95 - (width * 0.1) / 2 - ratio * width * 0.85 - 10,
        },

        positionOfPopupFinishDate: {
          bottom: -58,
          right:
            ratio < 0.6
              ? width * 0.95 -
                (width * 0.1) / 2 -
                ratio * width * 0.85 -
                10 -
                widthOfFinish +
                30
              : width * 0.95 - (width * 0.1) / 2 - ratio * width * 0.85 - 10,
        },
        width: ratio * width * 0.85,
        bgColor:
          dataTicketRedux.ticketStatus === 'completed' ? '#d9f2e4' : '#40bf79',
        typeOfArrow: {
          estimateDate: 'arrow-down',
          finishDate: 'arrow-up',
        },
      };
    } else {
      const ratio =
        differenceTime(
          dataTicketRedux.estimateDate,
          dataTicketRedux.createdDate,
        ) /
        differenceTime(dataTicketRedux.finishDate, dataTicketRedux.createdDate);
      return {
        positionOfEstimateDate: {
          bottom: -20,
          right:
            dataTicketRedux?.ticketStatus !== Status.voided
              ? width * 0.95 - (width * 0.1) / 2 - ratio * width * 0.85 - 10
              : width - width * 0.95 - 10,
        },
        positionOfPopupEstimateDate: {
          bottom: -58,
          right:
            dataTicketRedux?.ticketStatus !== Status.voided
              ? ratio < 0.6
                ? width * 0.95 -
                  (width * 0.1) / 2 -
                  ratio * width * 0.85 -
                  10 -
                  widthOfFinish +
                  30
                : width * 0.95 - (width * 0.1) / 2 - ratio * width * 0.85 - 10
              : width - width * 0.95 - 10,
        },

        positionOfFinishDate: {
          top: -20,
          right: (width * 0.1) / 2 - 10,
        },

        positionOfPopupFinishDate: {
          top: -58,
          right: (width * 0.1) / 2 - 10,
        },
        width: ratio * width * 0.85,
        bgColor:
          dataTicketRedux.ticketStatus === 'completed' ? '#d9f2e4' : '#40bf79',
        typeOfArrow: {
          estimateDate: 'arrow-up',
          finishDate: 'arrow-down',
        },
      };
    }
  };

  const differenceTime = (startTime: string, endTime: string) => {
    let timeDifference = 0;
    if (startTime && endTime) {
      const startTimeDate = new Date(startTime);
      const endTimeDate = new Date(endTime);
      timeDifference = endTimeDate.getTime() - startTimeDate.getTime();
    }
    return timeDifference;
  };

  const convertTimeSla = (
    estimateTime: string,
    startTime: string,
    finishDate: string,
  ) => {
    let totalTime = differenceTime(startTime, finishDate);
    let estimateTime_ = differenceTime(startTime, estimateTime);

    let slaTime: number;
    slaTime = Math.abs(estimateTime_ - totalTime);

    const days = Math.floor(slaTime / (24 * 60 * 60 * 1000));
    const hours = Math.floor(
      (slaTime - days * 24 * 60 * 60 * 1000) / (60 * 60 * 1000),
    );
    const minutes = Math.floor(
      (slaTime - days * 24 * 60 * 60 * 1000 - hours * 60 * 60 * 1000) /
        (60 * 1000),
    );
    const seconds = Math.floor(
      (slaTime -
        days * 24 * 60 * 60 * 1000 -
        hours * 60 * 60 * 1000 -
        minutes * 60 * 1000) /
        1000,
    );
    if (slaTime) {
      return (
        days + 'd' + (hours % 60) + 'h' + (minutes % 60) + 'm' + seconds + 's'
      );
    } else {
      return '---';
    }
  };

  return (
    <View style={{flex: 1}}>
      <ScrollView>
        <>
          <View style={styles.ctnSLA}>
            <View
              style={[
                {
                  width: width * 0.95,
                },
                styles.ctnBody,
              ]}>
              {/* create date */}
              <TouchableOpacity
                style={{
                  position: 'absolute',
                  top: -20,
                  left: (width * 0.1) / 2 - 10,
                  zIndex: 1,
                }}>
                <Icon width={20} height={20} name="arrow-down" />
              </TouchableOpacity>
              <View
                style={[
                  {
                    top: -58,
                    left: (width * 0.1) / 2 - 20,
                  },
                  styles.ctnDate,
                ]}>
                <TextCM style={styles.size}>{t('SR.created_at')}: </TextCM>
                <TextCM style={styles.size}>
                  {DateUtils.convertDateToDDMMYYYY_H_M(
                    dataTicketRedux.createdDate,
                  )}
                </TextCM>
              </View>

              {/* estimate date */}
              <TouchableOpacity
                style={[
                  {
                    position: 'absolute',
                    zIndex: 1,
                  },
                  getPositionOfFinishDateAndEstimateDate()
                    .positionOfEstimateDate,
                ]}>
                <Icon
                  width={20}
                  height={20}
                  name={
                    getPositionOfFinishDateAndEstimateDate().typeOfArrow
                      .estimateDate
                  }
                />
              </TouchableOpacity>
              <View
                style={[
                  getPositionOfFinishDateAndEstimateDate()
                    .positionOfPopupEstimateDate,
                  styles.ctnDate,
                ]}>
                <TextCM style={styles.size}>{t('TK.estimateDate')}: </TextCM>
                <TextCM style={styles.size}>
                  {DateUtils.convertDateToDDMMYYYY_H_M(
                    dataTicketRedux.estimateDate,
                  )}
                </TextCM>
              </View>

              {/* finish date */}
              {dataTicketRedux.ticketStatus === 'completed' && (
                <>
                  <TouchableOpacity
                    style={[
                      {
                        position: 'absolute',
                        zIndex: 1,
                      },
                      getPositionOfFinishDateAndEstimateDate()
                        .positionOfFinishDate,
                    ]}>
                    <Icon
                      width={20}
                      height={20}
                      name={
                        getPositionOfFinishDateAndEstimateDate().typeOfArrow
                          .finishDate
                      }
                    />
                  </TouchableOpacity>
                  <View
                    onLayout={e => {
                      setWidthOfFinish(e.nativeEvent.layout.width);
                    }}
                    style={[
                      getPositionOfFinishDateAndEstimateDate()
                        .positionOfPopupFinishDate,
                      styles.ctnDate,
                    ]}>
                    <TextCM style={styles.size}>{t('TK.finishDate')}: </TextCM>
                    <TextCM style={styles.size}>
                      {DateUtils.convertDateToDDMMYYYY_H_M(
                        dataTicketRedux.finishDate,
                      )}
                    </TextCM>
                  </View>
                </>
              )}
              <View style={styles.line}></View>
              <View
                style={{
                  height: 30,
                  backgroundColor: '#40bf79',
                  width: width * 0.85,
                }}>
                <View
                  style={{
                    height: 30,
                    backgroundColor:
                      getPositionOfFinishDateAndEstimateDate().bgColor,
                    width: getPositionOfFinishDateAndEstimateDate().width,
                  }}
                />
              </View>
              <View style={styles.line}></View>
            </View>
          </View>
          <Divider style={{marginVertical: 10}} />
          <TableCM
            tableHeaders={[
              'STT',
              'notionName',
              'createdBy',
              'status',
              'actualStartSate',
              'eventStartDate',
              'executionTime',
              'sla',
            ]}
            tableData={dataSLA || []}
            stickyLeft={true}
            stickyColumnLeftIndex={1}
          />
        </>
      </ScrollView>
    </View>
  );
};

//make this component available to the app
export default SLA;
