import {Pressable, TouchableOpacity, View} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {
  Button,
  Divider,
  Icon,
  IndexPath,
  List,
  MenuItem,
  OverflowMenu,
} from '@ui-kitten/components';
import DropdownListCM from '../../../../components/DropDownList';
import TextCM from '../../../../components/Text';
import {IDetailFile} from '../../../review-econtract-flow/review-econtract/interface';
import {Iconkey} from '../../../../../assets/images';
import {useCallback, useEffect, useState} from 'react';
import {
  IPermissionSelect,
  KeyOfPermissionSelect,
  permissionArr,
  typeLimitPermisson,
} from './helper';
import {LimitPermission, PERMISSION, PermType} from './enum';
import ListResource from '../listResource';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import {IDataUpdate, IResourceShare} from './interface';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import ModalFullScreen from '../../../../components/ModalFullScreen';
import SelectEmail from '../selectEmail';
import SelectOrg from '../selectOrg';
import styles from './styles';
import Clipboard from '@react-native-clipboard/clipboard';
import {useTranslation} from 'react-i18next';
import {getConfig} from '../../../../../services/httpClient';
import {Snackbar} from 'react-native-paper';
import React from 'react';
type Props = {
  toogleFunc: () => void;
  dataDetailFile: IDetailFile;
};
const ShareData = ({toogleFunc, dataDetailFile}: Props) => {
  const {t} = useTranslation();
  const [menuVisible, setMenuVisible] = useState(false);
  const [isReload, setIsReload] = useState(false);
  const [openSnackBar, setOpenSnackBar] = useState(false);
  const [isVisiableSelectEmail, setIsVisiableSelectEmail] = useState(false);
  const [isVisiableSelectOrg, setIsVisiableSelectOrg] = useState(false);
  const [dataSelect, setDataSelect] = useState<IPermissionSelect>({
    permission: PERMISSION.limit,
    limit: 'v',
    limitPermission: LimitPermission.viewer,
    selectOption: 'v',
    selectOptionOrg: 'v',
  });

  const [listDataUpdate, setListDataUpdate] = useState<IDataUpdate[]>([]);

  const [listPermissionState, setListPermissionState] = useState<IndexPath>(
    new IndexPath(
      typeLimitPermisson.findIndex(
        data => data.value === dataSelect.limitPermission,
      ),
    ),
  );

  const [lisDataResoures, setListDataResoures] = useState<IResourceShare[]>([]);
  const [listUser, setListUser] = useState([]);
  const [dataDefaultPublicLink, setDataDefaultPublicLink] = useState<any>();
  const ReviewTicket = useReviewServices();
  const ListServices = useTicketServices();
  const toogleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  const toggleSelectEmail = () => {
    setIsVisiableSelectEmail(!isVisiableSelectEmail);
  };

  const toggleSelectOrg = () => {
    setIsVisiableSelectOrg(!isVisiableSelectOrg);
  };

  const toogleSnackBar = () => setOpenSnackBar(!openSnackBar);

  useEffect(() => {
    handleGetResourceType();
  }, [isReload]);

  useEffect(() => {
    getDefaultPublic();
  }, []);

  const getDefaultPublic = async () => {
    const params = {
      resourceId: dataDetailFile.fileId,
    };
    const data = await ListServices.getDefaultPublic(params);
    setDataDefaultPublicLink(data);
    if (data) {
      setDataSelect(prev => ({
        ...prev,
        permission: PERMISSION.everyone,
        limitPermission: data.perm,
      }));
      setListPermissionState(
        new IndexPath(
          typeLimitPermisson.findIndex(
            dataType => dataType.value === data.perm,
          ),
        ),
      );
    }
  };

  const handleChangeAction = (type: KeyOfPermissionSelect, value: any) => {
    setDataSelect(prev => ({
      ...prev,
      [type]: value,
    }));
    toogleMenu();
  };

  const handleChangeLimitPer = (data: IndexPath) => {
    setListPermissionState(data);
    setDataSelect(prev => ({
      ...prev,
      limitPermission: typeLimitPermisson[data.row].value,
    }));
  };

  const handleGetResourceType = async () => {
    const params = {
      resourceId: dataDetailFile.fileId,
    };
    const data = await ReviewTicket.getResourceById(params);

    if (data) {
      const listEmail = data
        .filter((data: any) => data.permType !== PermType.orgin)
        .map((user: any) => user.assignee);
      const dataUser = await ListServices.getMultiLoginAccount(listEmail);
      setListUser(dataUser);
      setListDataResoures(data);
    }
  };

  const copyToClipboard = () => {
    Clipboard.setString(
      `${getConfig().BASE_URL_ECOLLABO}/negoti-doc/${dataDetailFile.fileId}`,
    );
    toogleSnackBar();
  };

  const submit = async () => {
    const params = {
      resourceId: dataDetailFile.fileId,
    };

    if (listDataUpdate.length > 0) {
      const dataResp = await ReviewTicket.updatePermissionOrg(
        params,
        listDataUpdate,
      );
    }
    if (!dataDefaultPublicLink && dataSelect.permission === PERMISSION.limit) {
      toogleFunc();
      return;
    }
    if (
      dataDefaultPublicLink &&
      dataSelect.limitPermission === dataDefaultPublicLink.perm
    ) {
      toogleFunc();
      return;
    }
    if (dataSelect.permission === PERMISSION.limit) {
      await handleLimitLink();
    } else {
      await handlePublicLink();
    }
    toogleFunc();
  };

  const handleLimitLink = async () => {
    const params = {
      resourceId: dataDetailFile.fileId,
    };
    const data = await ListServices.limitLink(params);
    if (data) {
    }
  };

  const handlePublicLink = async () => {
    const params = {
      resourceId: dataDetailFile.fileId,
      perm: dataSelect.limitPermission,
    };
    const data = await ListServices.publicLink(params);
    if (data) {
    }
  };

  const renderImagePer = () => {
    if (dataSelect.permission === PERMISSION.limit) {
      return (
        <View style={styles.ctnImagePer}>
          <Iconkey width={13} height={16} />
        </View>
      );
    } else {
      return (
        <View style={styles.ctnImagePer}>
          <Icon name="globe" fill={'#39ac6c'} width={20} height={20} />
        </View>
      );
    }
  };

  const renderOverFlowMenu = () => {
    return (
      <OverflowMenu
        anchor={() => (
          <TouchableOpacity hitSlop={15} onPress={toogleMenu}>
            <Icon name="arrow-down" width={16} height={16} fill={'#00204DB2'} />
          </TouchableOpacity>
        )}
        visible={menuVisible}
        onBackdropPress={toogleMenu}>
        {permissionArr.map(action => (
          <MenuItem
            key={action.value}
            title={t(action.value)}
            onPress={() => handleChangeAction('permission', action.label)}
          />
        ))}
      </OverflowMenu>
    );
  };

  const getLabelOfPermission = useCallback(() => {
    if (dataSelect.permission === PERMISSION.limit) {
      return t('RV.limit');
    }
    if (dataSelect.permission === PERMISSION.everyone) {
      switch (dataSelect.limitPermission) {
        case LimitPermission.viewer:
          return t('RV.viewer');
        case LimitPermission.editor:
          return t('RV.editor');
        case LimitPermission.reviewer:
          return t('RV.reviewer');
        default:
          return '';
      }
    }
  }, [dataSelect]);

  return (
    <>
      <View style={styles.ctnMain}>
        <HeaderCM
          style={{
            backgroundColor: '#fff',
          }}
          titleStyle={{
            color: '#00204D',
          }}
          fillIconBackLeft="#00204DB2"
          title={t('CM.share')}
          contentStyle={{
            backgroundColor: '#fff',
          }}
          onPressIconLeft={toogleFunc}
        />
        <Divider />
        <View style={styles.ctnBody}>
          <View style={styles.ctnGroup1}>
            <View style={styles.container}>
              <TextCM style={styles.title}>{t('CM.orgIn')}</TextCM>
              <Pressable onPress={toggleSelectOrg} style={styles.ctnSelect}>
                <TextCM>{t('CM.orgIn')}</TextCM>
                <Icon
                  width={16}
                  height={16}
                  name="arrow-right"
                  fill={'#00358059'}
                />
              </Pressable>
            </View>

            <View style={styles.container}>
              <TextCM style={styles.title}>Email</TextCM>
              <Pressable onPress={toggleSelectEmail} style={styles.ctnSelect}>
                <TextCM>{t('CM.emailShare')}</TextCM>
                <Icon
                  width={16}
                  height={16}
                  name="arrow-right"
                  fill={'#00358059'}
                />
              </Pressable>
            </View>
          </View>
          <View style={styles.ctnWrapper}>
            <TextCM style={styles.title}>{t('RV.userAccess')}</TextCM>
            <View style={styles.ctnGroup2}>
              <List
                style={{backgroundColor: '#fff'}}
                data={lisDataResoures}
                renderItem={({item}) => (
                  <ListResource
                    toogleReload={() => setIsReload(!isReload)}
                    setListDataUpdate={setListDataUpdate}
                    dataUser={listUser?.find(
                      (user: any) => user.email === item.assignee,
                    )}
                    dataResource={item}
                  />
                )}
              />
            </View>
          </View>

          <View style={styles.ctnWrapper}>
            <TextCM style={styles.title}>{t('RV.generalAccess')}</TextCM>
            <View style={styles.ctnImage}>
              {renderImagePer()}
              <View style={styles.ctnDes}>
                <View style={styles.ctnDesPer}>
                  <TextCM style={styles.title}>
                    {t(
                      permissionArr.find(
                        per => per.label === dataSelect.permission,
                      )?.value as any,
                    )}
                  </TextCM>
                  {renderOverFlowMenu()}
                </View>
                <TextCM style={styles.textLabel}>
                  {getLabelOfPermission()}
                </TextCM>

                {dataSelect.permission === PERMISSION.everyone && (
                  <DropdownListCM
                    isi18n={true}
                    size="medium"
                    onSelect={handleChangeLimitPer}
                    valueSelect={listPermissionState}
                    listData={typeLimitPermisson}
                  />
                )}
              </View>
            </View>
          </View>
        </View>
        <View style={{}}>
          <Divider />
          <View style={styles.ctnCopy}>
            <Pressable onPress={copyToClipboard} style={styles.ctnPress}>
              <Icon fill={'#366AE2'} width={16} height={16} name="link" />
              <TextCM style={styles.textCopy}>{t('RV.shareCopy')}</TextCM>
            </Pressable>
            <Button
              onPress={submit}
              size="small"
              children={<TextCM>{t('CM.confirm')}</TextCM>}
            />
          </View>
        </View>
      </View>

      <ModalFullScreen
        isVisible={isVisiableSelectEmail}
        children={
          <SelectEmail
            fileId={dataDetailFile.fileId}
            toogleReload={() => setIsReload(!isReload)}
            toogleFunc={toggleSelectEmail}
          />
        }
      />

      <ModalFullScreen
        isVisible={isVisiableSelectOrg}
        children={
          <SelectOrg
            toogleReload={() => setIsReload(!isReload)}
            fileId={dataDetailFile.fileId}
            toogleFunc={toggleSelectOrg}
          />
        }
      />

      <Snackbar
        visible={openSnackBar}
        onDismiss={toogleSnackBar}
        duration={1000}>
        {t('RV.copyLink')}
      </Snackbar>
    </>
  );
};

export default ShareData;
