import {
  Alert,
  FlatList,
  Image,
  Pressable,
  Share,
  TouchableOpacity,
  View,
} from 'react-native';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import {useEffect, useState} from 'react';
import styles from './styles';
import {Icon, MenuItem, OverflowMenu, Spinner} from '@ui-kitten/components';
import TextCM from '../../../../components/Text';
import {Color, String} from '../../../../../constants';
import {DateUtils} from '../../../../../utils';
import Video from 'react-native-video';
import RNFS from 'react-native-fs';
import {useTranslation} from 'react-i18next';
import React from 'react';
import RNFetchBlob from 'react-native-blob-util';

type Props = {
  userTaskId: string;
  ticketId: string;
};

const EkycInfo = ({userTaskId, ticketId}: Props) => {
  const {t} = useTranslation();
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingEkyc, setIsLoadingEkyc] = useState(false);
  const [isShowInfoCCCD, setIsShowInfoCCCD] = useState(true);
  const [isShowInfoEkyc, setIsShowInfoEkyc] = useState(true);
  const [listValueEkyc, setListValueEkyc] = useState<any[]>([]);
  const [listValueIdCard, setListValueIdCard] = useState<any[]>([]);
  const [listNumberTransIdIdCard, setListNumberTransIdIdCard] = useState<
    string[]
  >([]);
  const [listNumberTransIdEkyc, setListNumberTransIdEkyc] = useState<string[]>(
    [],
  );
  const [listDataType, setListDataType] = useState<string[]>([]);
  const [dataParams, setDataParams] = useState<any>();
  const [transIdIdCard, setTransIdIdCard] = useState<string>('');
  const [transIdIdEkyc, setTransIdIdEkyc] = useState<string>('');
  const [ekycRepositoryId, setEkycRepositoryId] = useState<string>('');
  const [menuVisible, setMenuVisible] = useState(false);
  const [menuVisibleEkyc, setMenuVisibleEkyc] = useState(false);
  const ReviewServices = useReviewServices();

  useEffect(() => {
    getEkycHistoryDetail();
  }, []);

  useEffect(() => {
    if (transIdIdCard && dataParams && listDataType.length > 0) {
      getDataIdCardByTransId(transIdIdCard);
    }
  }, [transIdIdCard, dataParams, listDataType]);

  useEffect(() => {
    if (transIdIdEkyc && dataParams && listDataType.length > 0) {
      getDataEkycByType(transIdIdEkyc);
    }
  }, [transIdIdEkyc, dataParams, listDataType]);

  const getDataIdCardByTransId = async (transId: string) => {
    try {
      setIsLoading(true);
      const values = await Promise.all(
        listDataType
          .filter((data: any) => data.includes(transId))
          .filter(
            (data: any) =>
              data.includes('backSide') ||
              data.includes('frontSide') ||
              data.includes('passport'),
          )
          .map((type: string) =>
            ReviewServices.getDataEkycByType({
              ...dataParams,
              order: type.split('_')[2],
              type: type.split('_')[0] + '_' + type.split('_')[1],
            }),
          ),
      );
      if (values) {
        setListValueIdCard(values);
      }
    } catch (error) {
      console.log('Error get data id card by trans id', error);
    } finally {
      setIsLoading(false);
    }
  };

  const getDataEkycByType = async (transId: string) => {
    try {
      setIsLoadingEkyc(true);
      const values = await Promise.all(
        listDataType
          .filter((data: any) => data?.includes(transId))
          .filter(
            (data: any) =>
              !data.includes('backSide') &&
              !data.includes('frontSide') &&
              !data.includes('passport'),
          )
          .map((type: string) =>
            ReviewServices.getDataEkycByType({
              ...dataParams,
              order: type.split('_')[2],
              type: type.split('_')[0] + '_' + type.split('_')[1],
            }),
          ),
      );
      if (values) {
        setListValueEkyc(values);
      }
    } catch (error) {
      console.log('Error get data ekyc by trans id', error);
    } finally {
      setIsLoadingEkyc(false);
    }
  };

  const toogleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  const toogleMenuEkyc = () => {
    setMenuVisibleEkyc(!menuVisibleEkyc);
  };

  const getEkycHistoryDetail = async () => {
    try {
      const params = {
        userTaskId,
        tickerId: ticketId,
      };
      const data = await ReviewServices.getHistoryEkycDetail(params);

      if (data) {
        setEkycRepositoryId(data.ekycRepositoryId);
        const dataEkycRepo = await ReviewServices.getEkycRepoById(
          data.ekycRepositoryId,
        );
        if (dataEkycRepo) {
          const params = {
            ekycRepositoryId: data.ekycRepositoryId,
            id: dataEkycRepo.resourceId,
            recipientId: dataEkycRepo.personalId,
          };
          setDataParams(params);
          const dataAllType = await ReviewServices.getAllTypesOfEkyc(params);
          if (dataAllType) {
            setListDataType(dataAllType);
            // id card
            const listTransIdIdCard = dataAllType
              .filter(
                (type: string) =>
                  type.includes('backSide') ||
                  type.includes('frontSide') ||
                  type.includes('passport'),
              )
              .map((type: string) => type.split('_')[1])
              .sort((a: any, b: any) => a - b);
            const listTransIdIdCardFilter = Array.from(
              new Set(listTransIdIdCard),
            ).sort((a, b: any) => b.localeCompare(a)) as string[];
            setListNumberTransIdIdCard(listTransIdIdCardFilter);
            setTransIdIdCard(listTransIdIdCardFilter[0]);

            // ekyc
            const listTransIdEkyc = dataAllType
              .filter(
                (type: string) =>
                  !type.includes('backSide') &&
                  !type.includes('frontSide') &&
                  !type.includes('passport'),
              )
              .map((type: string) => type.split('_')[1])
              .sort((a: any, b: any) => a - b);
            const listTransIdEkycFilter = Array.from(
              new Set(listTransIdEkyc),
            ).sort((a: any, b: any) => b.localeCompare(a)) as string[];
            setListNumberTransIdEkyc(listTransIdEkycFilter);
            setTransIdIdEkyc(listTransIdEkycFilter[0]);
          }
        } else {
          Alert.alert(t('CM.noti'), t('RV.eid.no-ekyc'));
        }
      }
    } catch (error) {
    } finally {
    }
  };

  const getDataIdCard = (type: string) => {
    return listValueIdCard.find((data: any) => data?.[0].type.includes(type));
  };

  const getDataImageEkyc = () => {
    return listValueEkyc.filter(
      (data: any) => !data?.[0].type.includes('videoEkyc'),
    );
  };

  const getDataVideoEkyc = () => {
    return listValueEkyc
      .filter((data: any) => data?.[0].type.includes('videoEkyc'))
      .sort((a, b) => a[0].order - b[0].order)
      .map(data => {
        return data[0].content;
      });
  };

  return (
    <View>
      <View style={styles.ctnBody}>
        <View style={styles.ctnSubBody}>
          <View style={styles.ctnOpen}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowInfoCCCD(!isShowInfoCCCD)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={
                  isShowInfoCCCD ? 'arrow-circle-down' : 'arrow-circle-right'
                }
              />
              <TextCM style={styles.textTitle}>{t('RV.eid.imageId')}</TextCM>
            </Pressable>

            <View style={{}}>
              <OverflowMenu
                anchor={() => (
                  <TouchableOpacity onPress={toogleMenu}>
                    <Icon width={20} height={20} name="more-vertical" />
                  </TouchableOpacity>
                )}
                visible={menuVisible}
                onBackdropPress={toogleMenu}>
                {listNumberTransIdIdCard.map((action, index) => (
                  <MenuItem
                    key={action}
                    title={
                      t('CM.step') +
                      ' ' +
                      (listNumberTransIdIdCard.length - index)
                    }
                    accessoryRight={
                      action === transIdIdCard ? (
                        <Icon fill={Color.BgPrimary} name="checkmark-outline" />
                      ) : undefined
                    }
                    onPress={() => {
                      setTransIdIdCard(action);
                      toogleMenu();
                    }}
                  />
                ))}
              </OverflowMenu>
            </View>
          </View>

          {isShowInfoCCCD && (
            <>
              {!isLoading ? (
                <>
                  <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                    {getDataIdCard('frontSide') && (
                      <IDCardView
                        data={getDataIdCard('frontSide')}
                        title={t('RV.eid.frontSide')}
                      />
                    )}
                    {getDataIdCard('backSide') && (
                      <IDCardView
                        data={getDataIdCard('backSide')}
                        title={t('RV.eid.backSide')}
                      />
                    )}
                    {getDataIdCard('passport') && (
                      <IDCardView
                        data={getDataIdCard('passport')}
                        title={t('RV.eid.passport')}
                      />
                    )}
                  </View>
                </>
              ) : (
                <View style={styles.ctnLoading}>
                  <Spinner />
                </View>
              )}
            </>
          )}
        </View>
      </View>

      <View style={styles.ctnBody}>
        <View style={styles.ctnSubBody}>
          <View style={styles.ctnOpen}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowInfoEkyc(!isShowInfoEkyc)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={
                  isShowInfoEkyc ? 'arrow-circle-down' : 'arrow-circle-right'
                }
              />
              <TextCM style={styles.textTitle}>{t('RV.eid.ekycInfo')}</TextCM>
            </Pressable>

            <OverflowMenu
              anchor={() => (
                <TouchableOpacity onPress={toogleMenuEkyc}>
                  <Icon width={20} height={20} name="more-vertical" />
                </TouchableOpacity>
              )}
              visible={menuVisibleEkyc}
              onBackdropPress={toogleMenuEkyc}>
              {listNumberTransIdEkyc.map((action, index) => (
                <MenuItem
                  key={action}
                  title={
                    t('CM.step') + ' ' + (listNumberTransIdEkyc.length - index)
                  }
                  accessoryRight={
                    action === transIdIdEkyc ? (
                      <Icon fill={Color.BgPrimary} name="checkmark-outline" />
                    ) : undefined
                  }
                  onPress={() => {
                    setTransIdIdEkyc(action);
                    toogleMenuEkyc();
                  }}
                />
              ))}
            </OverflowMenu>
          </View>

          {isShowInfoEkyc && (
            <>
              {!isLoadingEkyc ? (
                <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                  {ekycRepositoryId && transIdIdEkyc && (
                    <ViewImageEkyc
                      uriVideo={getDataVideoEkyc()}
                      listData={getDataImageEkyc()}
                      ekycRepoId={ekycRepositoryId}
                      selectEkycTime={transIdIdEkyc}
                    />
                  )}
                </View>
              ) : (
                <View style={styles.ctnLoading}>
                  <Spinner />
                </View>
              )}
            </>
          )}
        </View>
      </View>
    </View>
  );
};

export default EkycInfo;

type PropsIDCard = {
  title: string;
  data: any;
};

const IDCardView = ({title, data}: PropsIDCard) => {
  const {t} = useTranslation();
  return (
    <View style={styles.ctnIdCard}>
      <TextCM style={styles.textTitleImage}>{title}</TextCM>
      <TextCM>
        {t('RV.eid.uploadTime')}{' '}
        {DateUtils.convertDateToDDMMYYYY_H_M(data?.[0].createdDate)}
      </TextCM>
      <View style={styles.ctnViewImage}>
        <View
          style={{
            borderRadius: 6,
            overflow: 'hidden',
          }}>
          <Image
            style={{
              minHeight: 100,
            }}
            resizeMode="contain"
            source={{uri: String.PREFIX_IMG_BASE64 + data?.[0].content}}
          />
        </View>

        <TextCM style={styles.textImage}>{data?.[0].name}</TextCM>
        {/* <TextCM style={styles.textKb}>1.7 MB</TextCM> */}
      </View>
    </View>
  );
};

import {Buffer} from 'buffer';

const ViewImageEkyc = ({
  listData,
  uriVideo,
  ekycRepoId,
  selectEkycTime,
}: {
  listData: any[];
  uriVideo: string[];
  ekycRepoId: string;
  selectEkycTime: string;
}) => {
  const {t} = useTranslation();
  const [videoUri, setVideoUri] = useState<string | null>(null);
  const [err, setErr] = useState(false);
  useEffect(() => {
    async function createVideoFile() {
      if (uriVideo.length > 0) {
        try {
          // To tn file tm thi
          const fileName = `temp_video_${Date.now()}.mp4`;
          const fileUri = `${RNFS.DocumentDirectoryPath}/${fileName}`;

          // Ghi d liu base64 vo file
          for (const base64Chunk of uriVideo) {
            await RNFS.appendFile(fileUri, base64Chunk, 'base64');
          }
          // Cp nht state vi URI ca file video
          setVideoUri(`file://${fileUri}`);
        } catch (error) {
          console.error('Error creating video file:', error);
        }
      }
    }

    createVideoFile();

    // Cleanup function  xa file tm thi khi component b hy
    return () => {
      try {
        if (videoUri) {
          const filePath = videoUri.replace('file://', '');
          RNFS.unlink(filePath)
            .then(() => console.log('Temporary file deleted'))
            .catch(err => console.error('Error deleting temporary file:', err));
        }
      } catch (error) {}
    };
  }, [uriVideo]);

  return (
    <View>
      <TextCM
        style={[
          styles.textTitleImage,
          {
            marginBottom: 4,
          },
        ]}>
        {t('RV.eid.videoEkyc')}
      </TextCM>
      {/* <WebView
        startInLoadingState
        style={styles.video}
        mediaPlaybackRequiresUserAction={false} // Enable autoplay for videos
        allowsInlineMediaPlayback={true} // Allow inline video playback (for iOS)
        source={{
          uri:
            BASE_URL +
            `/p-webview?ekycRepositoryId=${ekycRepoId}&selectEkycTime=${selectEkycTime}`,
        }}
        webviewDebuggingEnabled
        onError={syntheticEvent => {
          const {nativeEvent} = syntheticEvent;
          console.error('WebView error: ', nativeEvent);
        }}
        onHttpError={syntheticEvent => {
          const {nativeEvent} = syntheticEvent;
          console.error('HTTP error: ', nativeEvent);
        }}
        javaScriptEnabled
      /> */}
      <View style={styles.ctnVideo}>
        {videoUri && (
          <>
            {!err ? (
              <Video
                style={styles.video}
                controls={true}
                resizeMode="contain"
                onError={error => {
                  console.log(error);
                  setErr(true);
                }}
                source={{uri: videoUri}}
              />
            ) : (
              <Icon width={50} height={50} name="alert-triangle-outline" />
            )}
          </>
        )}
      </View>

      <TextCM
        style={[
          styles.textTitleImage,
          {
            marginBottom: 4,
          },
        ]}>
        {t('RV.eid.imageEkyc')}
      </TextCM>
      <FlatList
        horizontal
        contentContainerStyle={{gap: 10}}
        data={listData}
        renderItem={({item}) => (
          <Image
            width={85}
            height={85}
            source={{uri: String.PREFIX_IMG_BASE64 + item?.[0].content}}
          />
        )}
      />
    </View>
  );
};
