//import liraries
import React, {useEffect, useState} from 'react';
import {FlatList, View} from 'react-native';
import styles from './styles';
import TextCM from '../../../../../../components/Text';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {DateUtils} from '../../../../../../../utils';
import {SafeAreaView} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../../../../constants/Size';
import {useTranslation} from 'react-i18next';
import {isNull} from 'lodash';
import {useTicketServices} from '../../../../../../../hooks/apiHooks/useTicketServices';
import {Icon} from '@ui-kitten/components';

export const HISTORY_ACTION = {
  CREATE: 'add',
  PUBLIC: 'public',
  UNPUBLIC: 'unpublic',
  ACTIVE: 'active',
  INACTIVE: 'inactive',
  UPDATE: 'edit',
  DELETE: 'delete',
  ASSIGN: 'assign',
  REQUEST_APP: 'request_approve',
  COMMENT: 'comment',
  REJECT: 'reject_approve',
  REJECT_CONSIDER: 'reject_consider',
  AUTHORITY: 'authority',
  REQUEST_UPDATE: 'request_update',
  SIGNING: 'signing',
  UPLOAD: 'upload',
  CANCEL: 'cancel',
  EDIT_COMMENT: 'edit_comment',
  REJECT_APPROVE: 'reject_approve',
  REVIEW_CONDUCT: 'review_conduct',
};

interface IHistories {
  id: string;
  ticketId: string;
  userTaskId: null;
  notionName: null;
  action: any;
  entity: 'ticket';
  createdDate: string;
  createdBy: string;
  lastModifiedDate: string;
  lastModifiedBy: string;
  custId: number;
  orgIn: string;
  name: null;
  avatar: null;
}

// create a component
const HistoryTicket = () => {
  const {t} = useTranslation();
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const [page, setPage] = useState<number>(0);
  const [totals, setTotals] = useState<number>(0);
  const [refreshing, setRefreshing] = useState<boolean>(false);
  const [listHistories, setListHistories] = useState<IHistories[]>([]);
  const {accessToken} = useAppSelector(state => state.common);
  const ListServices = useTicketServices();
  useEffect(() => {
    (async () => {
      await getTicketHistories(page);
    })();
  }, [page]);

  const getTicketHistories = async (page: number) => {
    try {
      const dataTicketHistory = await ListServices.getTicketHistories(
        page,
        dataTicketRedux.id,
        accessToken,
      );
      if (page === 0) {
        setListHistories(dataTicketHistory?.data);
      } else {
        setListHistories([...listHistories, ...dataTicketHistory?.data]);
      }
      setTotals(dataTicketHistory!.totalRecords);
    } catch (error) {
      console.error(error);
    }
  };

  const fetchNextPage = () => {
    if (listHistories.length < totals) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setRefreshing(true);
    }
    setPage(0);
  };

  const renderIcon = (bgColor: string, iconName: string, fill: string) => {
    return (
      <View
        style={[
          {
            backgroundColor: bgColor,
          },
          styles.ctnViewIcon,
        ]}>
        <Icon fill={fill} name={iconName} width={15} height={15} />
      </View>
    );
  };

  const getValueIcon = (action: any) => {
    switch (action) {
      case HISTORY_ACTION.CREATE:
        return renderIcon('#F2ECFD', 'plus', '#6F2CED');
      // case HISTORY_ACTION.PUBLIC:
      //   return 'public';
      // case HISTORY_ACTION.UNPUBLIC:
      //   return 'unpublic';
      // case HISTORY_ACTION.ACTIVE:
      //   return 'active';
      // case HISTORY_ACTION.INACTIVE:
      //   return 'inactive';
      // case HISTORY_ACTION.UPDATE:
      //   return 'edit';
      // case HISTORY_ACTION.DELETE:
      //   return 'delete';
      // case HISTORY_ACTION.ASSIGN:
      //   return 'assign';
      case HISTORY_ACTION.REQUEST_APP:
        return renderIcon('#E8F7EF', 'file', '#39AC6D');
      case HISTORY_ACTION.COMMENT:
        return renderIcon('#f3f5f8', 'message-circle', '');
      case HISTORY_ACTION.REJECT:
        return renderIcon('#FCEEED', 'close', '#E14337');
      case HISTORY_ACTION.REJECT_CONSIDER:
        return renderIcon('#FCEEED', 'minus', '#E14337');
      case HISTORY_ACTION.AUTHORITY:
        return renderIcon('#F2ECFD', 'swap', '#6F2CED');
      case HISTORY_ACTION.REQUEST_UPDATE:
        return renderIcon('#FFF3EB', 'arrow-circle-left', '#FC6B03');
      case HISTORY_ACTION.SIGNING:
        return renderIcon('#E8F7EF', 'edit', '#39AC6D');
      case HISTORY_ACTION.UPLOAD:
        return renderIcon('#FFF3EB', 'cloud-upload-outline', '#FC6B03');
      case HISTORY_ACTION.CANCEL:
        return renderIcon('#FCEEED', 'minus', '#E14337');
      case HISTORY_ACTION.EDIT_COMMENT:
        return renderIcon('#f3f5f8', 'edit-2-outline', '');
      case HISTORY_ACTION.REJECT_APPROVE:
        return renderIcon('#FCEEED', 'close', '#E14337');
      case HISTORY_ACTION.REVIEW_CONDUCT:
        return renderIcon('#E8F7EF', 'checkmark', '#39AC6D');
      default:
        return <View style={styles.ctnViewIcon} />;
    }
  };

  const renderItem = ({item, index}: {item: IHistories; index: number}) => {
    return (
      <>
        {!isNull(item.createdBy) && (
          <>
            <View style={styles.ctnItem}>
              {getValueIcon(item.action)}
              <View style={{rowGap: SizeDP(4)}}>
                <TextCM style={styles.title}>
                  {t(`TK.transaction-history.action.${item.action}`)}
                </TextCM>
                <View style={styles.ctnDes}>
                  <TextCM style={styles.Des}>
                    {`${DateUtils.convertDateToDDMMYYYY_H_M(
                      item.createdDate,
                    )} ${t('TK.by')} ${item.createdBy}`}
                  </TextCM>
                </View>
              </View>
            </View>
            {listHistories.length - 1 !== index && (
              <View style={styles.tabHoz}></View>
            )}
          </>
        )}
      </>
    );
  };

  return (
    <SafeAreaView style={styles.container} edges={['bottom']}>
      {listHistories.length > 0 && (
        <View>
          <FlatList
            keyExtractor={item => item.id}
            refreshing={refreshing}
            onRefresh={onRefresh}
            data={listHistories}
            onEndReached={fetchNextPage}
            onEndReachedThreshold={0.5}
            renderItem={renderItem}
          />
        </View>
      )}
    </SafeAreaView>
  );
};

//make this component available to the app
export default HistoryTicket;
