//import liraries
import React, {useEffect, useState} from 'react';
import {
  View,
  ScrollView,
  TouchableOpacity,
  Platform,
  Share,
} from 'react-native';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../../../../constants/Size';
import {
  IconDownload,
  IconTrash,
  IconUpload,
  IconView,
} from '../../../../../../../assets/images';
import TableCM from '../../../../../../components/Table';
import {IInfoFile, IInfoFileRelative, IInfoForm} from './interface';
import {CommonUtils, DateUtils} from '../../../../../../../utils';
import TextCM from '../../../../../../components/Text';
import PagingCM from '../../../../../../components/Paging';
import {useNavigation} from '@react-navigation/native';
import Toast from 'react-native-toast-message';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {Status} from '../../../../../../../model/enum/common.enum';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../../../../../hooks/apiHooks/useReviewTicketServices';
import RNFS, {DownloadFileOptions} from 'react-native-fs';
import ReactNativeBlobUtil from 'react-native-blob-util';
import ModalFullScreen from '../../../../../../components/ModalFullScreen';
import ViewFileScreen from './ViewFileWebView';
import {getConfig} from '../../../../../../../services/httpClient';
import ViewFormScreen from '../../../../../../components/ViewFormScreen';
import {useZutandStore} from '../../../../../../../zutand/store';

// create a component
const Document = () => {
  const pageSize = 3;
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const [currentPage, setCurrentPage] = useState(1);
  const [totalItems, setTotalItems] = useState<number>(0);
  const [dataInfoFile, setDataInfoFile] = useState<any[]>([]);
  const [dataForm, setDataForm] = useState<any[]>([]);
  const [dataRelative, setDataRelative] = useState<any[]>([]);
  const [isViewFile, setIsViewFile] = useState<boolean>(false);
  const [urlViewFile, setUrlViewFile] = useState<string>('');
  const [isShowViewForm, setIsShowViewForm] = useState<boolean>(false);
  const [dataViewForm, setDataViewForm] = useState<any>();
  const [viewNameFile, setViewNameFile] = useState<string>('');

  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);

  const {statusTicketSelect} = useZutandStore(state => state);
  const ReviewTicketServices = useReviewServices();
  useEffect(() => {
    getListInfoFile();
    getListForm();
  }, []);

  useEffect(() => {
    getListRelativeFile(currentPage);
  }, [currentPage]);

  const getListInfoFile = async () => {
    try {
      const listFile = await ReviewTicketServices.getListFileByTicketId(
        dataTicketRedux.id,
      );
      convertDataToTable(listFile);
    } catch (error) {}
  };

  const removeDuplicateUserTasks = (data: any) => {
    const seen = new Set();
    const uniqueArr = [];

    for (const item of data) {
      if (item.userTaskId === null) {
        // Always keep if userTaskId is null
        uniqueArr.push(item);
      } else if (!seen.has(item.userTaskId)) {
        seen.add(item.userTaskId);
        uniqueArr.push(item);
      }
    }
    return uniqueArr;
  };

  const getListForm = async () => {
    try {
      const dataStatusParty = await ReviewTicketServices.getStatusParty(
        dataTicketRedux?.id,
        accessToken,
      );
      if (dataStatusParty) {
        const statusParty = dataStatusParty.party?.recipients;

        let dataListFormId = await ReviewTicketServices.getListFormIdByTicketId(
          dataTicketRedux?.id,
        );
        if (!dataListFormId) return;
        dataListFormId = removeDuplicateUserTasks(dataListFormId);
        Promise.all(
          dataListFormId.map((data: any) =>
            ReviewTicketServices.getFormERequest(
              {
                versionId: data.versionId,
              },
              accessToken,
            ),
          ),
        ).then((values: IInfoForm[]) => {
          convertDataFromToTable(
            values?.map(value => ({
              ...value,
              userTaskId: dataListFormId?.find(
                (form: any) => form.formId === value.formId,
              )?.userTaskId,
              versionId: dataListFormId?.find(
                (form: any) => form.formId === value.formId,
              )?.versionId,
            })),
            statusParty,
          );
        });
      }
    } catch (error) {
      console.log('error', error);
    }
  };

  const getListRelativeFile = async (page: number) => {
    try {
      const params = {
        size: pageSize,
        page: page - 1,
        sort: 'createdDate,desc',
        ticketId: dataTicketRedux?.id,
      };

      const data = await ReviewTicketServices.getListFileRelative(
        params,
        accessToken,
      );
      if (data) {
        if (data?.data?.length === 0) {
          if (page > 1) {
            setCurrentPage(page - 1);
          } else {
            setDataRelative([]);
            setTotalItems(0);
          }
          return;
        }
        setTotalItems(data?.totalRecords);
        convertDataFileToTable(data?.data);
      }
    } catch (error) {}
  };

  const convertDataToTable = (data: IInfoFile[]) => {
    const newData = data.map((infoFile: IInfoFile, index: number) => ({
      STT: String(index + 1),
      keyName: (
        <TextCM style={styles.txtText} numberOfLines={1}>
          {infoFile?.name}
        </TextCM>
      ),
      notionName: infoFile?.notionName || '---',
      createdBy: infoFile.createdBy,
      documentSize: CommonUtils.getSize(infoFile?.fileSize),
      type: infoFile?.name?.split('.').slice(-1)[0],
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
      action: renderActionInfoFile(true, infoFile),
    }));
    setDataInfoFile(newData);
  };

  const convertDataFromToTable = (
    data: IInfoForm[],
    dataStatusParty: any[],
  ) => {
    const newData = data.map((infoForm: IInfoForm, index: number) => ({
      STT: String(index + 1),
      keyName: (
        <TouchableOpacity
          onPress={() => {
            setIsShowViewForm(true);
            setDataViewForm(infoForm);
          }}>
          <TextCM style={styles.txtText} numberOfLines={1}>
            {infoForm?.formName}
          </TextCM>
        </TouchableOpacity>
      ),
      createdBy: infoForm.createdBy,
      notionName:
        dataStatusParty?.find(d => infoForm.userTaskId == d.id)?.notionName ??
        'Form quy trnh',
    }));
    setDataForm(newData);
  };

  const convertDataFileToTable = (data: IInfoFileRelative[]) => {
    const newData = data.map((infoFile: IInfoFileRelative, index: number) => ({
      STT: String((currentPage - 1) * pageSize + 1 + index),
      keyName: (
        <TextCM style={styles.txtText} numberOfLines={1}>
          {infoFile?.fileName}
        </TextCM>
      ),
      notionName: infoFile?.notionName || '---',
      createdBy: infoFile.createdBy,
      documentSize: CommonUtils.getSize(infoFile.size),
      type: infoFile?.fileName?.split('.')?.pop(),
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
      action: renderActionInfoFile(false, infoFile),
    }));
    setDataRelative(newData);
  };

  const handleViewFile = async (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    if (!data?.name) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: 'File khng c tn',
      });
      return;
    }
    if (isInfoFile) {
      setViewNameFile(data?.name);
    } else {
      setViewNameFile((data as IInfoFileRelative).fileName?.split('.')?.[0]);
    }
    try {
      Toast.show({
        type: 'info',
        text1: t('CM.noti'),
        text2: t('TK.viewFile'),
      });
      const formData = new FormData();
      if (isInfoFile && (data as IInfoFile).documentType === 'ewriter') {
        const base64String = await getFiles(
          (data as IInfoFile).notionId,
          data.id,
          data?.name,
        );
        formData.append('file', base64String);
        const dataView = await ReviewTicketServices.viewFile(
          formData,
          accessToken,
        );
        if (dataView?.code == 1) {
          setIsViewFile(true);
          setUrlViewFile(dataView.message);
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: t('CM.error'),
          });
        }
        return;
      }

      let keyName: string = (data as any)?.name;
      if (isInfoFile) {
        keyName = (data as IInfoFile).keyName;
      } else {
        keyName = (data as IInfoFileRelative).name;
      }
      let bucketName: string = data.bucketName;
      if (!data.bucketName) {
        // if (data.name.includes('minio')) {
        //   const pathFile = (data as any)?.name?.split('https://')?.[1];
        //   const arr = pathFile?.split('/');
        //   keyName = `/${arr?.slice(2)?.join('/')}`;
        //   bucketName = arr?.[1];
        // } else if (data.name.includes('s3-hfx03')) {
        //   const pathFile = (data as any)?.name?.split('https://')?.[1];
        //   const arr = pathFile?.split('.s3-hfx03.fptcloud.com:443');
        //   keyName = arr?.[1];
        //   bucketName = arr?.[0];
        // }
        formData.append('href', data?.name);
      } else {
        formData.append('keyName', keyName);
        formData.append('bucketName', bucketName);
        formData.append('beanName', data.beanName);
      }

      const dataView = await ReviewTicketServices.viewFile(
        formData,
        accessToken,
      );
      if (dataView?.code == 1) {
        setIsViewFile(true);
        setUrlViewFile(dataView.message);
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.error'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.error'),
      });
    }
  };

  const handleClearFile = async (idFile: string) => {
    try {
      const data = await ReviewTicketServices.deleteFileRelative(
        idFile,
        accessToken,
      );
      await getListRelativeFile(currentPage);
      Toast.show({
        type: 'success',
        text1: t('CM.noti'),
        text2: t('TK.deleteFileSuccess'),
      });
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('TK.deleteFileErr'),
      });
    }
  };

  const handleUploadFile = async () => {
    try {
      const file = await CommonUtils.handleGetFile();
      if (!file) return;

      const formData = new FormData();
      formData.append('uploadFile', file);
      formData.append(
        'attachmentJson',
        JSON.stringify({
          ticketId: dataTicketRedux?.id,
        }),
      );
      Toast.show({
        type: 'info',
        text1: t('CM.noti'),
        text2: t('NT.notification.downloadingPleaseWait'),
      });
      const data = await ReviewTicketServices.uploadFileRelative(
        formData,
        accessToken,
      );
      if (data) {
        await getListRelativeFile(currentPage);
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('TK.uploadFileSuccess'),
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('TK.uploadFileErr'),
        });
      }
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('TK.uploadFileErr'),
      });
    }
  };

  const handleDownload = async (
    data: IInfoFile | IInfoFileRelative,
    isInfoFile: boolean,
  ) => {
    let fileName: string = (data as any).fileName;
    if (isInfoFile) {
      fileName = (data as IInfoFile).keyName.replace('/', '_');
    } else {
      fileName = (data as IInfoFileRelative).fileName;
    }

    const toFile = `${
      Platform.OS === 'android'
        ? RNFS.DownloadDirectoryPath
        : RNFS.DocumentDirectoryPath
    }/${new Date().getTime()}_${fileName}`;

    if (isInfoFile) {
      await downloadFileInfoForm(
        toFile,
        data.id,
        (data as IInfoFile)?.notionId,
      );
    } else {
      if (!data.bucketName) {
        await downloadFileNotBucketName(toFile, data.name);
      } else {
        await downloadFileWithBucketName(toFile, data.id);
      }
    }
  };

  const downloadFileNotBucketName = async (toFile: string, fromUrl: string) => {
    const options: DownloadFileOptions = {
      fromUrl: fromUrl,
      toFile,
      background: true,
      begin: (res: any) => {},
      progress: (res: any) => {
        let percentage = (res.bytesWritten / res.contentLength) * 100;
        // setProgress(percentage);
      },
      connectionTimeout: 1000 * 5,
    };
    try {
      const result = await RNFS.downloadFile(options).promise;
      if (result.statusCode === 200) {
        if (Platform.OS === 'ios') {
          const dataShare = await Share.share({
            url: toFile,
          });
          if (dataShare.action == Share.dismissedAction) {
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('CM.downloadFail'),
            });
          } else {
            Toast.show({
              type: 'success',
              text1: t('CM.noti'),
              text2: t('CM.downloadSuccess'),
            });
          }
        } else {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.downloadSuccess'),
          });
        }
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.downloadFail'),
        });
      }
    } catch (err: any) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.downloadFail'),
      });
    }
  };

  const downloadFileWithBucketName = async (toFile: string, id: any) => {
    try {
      Toast.show({
        type: 'info',
        text1: t('CM.noti'),
        text2: t('CM.pleaseWait'),
      });
      const dataBase64 = await ReactNativeBlobUtil.fetch(
        'GET',
        `${
          getConfig().BASE_URL
        }/services/erequest/api/attachments/download-file?attachmentId=${id}`,
      )
        .then(res => {
          let status = res.info().status;
          if (status == 200) {
            let base64Str = res.base64();
            return base64Str;
          } else return null;
        })
        .catch(() => null);
      if (dataBase64) {
        try {
          await RNFS.writeFile(toFile, dataBase64, 'base64');
        } catch (error) {
          console.log(error);
        }
        if (Platform.OS === 'ios') {
          const dataShare = await Share.share({
            url: toFile,
          });
          if (dataShare.action == Share.dismissedAction) {
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('CM.downloadFail'),
            });
          } else {
            Toast.show({
              type: 'success',
              text1: t('CM.noti'),
              text2: t('CM.downloadSuccess'),
            });
          }
        } else {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.downloadSuccess'),
          });
        }
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.downloadFail'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.downloadFail'),
      });
    }
  };

  const getFiles = async (
    userTaskId: string,
    fileId: any,
    fileName: string,
  ) => {
    try {
      const type = fileName?.split('.')?.[1];
      const path = `${ReactNativeBlobUtil.fs.dirs.DocumentDir}/${new Date()
        .getTime()
        .toString()}.${type}`;
      const dataPath = await ReactNativeBlobUtil.config({
        path: path,
      })
        .fetch(
          'GET',
          `${getConfig().BASE_URL}/services/erequest/api/tickets/${
            dataTicketRedux.id
          }/userTaskId/${userTaskId}/fileId/${fileId}`,
        )
        .then(res => {
          let status = res.info().status;
          if (status == 200) {
            let base64Str = res.path();
            return base64Str;
          } else return null;
        })
        .catch(() => null);
      const file = {
        uri: Platform.OS === 'ios' ? dataPath : 'file://' + dataPath,
        name: `${new Date().getTime().toString()}.${type}`,
        type: CommonUtils.getMimeType(fileName),
      };
      return file;
    } catch (error) {
      console.log(error);
    }
  };

  const downloadFileInfoForm = async (
    toFile: string,
    id: any,
    userTaskId: string,
  ) => {
    try {
      const dataBase64 = await ReactNativeBlobUtil.fetch(
        'GET',
        `${getConfig().BASE_URL}/services/erequest/api/tickets/${
          dataTicketRedux.id
        }/userTaskId/${userTaskId}/fileId/${id}/content`,
      )
        .then(res => {
          let status = res.info().status;
          if (status == 200) {
            let base64Str = res.base64();
            return base64Str;
          } else return null;
        })
        .catch(() => null);
      if (dataBase64) {
        await RNFS.writeFile(toFile, dataBase64, 'base64');
        if (Platform.OS === 'ios') {
          const dataShare = await Share.share({
            url: toFile,
          });
          if (dataShare.action == Share.dismissedAction) {
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('CM.downloadFail'),
            });
          } else {
            Toast.show({
              type: 'success',
              text1: t('CM.noti'),
              text2: t('CM.downloadSuccess'),
            });
          }
        } else {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.downloadSuccess'),
          });
        }
      } else {
        Toast.show({
          type: 'error',
          text2: t('CM.downloadFail'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text2: t('CM.downloadFail'),
      });
    }
  };

  const renderActionInfoFile = (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    return (
      <View style={styles.ctnAction}>
        <TouchableOpacity
          onPress={() => handleViewFile(isInfoFile, data)}
          hitSlop={SizeDP(15)}>
          <IconView width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        <TouchableOpacity
          onPress={() => handleDownload(data, isInfoFile)}
          hitSlop={SizeDP(15)}>
          <IconDownload width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        {!isInfoFile &&
          (data as IInfoFileRelative).allowDeleted &&
          dataTicketRedux?.ticketStatus !== Status.voided &&
          dataTicketRedux?.ticketStatus !== Status.rejected && (
            <TouchableOpacity
              onPress={() => handleClearFile(data?.id?.toString())}
              hitSlop={SizeDP(15)}>
              <IconTrash width={SizeDP(16)} height={SizeDP(16)} />
            </TouchableOpacity>
          )}
      </View>
    );
  };

  return (
    <SafeAreaView style={{flex: 1}} edges={[]}>
      <ScrollView showsVerticalScrollIndicator={false}>
        <View style={styles.ctnForm}>
          <View style={styles.ctnView}>
            <TextCM style={styles.txtTitle}>{t('TK.docRelative')}</TextCM>
            {statusTicketSelect === Status.processing && (
              <TouchableOpacity onPress={handleUploadFile} hitSlop={SizeDP(15)}>
                <IconUpload width={SizeDP(24)} height={SizeDP(24)} />
              </TouchableOpacity>
            )}
          </View>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'notionName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={dataRelative}
          />
          {totalItems > 0 && (
            <PagingCM
              collectionSize={totalItems}
              currentPage={currentPage}
              pageSize={pageSize}
              onPageChange={currentPage => setCurrentPage(currentPage)}
            />
          )}
        </View>
        <View style={styles.ctnForm}>
          <TextCM style={styles.txtTitle}>{t('TK.templateInfo')}</TextCM>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'notionName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={dataInfoFile}
          />
        </View>
        <View style={styles.ctnForm}>
          <TextCM style={styles.txtTitle}>{t('TK.templateForm')}</TextCM>
          <TableCM
            stickyLeft={false}
            tableHeaders={['STT', 'keyName', 'createdBy', 'notionName']}
            tableData={dataForm}
          />
        </View>
      </ScrollView>

      <ModalFullScreen
        isVisible={isViewFile}
        children={
          <ViewFileScreen
            url={urlViewFile}
            onClose={() => setIsViewFile(false)}
            name={viewNameFile}
          />
        }
      />

      <ModalFullScreen
        isVisible={isShowViewForm}
        children={
          <ViewFormScreen
            onClose={() => setIsShowViewForm(false)}
            isOnlyView={true}
            readOnly={true}
            versionId={dataViewForm?.versionId}
            data={{
              _userTaskId: dataViewForm?.userTaskId,
              _ticketId: dataTicketRedux?.id,
              formId: dataViewForm?.formId,
            }}
            nameHeader={dataViewForm?.formName}
          />
        }
      />
    </SafeAreaView>
  );
};

//make this component available to the app
export default Document;
