import {getConfig} from './httpClient';

export const createEcollaboServices = (fetchRequest: any) => ({
  async getFileById(fileId: string) {
    try {
      const endpoint = '/services/collabora/api/files/' + fileId;
      const data = await fetchRequest({
        url: getConfig().BASE_URL_ECOLLABO + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('getFileById', error);
      return Promise.reject(error);
    }
  },

  async changeStatusFile(body: any) {
    try {
      const endpoint = '/services/collabora/api/files';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_ECOLLABO + endpoint,
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('changeStatusFile', error);
      return Promise.reject(error);
    }
  },
});
