import {View} from 'react-native';
import HeaderCM from '../../../../../../../components/Header/HeaderCM';
import {String} from '../../../../../../../../constants';
import WebView from 'react-native-webview';
import {useEffect, useState} from 'react';
import {
  Divider,
  Icon,
  IndexPath,
  Menu,
  MenuGroup,
  MenuItem,
  TopNavigationAction,
} from '@ui-kitten/components';
import BottomSheetCM from '../../../../../../../components/BottomSheet';
import {FILE_ITEM_STATUS} from '../../../../interface';
import {useReviewCollaboServices} from '../../../../../../../../hooks/apiHooks/useReviewCollabo';
import {IFileFromCollabo} from '../../../../../../review-ecollabo-flow/interface';
import {Snackbar} from 'react-native-paper';
import {useTranslation} from 'react-i18next';

export const ROOT_FOLDER_ID = '000010000000000000';

type Props = {
  url: string;
  toogleViewFile: () => void;
  isView: number;
  fileId: string;
  changeDataFile: (data: any) => void;
  fileName: string;
};

const ViewAndEdit = ({
  url,
  toogleViewFile,
  isView,
  fileId,
  changeDataFile,
  fileName,
}: Props) => {
  const {t} = useTranslation();
  const [showMoreAction, setShowMoreAction] = useState(false);
  const [fileInfo, setFileInfo] = useState<IFileFromCollabo>();
  const [keyReload, setKeyReload] = useState(0);
  const [changeStatus, setChangeStatus] = useState(isView);
  const [changeStatusName, setChangeStatusName] = useState('');
  const [visibleSnackBar, setVisibleSnackBar] = useState(false);

  const onToggleSnackBar = () => setVisibleSnackBar(!visibleSnackBar);

  const EcollaboServices = useReviewCollaboServices();
  const toogleShowMore = () => {
    setShowMoreAction(!showMoreAction);
  };

  useEffect(() => {
    getDataFile();
  }, []);

  const getDataFile = async () => {
    const dataResp = await EcollaboServices.getFileById(fileId);
    if (dataResp) {
      setFileInfo(dataResp);
    }
  };

  const renderRightAction = () => {
    return (
      <TopNavigationAction
        icon={<Icon name="more-vertical" />}
        onPress={toogleShowMore}
      />
    );
  };

  const handleChangeStatusFile = async (status: number) => {
    const body = {
      ...fileInfo,
      status: status,
      versionId: status === FILE_ITEM_STATUS.DONE ? null : fileInfo?.versionId,
      fileParentId:
        fileInfo?.fileParentId === ROOT_FOLDER_ID ? '' : fileInfo?.fileParentId,
    };
    const dataResp = await EcollaboServices.changeStatusFile(body);
    if (dataResp) {
      onToggleSnackBar();
      setChangeStatusName('CL.changeStatusDone');
      changeDataFile((prev: any) => ({
        ...prev,
        status: status,
      }));
      setChangeStatus(status);
      setKeyReload(keyReload + 1); // Reload when change status success
    } else {
      setChangeStatusName('CL.changeStatusStop');
    }
  };

  const handleSubmit = (status: Number) => {
    if (status === FILE_ITEM_STATUS.DONE) {
      handleChangeStatusFile(FILE_ITEM_STATUS.DONE);
    } else if (status === FILE_ITEM_STATUS.STOP) {
      handleChangeStatusFile(FILE_ITEM_STATUS.STOP);
    }
    toogleShowMore();
  };

  return (
    <View style={{flex: 1}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={fileName}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        onPressIconLeft={toogleViewFile}
        renderContentRight={
          changeStatus === FILE_ITEM_STATUS.EDITING
            ? renderRightAction
            : undefined
        }
      />
      <Divider />
      <View style={{backgroundColor: '#fff', flex: 1}}>
        <WebView
          key={keyReload}
          javaScriptEnabled
          injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
          source={{uri: url}}
        />
      </View>

      <BottomSheetCM
        title={t('CL.changeStatus')}
        isVisible={showMoreAction}
        onClose={toogleShowMore}
        renderContent={
          <View>
            <MenuItem
              onPress={() => handleSubmit(FILE_ITEM_STATUS.DONE)}
              title={t('CL.done')}
            />
            <MenuItem
              onPress={() => handleSubmit(FILE_ITEM_STATUS.STOP)}
              title={t('CL.stop')}
            />
          </View>
        }
      />

      <Snackbar
        visible={visibleSnackBar}
        duration={1000}
        onDismiss={onToggleSnackBar}>
        {t(changeStatusName)}
      </Snackbar>
    </View>
  );
};

export default ViewAndEdit;
