import React, {useState, useCallback, useEffect} from 'react';
import {View, TouchableWithoutFeedback, FlatList} from 'react-native';

import {useFocusEffect} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import {Spinner, Divider} from '@ui-kitten/components';

import styles from './styles';
import {
  Recipient,
  ReceiptDetailResponseItem,
  PartyResponse,
  UserDataItem,
} from './interface';
import NoteItem from './components/NoteItem';
import HeaderCM from '../../../../components/Header/HeaderCM';
import TextCM from '../../../../components/Text';

import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import {useReviewEcontractServices} from '../../../../../hooks/apiHooks/useReviewEcontractServices';

type Props = {
  onClose: () => void;
};

const NoteTicket: React.FC<Props> = ({onClose}) => {
  const {t} = useTranslation();
  const {accessToken} = useAppSelector(state => state.common);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const ListServices = useTicketServices();
  const ReviewTicketServices = useReviewServices();
  const TicketReviewEcontractServices = useReviewEcontractServices();

  const [showListDataLoading, setshowListDataLoading] = useState<
    'loading' | 'done' | 'error'
  >('loading');
  const [noteData, setNoteData] = useState<Recipient[]>([]);
  const [dataUsers, setDataUsers] = useState<UserDataItem[]>([]);
  const [recipient, setRecipient] = useState<ReceiptDetailResponseItem[]>([]);

  const getListReviewerInformation = async (listEmail: string[]) => {
    const user: UserDataItem[] = await ListServices.getMultiLoginAccount(
      [...new Set(listEmail)],
      accessToken,
    );
    if (user) {
      setDataUsers(user);
    }
  };

  useFocusEffect(
    useCallback(() => {
      getData();
    }, []),
  );
  useEffect(() => {
    const newNote = [...noteData].map(item => {
      item.implementerId;
      let findedReceipient = recipient.find(
        rec => rec.id == item.implementerId,
      );
      if (findedReceipient) {
        let email = findedReceipient?.recipient?.contact?.email,
          userName = '',
          avatar;
        let user = dataUsers.find(item => item.email == email);
        if (user) {
          userName = user.personalName || '---';
          avatar = user.avatar;
        }
        return {
          ...item,
          email: findedReceipient?.recipient?.contact?.email,
          userName,
          avatar,
        };
      }
      return item;
    });
    setNoteData(newNote);
  }, [dataUsers]);

  const getData = async () => {
    try {
      const data: PartyResponse = await ReviewTicketServices.getStatusParty(
        dataTicketRedux.id,
        accessToken,
      );
      if (data) {
        setNoteData(data?.party?.recipients);
        const recipientList: ReceiptDetailResponseItem[] =
          await TicketReviewEcontractServices.getRecipientDetail(
            data?.party?.recipients[0]?.id || '',
            data?.ticketId || '',
          );
        if (recipientList) {
          setRecipient(recipientList);
          const listEmail = recipientList.map(
            item => item?.recipient?.contact?.email || '',
          );
          await getListReviewerInformation(listEmail);
        }
      }
    } catch (error) {
      setshowListDataLoading('error');
    } finally {
      setshowListDataLoading('done');
    }
  };

  return (
    <TouchableWithoutFeedback>
      <View style={styles.indexContainer}>
        <HeaderCM
          titleStyle={styles.titleContainer}
          fillIconBackLeft="#00204DB2"
          contentStyle={styles.backgroundContainer}
          title={t('CM.note')}
          onPressIconLeft={onClose}
        />
        <Divider />
        <View style={{flex: 1}}>
          {showListDataLoading === 'loading' && (
            <View style={styles.wrapSpin}>
              <Spinner size="small" />
            </View>
          )}
          {showListDataLoading === 'done' && (
            <FlatList
              data={noteData}
              renderItem={({item}) =>
                item.id && item.status == 'accepted' ? (
                  <NoteItem item={item} />
                ) : null
              }
              style={styles.container}
              ListEmptyComponent={() => (
                <View>
                  <TextCM style={{textAlign: 'center'}}>
                    {t('TK.noteEmptyDescription')}
                  </TextCM>
                </View>
              )}
            />
          )}
          {showListDataLoading === 'error' && (
            <View>
              <TextCM>{t('CM.emptyData')}</TextCM>
            </View>
          )}
        </View>
      </View>
    </TouchableWithoutFeedback>
  );
};

export default NoteTicket;
