import {IComments} from './interface';

function filterCommentsByParentId(comments: IComments[]) {
  const commentMap = new Map();

  // u tin, chng ta to mt Map vi key l commentId
  comments.forEach(comment => {
    const parentId = comment.commentId === null ? 'root' : comment.commentId;
    if (!commentMap.has(parentId)) {
      commentMap.set(parentId, []);
    }
    commentMap.get(parentId).push(comment);
  });

  return commentMap;
}

const decodeUnicodeToEmoji = (str: string): string => {
  return (
    str
      // Gii m chui dng \u{xxxxxx} thnh k t
      .replace(/\\u\{([0-9A-F]{1,6})\}/g, (_, codePoint) => {
        return String.fromCodePoint(parseInt(codePoint, 16));
      })
      // Gii m chui dng \uXXXX thnh k t
      .replace(/\\u([0-9A-F]{4})/g, (_, codeUnit) => {
        return String.fromCharCode(parseInt(codeUnit, 16));
      })
  );
};

const encodeEmojiToUnicode = (str: string): string => {
  return (
    str
      // Loi b vic m ha k t xung dng \n
      .replace(
        /([\uD800-\uDBFF][\uDC00-\uDFFF])|([^\n])/g,
        (match, emoji, otherChar) => {
          if (emoji) {
            // X l cc k t emoji (UTF-16 surrogate pairs)
            const codePoint =
              (emoji.charCodeAt(0) - 0xd800) * 0x400 +
              (emoji.charCodeAt(1) - 0xdc00) +
              0x10000;
            return '\\u{' + codePoint.toString(16).toUpperCase() + '}';
          } else if (otherChar) {
            // X l cc k t thng thng (ngoi tr \n)
            return (
              '\\u' +
              otherChar
                .charCodeAt(0)
                .toString(16)
                .toUpperCase()
                .padStart(4, '0')
            );
          }
          // Gi nguyn k t xung dng
          return match;
        },
      )
  );
};

const extractSpanList = (htmlString: string) => {
  const regex =
    /<span(?: [^>]*id="([^"]*)")?(?: [^>]*data-user-id="([^"]*)")?(?: [^>]*data-email="([^"]*)")?(?: [^>]*data-org-in="([^"]*)")?(?: [^>]*data-cust-id="([^"]*)")?[^>]*>(.*?)<\/span>/g;
  let match;
  const spanList = [];

  while ((match = regex.exec(htmlString)) !== null) {
    spanList.push({
      id: match[1],
      userId: match[2],
      email: match[3],
      orgIn: match[4],
      custId: match[5],
      textContent: match[6].trim(),
    });
  }

  return spanList;
};

const splitStringWithSpans = (input: string) => {
  const regex = /<span[^>]*>.*?<\/span>/g;
  const matches = input.match(regex); // Ly cc th <span>
  const splitText = input.split(regex); // Tch chui theo cc th <span>

  // Kt hp cc phn t text v <span> thnh mt mng
  const result: any[] = [];
  splitText.forEach((text, index) => {
    if (text.trim()) result.push(text.trim()); // Thm vn bn khng rng
    if (matches && matches[index]) result.push(matches[index]); // Thm th <span>
  });

  return result;
};

export {
  filterCommentsByParentId,
  encodeEmojiToUnicode,
  decodeUnicodeToEmoji,
  extractSpanList,
  splitStringWithSpans,
};
