import {Image, TouchableOpacity, View} from 'react-native';
import TextCM from '../../../../components/Text';
import styles from './styles';
import {useTranslation} from 'react-i18next';
import {FontSize} from '../../../../../constants';
import {IInfo} from '../../interface';
import {Fragment} from 'react';
import React from 'react';

type Props = {
  dataInfo: IInfo[];
};

const InfoBodyScreen = ({dataInfo}: Props) => {
  const {t} = useTranslation();

  const renderStatus = (info: IInfo) => {
    return (
      <View style={{flex: 1, flexDirection: 'row'}}>
        <View style={styles.ctnStatus}>
          <TextCM
            style={[
              styles.txtValue,
              {
                color: '#EF5DA8',
              },
            ]}>
            {t(`TK.${info?.value?.status}`)}
          </TextCM>
        </View>
        <View style={{flex: 1}} />
      </View>
    );
  };
  return (
    <View style={{flex: 1}}>
      {dataInfo &&
        dataInfo.map(info => (
          <Fragment key={info.key}>
            <View
              style={[
                styles.ctnWrapperItem,
                info?.type === 'status' && {
                  justifyContent: 'flex-start',
                  columnGap: 0,
                },
              ]}>
              <TextCM style={styles.txtTitle}>{t(`${info.key}`)}:</TextCM>
              {info.type === 'normal' && (
                <View style={styles.ctnNormal}>
                  {info.value.onlyIcon ? (
                    info.value.onlyIcon
                  ) : (
                    <>
                      <TextCM style={styles.txtValue}>
                        {info?.value?.name || '---'}
                      </TextCM>
                      {info.value.withIcon && info.value.withIcon}
                    </>
                  )}
                </View>
              )}

              {info.type === 'avatar' && (
                <View style={styles.ctnView}>
                  <Image
                    width={24}
                    height={24}
                    borderRadius={12}
                    source={{
                      uri: info?.value?.avatarUrl
                        ? info?.value?.avatarUrl
                        : 'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                    }}
                  />
                  <View style={{flex: 1}}>
                    <TextCM style={styles.txtValue}>
                      {info?.value?.name || '---'}
                    </TextCM>
                    <TextCM style={styles.txtValue}>
                      {info?.value?.email || '---'}
                    </TextCM>
                  </View>
                </View>
              )}

              {info.type === 'status' && <>{renderStatus(info)}</>}

              {info.type === 'form' && (
                <TouchableOpacity
                  style={{flex: 1}}
                  onPress={info.value.funcViewFrom}>
                  {info.value ? (
                    <>
                      <TextCM style={[styles.txtValue, styles.formName]}>
                        {info.value?.name || '---'}
                      </TextCM>
                      <TextCM
                        style={{
                          fontSize: FontSize.FontTinier,
                          fontStyle: 'italic',
                        }}>
                        {t('RV.fromViewDetail')}
                      </TextCM>
                    </>
                  ) : (
                    <TextCM style={styles.txtValue}>{'---'}</TextCM>
                  )}
                </TouchableOpacity>
              )}
            </View>
          </Fragment>
        ))}
    </View>
  );
};

export default InfoBodyScreen;
